<template>
	<div class="home_AS">
		<div @click="mod()" class="home_AS_div">
			<img :src="imgs[0]" alt="">
			<div>{{ogg}}</div>
		</div>
		<ol class="home_AS_ol" v-if="store.length">
			<li v-for="(val,index) in store" :key="index" class="home_AS_Oli" @click="goto(val.id)">
			<router-link :to="{name:'shop'}" class="home_router_link">
			<!-- to="###"-->
			<el-row :gutter="20" class="home_AS_Oli_E">
				<!-- 左边 -->
				<el-col :span="5"><img :src="'https://elm.cangdu.org/img/'+val.image_path" alt="图片" class="home_AS_Oli_ECImg"></el-col>
				<!-- https://elm.cangdu.org/img/164ad0b6a3917599.jpg -->
				<!-- 右边 -->
				<el-col :span="18" style="margin-left:0.08rem;padding:0rem 0.05rem">
					<el-row :gutter="5" class="el-row2" style="position:relation;">
					  <el-col :span="3" ><div style="background:#ffd930;color:black;font-size:0.12rem;height:0.18rem;line-height:0.18rem;margin-top:0.02rem">品牌</div></el-col>
					  <el-col :span="14" style="text-align:left;font-size:0.19rem;font-weight:600;color:#333;width:1.5rem;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">{{val.name}}</el-col>
					  <el-col :span="7" style="text-align:right;font-size:0.12rem;color:#999;position:absolute;right:0;top:0;">
					  <span v-for="(value,index) in val.supports" style="margin:0 0.02rem;font-size:0.12rem;">{{value.icon_name}}</span>
					  </el-col>

					</el-row>
					<el-row :gutter="5" class="el-row2">
						<div class="shop_SM_msg_p2">
							<span class="shop_SM_msg_p2_span"  style="color:#ff9900">商家评分:</span>
							<span class="shop_SM_msg_p2_span">4.7</span>
						</div>
					  <el-col :span="8" style="font-size:0.12rem;text-align:left;width:0.8rem;">月销售{{val.recent_order_num}}单</el-col>
					  <el-col :span="10" style="text-align:right;width:1.02rem;float:right">
						  <span v-if="val.delivery_mode" style="font-size:0.08rem;border:0.01rem solid #3190e8;background:#3190e8;color:white;border-radius:0.02rem;padding:0.01rem">{{val.delivery_mode.text}}</span>
						  <span v-if="val.delivery_mode" style="font-size:0.08rem;border:0.01rem solid #3190e8;color:#3190e8;border-radius:0.02rem;padding:0.01rem">准时达</span>
					   </el-col>
					</el-row>

					<el-row :gutter="5" class="el-row2">
					  <el-col :span="11" style="font-size:0.12rem;text-align:left;color:#999;width:1.25rem;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">
						{{val.float_minimum_order_amount}}元起送/{{val.piecewise_agent_fee.tips}}
					  </el-col>
					 
					  <el-col :span="13" class="el_col_last1" style="text-align:right;color:#999">
						<span style="font-size:0.12rem">{{val.distance}}</span>/<span style="font-size:0.12rem">{{val.order_lead_time}}</span>
					  </el-col>
					 
					</el-row>
				</el-col>
			</el-row>

			</router-link>
			</li>
		</ol>
	</div>
</template>
<script>

	import img01 from "@/components/home/msg/msg01.jpg"
	import { Loading } from 'element-ui';
	var AroundStore ={
		name:"AroundStore",
		data(){
			return{
				store:[],
				// 获取城市
				tude:{
					longitude:"113.624931",
					latitude:"34.74725",
					name:"",
				},
				imgs:[img01],
				value5: 4.7,
				ogg:"附近商家"
			}
		},
		
		created(){
			var loadingInstance = Loading.service({ fullscreen: true });
			// 获取周围商家
			var _this = this
			// 定位城市
			// this.positionCity(function(el){
			// 	_this.tude.latitude=el.latitude
			// 	_this.tude.longitude=el.longitude
			// 	_this.tude.name=el.name
			// 	var cityMsg={
			// 		id:el.id,
			// 		latitude:el.latitude,
			// 		longitude:el.longitude
			// 	}
			// 	_this.$store.commit("cityMsg",cityMsg)  
			// })
			console.log(this.$store.state.cityMsg)
			var west =this.$store.state.cityMsg.latitude;
			var nest =this.$store.state.cityMsg.longitude;
			var orderBy=this.$store.state.orderBy.id;
			this.ogg=this.$store.state.orderBy.name;
			// 按条件搜索信息
			this.SearchAround(west,nest,orderBy,function(el){
				el.shift()
				_this.store=el
				loadingInstance.close();
				_this.$store.commit("AllStore",el)
			})
		},
		beforeRouteUpdate(to,from,next){
			next()
		},
		props:["km","kd","od"],
		methods:{
			mod(){

			},
			goto(el){
				this.$store.commit("shopIds",el)
			}
		},
		watch:{
			km(news){
				var west =this.$store.state.cityMsg.latitude;
				var nest =this.$store.state.cityMsg.longitude;
				var orderBy=this.$store.state.orderBy.id;
				this.ogg=this.$store.state.orderBy.name;
				var _this=this;
				
				this.SearchAround(west,nest,orderBy,function(el){
					el.shift()
					_this.store=el
					_this.$store.commit("AllStore",el)
				})
				// var el = this.$store.state.AllStore;
				// el.shift()
				// this.store=el
				console.log("监听km")
			},
			kd(news){
				// this.$store.state.goodSelectMsg:[]
				var nnb = this.store
				var pock= this.$store.state.goodSelectMsg[0]
				var nnt=nnb.filter(function(item){
					return item.category==pock
				});
				this.store=nnt
			},
			od(news){
				// this.$store.state.selectMsg:[],
				var nnb = this.store
				var pock= this.$store.state.selectMsg
				var _this=this
				if (pock.length) {
					this.store=nnb
				}else{
					pock.forEach(function(val,ind){
						if(val.id==0){
							_this.store = nnb.filter(function(item){
								return item.delivery_mode.text=="蜂鸟专送"
							});
						}
						if(val.id==1){
							_this.store = nnb.filter(function(item){
								return item.is_premium==true
							});
						}
						if(val.id==2){
							nnb.forEach(function(item){
								var a = item.supports.filter(function(val){
									return val.name=="外卖保"
								})
								if (a!=' ') {
									_this.store = _this.store
								}else{
									_this.store = []
								}
							});
			
						}
						if(val.id==3){
							nnb.forEach(function(item){
								var a = item.supports.filter(function(val){
									return val.name=="准时宝"
								})
								if (a!=' ') {
									_this.store = _this.store
								}else{
									_this.store = []
								}
							});
						}
						if(val.id==4){
							_this.store = nnb.filter(function(item){
								return item.is_new==true
							});
						}
						if(val.id==5){
							_this.store = nnb.filter(function(item){
								return item.is_new==true
							});
						}
						if(val.id==6){
							nnb.forEach(function(item){
								var a = item.supports.filter(function(val){
									return val.name=="开发票"
								})
								if (a!=' ') {
									_this.store = _this.store
								}else{
									_this.store = []
								}
							});
						}
					})
				}

			}
		}
	}
	export default AroundStore;

</script>

<style scoped>
	.home_AS{

	}
	
	.home_AS_div{
		width: 100%;
		height: 0.41rem;
		overflow: hidden;
	}
	.home_AS_div img{
		width: 0.2rem;
		margin-top: 0.08rem;
		float:left;
		margin-left:0.12rem;
	}
	.home_AS_div div{
		line-height: 0.41rem;
		font-size: 0.18rem;
		float:left;
		margin-left:0.1rem;
	}
	.home_AS_ol{
		width: 100%;
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
	}
	.home_AS_Oli{
		height:0.8rem;
		overflow: hidden;
		padding: 0.12rem 0.12rem 0.05rem 0.12rem;
		font-size: 0.15rem;
		color:#666;
	}
	.home_router_link{
		font-size: 0.15rem;
		color:#666;
	}
	.home_AS_Oli_E{
		
	}
	.home_AS_Oli_ECImg{
		width: 0.67rem;
	}
	.home_AS .el_col_last1 span{
		font-size: 0.14rem;
	}
	.home_AS .el_col_last1 span+span{
		color:#3190e8;
	}
	.home_AS .el-row:nth-child(2){
		margin-top: 0.1rem;
		margin-bottom: 0.1rem;
	}
	.home_AS .el-row2{
		height:0.18rem;
	}
	.home_AS .el-row2 .el-col{
		line-height: 0.2rem;
	}
	.home_AS .el-rate__icon{
		font-size: 0.05rem !important;
	}
	.shop_SM_msg_p2{
		display: flex;
		justify-content:flex-start;
		font-size: 0.14rem;
		color:#666;
		float:left;
		margin-top: 0.02rem;
		line-height: 0.14rem;
	}
	
</style>
	



