<template>
	<div id="orderBody">
		<nav>
			<Order_Top></Order_Top>
		</nav>
		<div id="orderFood">
			<Order_Msg></Order_Msg>
		</div>
		<footer>
			<Order_navbottom></Order_navbottom>
		</footer>
	</div>
</template>
<script>
import Vue from 'vue'  ;
import Order_Top from "@/components/order/ordertop"
import Order_Msg from "@/components/order/orderMsg"
import Order_navbottom from "@/components/home/navBottom"
	var order = {
		name:"order",
		data(){
			return {
				store:[]
			}
		},
		components:{
		   Order_navbottom,
		   Order_Top,
		   Order_Msg
		},
		// 请求演示
		created(){
			

		}
	}
	export default order

</script>
<style scoped>
	#orderBody{
		height: 100%;
		background-color: #f5f5f5;
	}
	nav{
		position:fixed;
		top: 0;
		left: 0;
		z-index: 5;
		height: 0.5rem;
		background: #3190e8;
		width: 100%;
	}
	#orderFood{
		height: 1.9rem;
		padding-top:0.5rem; 
		border-bottom:0.01rem solid #e4e4e4;
		background-color: #fff;
	}
	footer{
		position: fixed;
		height:0.5rem;
		bottom: 0;
		z-index: 6;
		left: 0;
		width: 100%;
		background: #fff;
		overflow: hidden;
	}

</style>


