<template>
	<div id="search_History">
		<ul v-if="historys.length&&isHaveHis">
			<div class="search_History_div1">搜索历史</div>
			<li v-for="(val,ind) in historys" :key="ind" >
				<router-link to="###" class="home_router_link">
				<!-- :to="{name:'shop'}" -->
					<div @click="goBack(val)">{{val}}</div>
					<i class="el-icon-close" @click="delHistory(val)" v-if="onHasHis"></i>
				</router-link>
			</li>
			<div class="search_History_div2" @click="delAll()">清空搜索历史</div>
		</ul>
		<ul v-if="historys.length==0&&!isHaveHis" class="search_History_ul2">
			<div>很抱歉!无搜索结果</div>
		</ul>
		<ul v-if="historys.length&&!isHaveHis" class="search_History_ul2">
			<div>很抱歉!周围没有此店铺</div>
		</ul>
	</div>
</template>
<script>
	var searchHistory = {
		name:"searchHistory",
		data(){
			return {
				Allmsg:[],
				historys:[],
				isHaveHis:true,
				// 匹配后的内容
				onHasHis:true,
				onShopId:0,
			}
		},
		// cli把提交按钮点击并传过来信息
		// clo把清空内容信息给我,我做内容
		props:["cli","clo","cd"],
		components:{
		    
		},
		// 请求演示
		created(){
			this.$store.commit("getLocal","searchLocalHistory")
			this.historys = this.$store.state.searchLocalHistory
		},
		methods:{
			delHistory(el){
				var msg={
					name:"searchLocalHistory",
					val:el
				}
				this.$store.commit("removeHist",msg)
				this.onHasHis=true
			},
			// 点击跳转页面
			goBack(el){
				this.cd(el)
				// 思路,跳转页面,进行搜索,先查找到所有的店铺,然后在进行匹配名称,匹配到了就给我店铺id,没有就gg
				// 优化思路,在刚开始打开首页的时候,就存一下id
				var _this=this
				var citylatitude = this.$store.state.cityMsg.latitude
				var citylongitude = this.$store.state.cityMsg.longitude
				var cityid = this.$store.state.cityMsg.id
				this.SearchAllShop(citylatitude,citylongitude,function(val){
					_this.$store.commit("AllShop",val)
					var aaa = _this.$store.state.AllShop
					var bbb = aaa.filter((val)=>val.name==el)
					if (bbb.id) {
						_this.$store.commit("shopIds",bbb.id)
					}else{
						_this.isHaveHis=false
					}
				})
			},
			delAll(){
				this.$store.commit("removeAllHist","searchLocalHistory")
				this.historys = []
				this.onHasHis=true
			}
		},
		watch:{
			cli(news){
				var _this=this
				var cityId=this.$store.state.cityMsg.id
				this.SearchStore(cityId,news.value,function(el){
					var arr = []
					for (var i = 0; i < el.length; i++) {
						arr.push(el[i].name)
					};
					_this.historys = arr
					if (arr.length) {
						_this.isHaveHis=true
					}else{
						_this.isHaveHis=false
					}
				})
				this.onHasHis=false
			},
			clo(news){
				this.historys =this.$store.state.searchLocalHistory
				this.isHaveHis=true
				this.onHasHis=true
			}
		}
	}
	export default searchHistory

</script>
<style scoped>
	#search_History{
		width: 100%;
		text-align: left;
	}
	#search_History ul{
		width: 100%;
		display: flex;
		justify-content:flex-start;
		flex-direction:column;
	}
	.search_History_ul2 div{
		text-align: center;
		width:100%;
		line-height: 0.42rem;
		height:0.42rem;
		background-color:#fff;
	}
	.search_History_div1{
		line-height: 0.28rem;
		font-size: 0.14rem;
		height:0.35rem;
		color:#666;
		margin-left: 0.15rem;
		font-weight: 600;
	}
	.search_History_div2{
		color:#3190e8;
		text-align: center;
		line-height: 0.4rem;
		font-size: 0.16rem;
		font-weight: 600;
		margin-bottom: 0.5rem;
		height:0.4rem;
		background-color: #fff;
	}	
	#search_History ul li{
		width:100%;
		height:0.43rem;
		background-color: #fff;
		border-bottom: 0.005rem solid #e4e4e4;
	}
	#search_History ul li a{
		display: flex;
		justify-content:space-between;
		flex-direction:row;
		height:100%;
		line-height: 0.42rem;
		color:#333;
	}
	#search_History ul li div{
		margin-left: 0.15rem;
		width:3.2rem;
	}
	#search_History ul li i{
		margin-right: 0.15rem;
		margin-top: 0.08rem;
		font-weight: 1000;
	}

</style>


