<template>
	<div id="homeBody">
		<nav>
			<Home_top></Home_top>
		</nav>
		<div id="selectFood">
			<Home_guideTitle></Home_guideTitle>
		</div>
		<div id="AroundStore">
			<Home_AroundStore></Home_AroundStore>
		</div>
		<footer>
			<Home_navbottom></Home_navbottom>
		</footer>
	</div>
</template>
<script>
import Vue from 'vue'  ;
import Home_navbottom from "@/components/home/navBottom"
import Home_AroundStore from "@/components/home/AroundStore"
import Home_top from "@/components/home/hometop"
import Home_guideTitle from "@/components/home/guideTitle"
import { Loading } from 'element-ui';

	var Home = {
		name:"home",
		data(){
			return {
				store:[]
			}
		},
		components:{
		    Home_top,
		    Home_guideTitle,
		    Home_AroundStore,
		    Home_navbottom,
		},
		// 请求演示
		created(){
			var loadingInstance = Loading.service({ fullscreen: true });
			var _this = this
			var west =31.22967;
			var nest = 121.4762;
			this.StoreMsg(5,function(el){
				_this.store=el
				loadingInstance.close();
			})
		}
	}
	export default Home

</script>
<style scoped>
	#homeBody{
		height: 100%;
		background-color: #f5f5f5;
	}
	nav{
		position:fixed;
		top: 0;
		left: 0;
		z-index: 5;
		height: 0.5rem;
		background: #3190e8;
		width: 100%;
	}
	#selectFood{
		height: 1.9rem;
		padding-top:0.5rem; 
		border-bottom:0.01rem solid #e4e4e4;
		background-color: #fff;
	}
	#AroundStore{
		margin-top: 0.1rem;
		border-top: 1px solid #e4e4e4;
		background-color: #fff;
		margin-bottom: 0.5rem;
	}
	footer{
		position: fixed;
		height:0.5rem;
		bottom: 0;
		z-index: 6;
		left: 0;
		width: 100%;
		background: #fff;
		overflow: hidden;
	}

</style>


