<template>
	<div id="shop_SM">
		<div id="shop_SM_mark">
			<div class="shop_SM_left">
				<p class="shop_SM_left_p1">{{revMsg}}</p>
				<p class="shop_SM_left_p2">综合评价</p>
				<p class="shop_SM_left_p3">高于周围商家{{baifenMsg}}</p>
			</div>
			<div class="shop_SM_right">
				<div class="shop_SM_right_p1"><span>服务态度</span>
					<span style="color:#ff9900">{{value5}}</span>
				</div>
				<div class="shop_SM_right_p2"><span>菜品评价</span>
					<span style="color:#ff9900">{{value6}}</span>
				</div>
				<p class="shop_SM_right_p3">送达时间 <span>{{Mark.deliver_time}}分钟</span></p>
			</div>
		</div>
		<div id="shop_SM_type">
			<ul>
				<li v-for="(val,ind) in Type" :key="ind" @click="btns(ind)" :class="{SM_type_key:isCli==ind}">
					<router-link to="###">
						{{val.name}}({{val.count}})
					</router-link>
				</li>
			</ul>
		</div>
		<div id="shop_SM_msg">
			<div  v-for="(val,ind) in Msg" class="shop_SM_msg_ol">
				<div class="shop_SM_msg_left">
					<img src="https://elm.cangdu.org/img/default.jpg" alt="" >
				</div>
				<div class="shop_SM_msg_center">
					<p class="shop_SM_msg_p1">
						{{val.username}}
					</p>
					<p v-if="val.time_spent_desc" class="shop_SM_msg_p2">
						<span class="shop_SM_msg_p2_span">{{val.time_spent_desc}}</span>
					</p>
					<div class="shop_SM_msg_p3">
						<img v-for="(value,index) in val.item_ratings" :src="'https://fuss10.elemecdn.com/'+value.image_hash+'.jpeg'" alt="" v-if="value.image_hash">
					</div>
					<div class="shop_SM_msg_p4">
						<div v-for="(value,index) in val.item_ratings" v-if="value.food_name">
							{{value.food_name}}
						</div>
					</div>
				</div>
				<div class="shop_SM_msg_right">{{val.rated_at}}</div>
			</div>
		</div>
	</div>
</template>
<script>
import img01 from "@/components/shop/shopImg/imgs10.jpg"
	var shopMsg ={
		name:"shopMsg",
		data(){
			return {
				// 评价分数
				Mark:{},
				// 评价类型
				Type:[],
				// 评价信息
				Msg:[],
				value5: 4.7,
				value6:4.8,
				value7:5,
				isCli:0,
				onimg:img01
			}
		},
		computed:{
			revMsg(){
				var a=this.Mark.deliver_time
				var b = this.Mark.service_score
				var c=this.Mark.food_score
				var d= (a/10+b+c)/3
				return parseFloat(d).toFixed(1)
			},
			// 百分数
			baifenMsg(){
				var a = this.Mark.compare_rating
				var b =a*100
				var c = b.toFixed(1)
				return c+"%"
			},


		},
		created(){
			var _this=this
			var shopid= this.$store.state.shopId
			console.log(shopid)
			// 评价信息
			this.restMsg(shopid,function(el){
				_this.Msg=el
			})
			// 评价分数
			this.restMark(shopid,function(el){
				_this.Mark=el
			})
			// 评价类型
			this.restType(shopid,function(el){
				_this.Type=el
			})
		},
		methods:{
			btns(index){
				this.isCli=index
			}
		},
		props:["shopid"]

	}
	export default shopMsg
</script>
<style scoped>
	.el-rate__icon{
		font-size: 0.1rem;
		margin:0;
	}
	.el-rate__text{
		font-size: 0.16rem;
		margin-left: 0.02rem;
	}
	#shop_SM{
		width:3.75rem;
		height:100%;
		background: #f5f5f5;
		overflow-x:hidden;
		overflow-y:scroll;
	}
	#shop_SM::-webkit-scrollbar {display:none}
	#shop_SM_mark{
		width:100%;
		height:1.09rem;
		display: flex;
		justify-content:center;
		background: white;

	}
	.shop_SM_left{
		display: flex;
		justify-content:center;
		flex-direction:column;
		width: 1.5rem;
	}
	.shop_SM_left_p1{
		font-size: 0.25rem;
		color:#f60;
		font-weight: 600;
	}
	.shop_SM_left_p2{
		margin-top: 0.05rem;
		margin-bottom: 0.05rem;
		font-size: 0.16rem;
		color:#666;
		font-weight: 500;
	}
	.shop_SM_left_p3{
		font-size: 0.14rem;
		color:#999;
		font-weight: 400;
	}
	.shop_SM_right{
		width: 2rem;
		display: flex;
		justify-content:center;
		flex-direction:column;
		align-items:flex-start;
	}
	.shop_SM_right_p1{
		display: flex;
		justify-content:center;
	}
	.shop_SM_right_p1>span{
		font-size: 0.15rem;
		color:#666;
		margin-right: 0.05rem;
	}
	.shop_SM_right_p2{
		display: flex;
		justify-content:center;
		margin-top: 0.03rem;
		margin-bottom: 0.03rem;
	}
	.shop_SM_right_p2>span{
		font-size: 0.15rem;
		color:#666;
		margin-right: 0.05rem;
	}
	.shop_SM_right_p3{
		font-size: 0.15rem;
		color:#666;
	}
	.shop_SM_right_p3 span{
		font-size: 0.13rem;
		color:#666;
		margin-left: 0.01rem;
	}
	#shop_SM_type{
		width:100%;
		padding: 0.12rem;
		background: #fff;
		margin-top: 0.12rem;
	}
	#shop_SM_type ul{
		display: flex;
		justify-content:flex-start;
		flex-wrap:wrap;
	}
	#shop_SM_type ul li{
		margin-right: 0.1rem;
		padding:0.05rem;
		background: #ebf5ff; 
		height:0.16rem;
		margin-bottom: 0.08rem;
		text-align: center;
		line-height: 0.16rem;
		border-radius: 0.06rem;
	}
	#shop_SM_type ul li:nth-child(3) a{
		background: #f5f5f5; 
		color:#aaa;
	}
	#shop_SM_type ul li a{
		color:#6d7885;
		font-size: 0.15rem;
	}

	.SM_type_key a{
		padding:0.05rem;
		height:0.16rem;
		line-height: 0.16rem;
		border-radius: 0.06rem;
		background: #3190e8 !important;
		color:#fff !important;
	}
	#shop_SM_msg{
		padding: 0.02rem;
		width:3.7rem;
		margin-top: 0.02rem;
		background: #fff;
	}
	.shop_SM_msg_ol{
		width:100%;
		position: relative;
		margin-top: 0.2rem;
		border-bottom: 1px solid #f1f1f1;
		padding-bottom: 0.2rem;
	}
	.shop_SM_msg_left{
		width:0.4rem;
		height:0.4rem;
		position: absolute;
		left: 0.05rem;
		top: 0.05rem;
		border-radius: 50%;
		overflow: hidden;
	}
	.shop_SM_msg_left img{
		width: 100%;
		height: 100%;
	}
	.shop_SM_msg_center{
		text-align: left;
		margin-left:0.6rem;
		margin-top: 0.05rem;
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
	}
	.shop_SM_msg_p1{
		font-size: 0.14rem;
		color:#666;
	}
	.shop_SM_msg_p2{
		display: flex;
		justify-content:flex-start;
		font-size: 0.16rem;
		color:#666;
		margin-top: 0.05rem;
	}
	.shop_SM_msg_p2_span{
		/*margin-left: 0.05rem;*/
		color:#aaa;
		font-size:0.14rem;
	}
	.shop_SM_msg_p3{
		height:0.7rem;
		margin-top: 0.05rem;
	}
	.shop_SM_msg_p3 img{
		width: 0.7rem;
		float: left;
	}
	.shop_SM_msg_p3 img:nth-child(even){
		margin-left: 0.08rem;
	}
	.shop_SM_msg_p4{
		display: flex;
		justify-content:flex-start;
		height:0.26rem;
		margin-top: 0.05rem;

	}
	.shop_SM_msg_p4 div{
		font-size: 0.15rem;
		line-height: 0.2rem;
		padding: 0.05rem;
		border: 1px solid #ebebeb;
		color:#999;
		margin-right: 0.1rem;
		width:0.5rem;
		overflow: hidden;
		text-align: center;
		text-overflow:ellipsis;
		white-space: nowrap;
	}
	.shop_SM_msg_right{
		position: absolute;
		right: 0.15rem;
		top: 0rem;
		font-size: 0.12rem;
		color:#999;
	}

</style>


