<template>
	<div id="city_wrap">
		<div id="hometop_to">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<div class="hometop_city_div">
				<router-link to="/login" class="router" v-if="!loginOK">登录 | 注册</router-link>
				<router-link to="/mui" v-else class="citynav_img">
					<img src="@/../static/imgs/用户中心.png" />
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
	var Citynav = {
		name: "citynav",
		data(){
			return {
				loginOK:false,
				
			}
		},
		methods:{
			sear(){
				this.$router.go(-1);
			}
		},
		created(){
			this.personLocal = this.$store.state.personLocal[0]
			if(this.personLocal.id){
				this.loginOK = true
			}
		}
	}
	export default Citynav
</script>
<style scoped>
	#hometop_to {
		width: 100%;
		line-height: 0.5rem;
		height: 0.5rem;
		font-size: 0.15rem;
		box-sizing: border-box;
		position: relative;
	}
	.hometop_city_div{
		position: absolute;
		right: 0.1rem;
		top: 50%;
		transform: translateY(-50%);
	}
	.router{
		color: white;
		
	}
	#hometop_to>i{
		position: absolute;
		left: 0.1rem;
		top: 50%;
		transform: translateY(-50%);
		color: white;
		
	}
	.citynav_img{
		width: 0.3rem;
		height: 0.3rem;
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
	}
	.citynav_img>img{
		width: 0.3rem;
		height: 0.3rem;
	}
</style>


