<template>
	<div id="shopBody">
		<div class="shopTitle">
			<Shop_shopTitle :isBlock="BlockActive"></Shop_shopTitle>
		</div>
		<div class="shopChoose">
			<Shop_shopGuide></Shop_shopGuide>
		</div>
		<div class="shopfood">
			<!-- <Shop_shopFood></Shop_shopFood> -->
			<router-view></router-view>
		</div>
		<div v-if="isBlockActive" id="shop_shopActive">
			<Shop_ShopActive :hidd="HiddenActive"></Shop_ShopActive>
		</div>
	</div>
</template>
<script>
import Vue from 'vue';
import Shop_shopFood from "@/components/shop/shopFood"
import Shop_shopGuide from "@/components/shop/shopGuide"
import Shop_shopTitle from "@/components/shop/shopTitle"
import Shop_shopMsg from "@/components/shop/shopMsg"
import Shop_ShopActive from "@/components/shop/shopActive"

var Shop = {
	name:"shop",
	data(){
		return {
			shopId:"",
			isBlockActive:false,
			isbackground:false,
		}
	},
	components:{
		Shop_shopFood,
		Shop_shopGuide,
		Shop_shopTitle,
		Shop_shopMsg,
		Shop_ShopActive
	},
	methods:{
		BlockActive(){
			this.isBlockActive=true
		},
		HiddenActive(){
			this.isBlockActive=false
		},
		// 隐藏背景大框
		hiddenMe(){
			this.isbackground=false;
		},
	},

}
export default Shop
</script>
<style scoped>
	#shopBody{
		height:100%;
		background-color: #f5f5f5;
	}
	.shopTitle{
		position:fixed;
		top: 0;
		left: 0;
		z-index: 2;
		height:1.05rem;
		width: 100%;
	}
	.shopChoose{
		position:fixed;
		width: 100%;
		top: 1.05rem;
		left: 0;
		z-index: 4;
		height: 0.5rem;
	}
	.shopfood{
		width: 100%;
		margin-top:1.55rem;
		border-top: 1px solid #888;
		background-color: #fff;
		height:4.7rem;
		overflow: hidden;
	}
	#shop_shopActive{
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: #262626;
		z-index: 49;
	}
	
</style>


