<template>
	<div id="shop_foodMsg">
		<div class="shop_foodMsg_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p>{{msg.name}}</p>
		</div>
		<div class="shop_foodMsg_center_img">
			<img :src="'https:elm.cangdu.org/img/'+msg.image_path" alt="餐厅照">
		</div>
		<ul class="shop_foodMsg_ul">
			<li>{{msg.description}}</li>
			<li class="shop_foodMsg_uli2">{{msg.name}}</li>
			<li>评分
				<el-rate
					v-model="msg.rating"
					disabled
					text-color="#ff9900"
					score-template="{value}">
				</el-rate>
				<span class="shop_foodMsg_uli_span">{{msg.rating}}</span>
			</li>
			<li>
				<div class="shop_foodMsg_uli_div">
					<span>月销售</span>{{msg.month_sales}}单
				</div>
				<div class="shop_foodMsg_uli_div2">
					<span>售价</span>{{msg.specfoods[0].price}}元起
				</div>
			</li>
			<li>
				<div class="shop_foodMsg_uli_div">
					<span>评价数:</span>{{msg.rating_count}}
				</div>
				<div class="shop_foodMsg_uli_div">
					<span>好评率:</span>{{msg.satisfy_rate}}%
				</div>
			</li>
		</ul>
	</div>
</template>
<script>
	import { Loading } from 'element-ui';
	var foodMsg={
		name:"foodMsg",
		data(){
			return {
				msg:{}
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		created(){
			var loadingInstance = Loading.service({ fullscreen: true });
			this.msg=this.$store.state.foodMsg
			loadingInstance.close();
		},
		components:{

		}
	}
	export default foodMsg
</script>
<style scoped>
	#shop_foodMsg{
		position: relative;
		width: 100%;
		background-color: #fff;
		overflow: hidden;
	}
	.shop_foodMsg_nav {
		position: relative;
		width: 100%;
		line-height: 0.5rem;
		background: #3190e8;
		height: 0.45rem;
		font-size: 0.18rem;
		text-align: center;
		font-weight: 700;
	}
	.shop_foodMsg_nav p,i{
		color: white;
	}
	.shop_foodMsg_nav i {
		float: left;
		line-height: 0.45rem;
		margin-left: 0.03rem;
		position: absolute;
		left: 0.08rem;
		top: 0;
		font-size: 0.22rem;
	}
	.shop_foodMsg_center_img{
		width: 100%;
		height: 3.75rem;
		overflow: hidden;
	}
	.shop_foodMsg_center_img img{
		width: 100%;
		height: 100%;
	}
	.shop_foodMsg_ul{
		margin-top: 0.08rem;
		width: 100%;
		padding-left: 0.12rem;
		overflow:hidden;
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		text-align:left;
	}
	.shop_foodMsg_ul li{
		font-size: 0.14rem;
		height:0.25rem;
		line-height: 0.25rem;
		margin-top: 0.03rem;
		display: flex;
		justify-content:flex-start;
		color:#666;
	}
	.shop_foodMsg_uli2{
		font-size: 0.18rem !important;
		font-weight: 400;
	}
	.shop_foodMsg_uli_div{
		margin-right:0.08rem;
	}
	.shop_foodMsg_uli_div span{
		margin-right: 0.05rem;
	}
	.shop_foodMsg_uli_div2{
		color:#f60;
	}
	.shop_foodMsg_uli_div2 span{
		margin-right: 0.05rem;
	}
</style>
<style>
	.el-rate{
		margin-left: 0.1rem;
		margin-top: 0.04rem;
	}
	.el-rate__icon{
		font-size: 0.1rem;
		margin:0;
	}
	.el-rate__text{
		font-size: 0.16rem;
	}
	.shop_foodMsg_uli_span{
		color:#f60;

	}
</style>


