<template>
<div id="shangcheng_wrap">
		
	<router-link to="/order">
		<div class="shangcheng_wrap_link">
			<img src="../../../static/imgs/主页.png" alt=""style="width: 0.15rem;"/>
			<span>我的订单</span>
			<i class="el-icon-arrow-right"></i>
		</div>
	</router-link>

	<router-link to="">
		<div class="shangcheng_wrap_link" @click="centerDialogVisible=true">
			<img src="../../../static/imgs/主页.png" alt=""style="width: 0.15rem;"/>
			<span>积分商城</span>
			<i class="el-icon-arrow-right"></i>
		</div>
	</router-link>
	<el-dialog title="提示" :visible.sync="centerDialogVisible" width="75%" style="text-align: center;">
				<p style="font-size: 0.7rem;font-weight: bolder;color: red;">!</p>
				<span style="font-size: 0.2rem;">请在饿了么APP中打开</span>
				<span slot="footer" class="dialog-footer">
					<el-button type="primary" @click="returnMui()" style="width: 100%;">确定</el-button>
				</span>
		</el-dialog>
	
	<router-link to="/clubcenter">
		<div class="shangcheng_wrap_link">
			<img src="../../../static/imgs/主页.png" alt=""style="width: 0.15rem;"/>
			<span>饿了么会员卡</span>
			<i class="el-icon-arrow-right"></i>
			
		</div>
	</router-link>

</div>
</template>

<script>
	var Shangcheng = {
		name:"denglu",
		data(){
			return {
				centerDialogVisible:false
			}
		},
		methods:{
			returnMui(){
				this.centerDialogVisible= false
			}
		}
	}
	export default Shangcheng
</script>

<style scoped>
	#shangcheng_wrap{
		margin-top:0.15rem;
		background: white;
		clear: both;
		width:100%;
		/*height: 0.7rem;*/
	}
	.shangcheng_wrap_link{
		padding: 0 0.1rem;
		height: 0.45rem;
		line-height: 0.45rem;
		border-bottom: 1px solid rgb(245,245,245);

	}
	.shangcheng_wrap_link i{
		float: right;
		line-height: 0.45rem;
	}
	span{
		color: black;
		margin-left: 0.1rem;
	}
</style>


