<template>
<div id="city">
	<!--导航-->
	<div id="city_nav">
		<Citynav></Citynav>
	</div>
	<!--定位城市-->
	<div id="city_positions"> 
		<Positionscity></Positionscity>
	</div>
	<!--热门城市-->
	<div id="city_hot">
		<Hotcity></Hotcity>
	</div>
	<!--所有城市-->
	<div id="city_all">
		<Allcity></Allcity>
	</div>	
</div>
</template>

<script>
	import Citynav from '@/components/citys/citynav'
	import Allcity from '@/components/citys/allcity'
	import Hotcity from '@/components/citys/hotcity'
	import Positionscity from '@/components/citys/positionscity'
	var City = {
		name:"city",
		components:{
			Citynav,
			Allcity,
			Hotcity,
			Positionscity
		}
		
	}
	export default City
</script>

<style scoped>
	#city{
		background-color: #F5F5F5;
		text-align: left;
		font-size: 0.15rem;
		width: 100%;
	}
	/*导航*/
	#city_nav{
		width:100%;
		position: fixed;
		left: 0;
		top: 0;
		right: 0;
		z-index: 10;
		background: #3190E8;

	}
	/*热门城市*/
	#city_hot{
		background-color:white ;
		margin-bottom: 0.1rem;
		width: 100%;
	}
	/*定位城市*/
	#city_positions{
		margin-top: 0.5rem;
		background-color:white ;
		margin-bottom: 0.1rem;
		width: 100%;
		margin-top: 0.5rem;
	}
	/*所有城市*/
	#city_all{
		background-color: white;
		width: 100%;
	}
</style>


