<template>
	<div id="newprofile_wrap">
		<!--导航条-->
		<div class="newprofile_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;">新增地址</p>
		</div>
		<!--内容-->
		<div class="newprofile_center">
			<input type="text" placeholder="请填写姓名" v-model="proname" />
			<router-link to="/logined/profile/address/adddetail">
				<input type="text" placeholder="小区/学校/楼等" v-model="protower" />
			</router-link>
			<input type="text" placeholder="填写详细地址" v-model="proaddress" />
			<input type="text" placeholder="填写手机号" v-model="prophone" />
			<input type="text" placeholder="备用练习电话" v-model="proappphone" />
			 <el-button type="success" @click="pronew()">新增地址</el-button>
		</div>	
	</div>
</template>
<script>
	var Newprofile = {
		name:"newprofile",
		data(){
			return {
				id:"",			//用户id
				protower:"",	//地址
				proaddress:"",	//详细地址
				proname:"",		//收货姓名
				latitude:"",    //纬度
				longitude:"",	//经度
				prophone:"",	//手机号
				sex:"",			//性别
				proappphone:"",	//备用手机号
				tag:"1",		//标签
				tag_type:"2",	//标签类型
				address_id:"",
			}
		},
		created(){
			this.protower = this.$store.state.addressText
			var c = localStorage.getItem("personLocal")
			this.id=1;
			if (c) {
				var msg =JSON.parse(c)
				this.id= msg[0].id
			}else{
				this.id= this.$store.state.personLocal[0].id;
			}
			this.latitude= this.$store.state.cityMsg.latitude
			this.longitude= this.$store.state.cityMsg.longitude
			this.sex = 1
			var localText = this.$store.state.localText
			this.proname = localText.proname 
			this.proaddress = localText.proaddress
			this.prophone = localText.prophone
			this.proappphone = localText.proappphone
		},
		methods:{
			sear(){
				this.$router.go(-1)
			},
			pronew(){
				var _this = this
				var user_id=this.id 	//用户id
				var pro_api = "https://elm.cangdu.org/v1/users/"+user_id+"/addresses"
				_this.$http({
					method:"post",
					url:pro_api,
					data:{
						user_id:user_id,			//用户id
						address:_this.protower,		//地址
						address_detail:_this.proaddress,		//详细地址
						geohash:_this.latitude+","+_this.longitude,  //经纬度
						name:_this.proname,			//收货姓名
						phone:_this.prophone,		//手机号
						tag:_this.tag,				//标签
						sex:_this.sex,				//性别
						phone_bk:_this.proappphone,	//备用手机
						tag_type:_this.tag_type,
						id:_this.address_id
					},
				}).then(function(el){
					if (!el.data.status) {
						alert(el.data.message)
					}else{
						_this.$router.push({name:'profile'})
						alert("添加成功")
					}
				})
			},
			localText(){
				var localText = {
					proname:this.proname,
					proaddress:this.proaddress,
					prophone:this.prophone,
					proappphone:this.proappphone,
				}
				this.$store.commit("localText",localText)
			}
		},
		watch:{
			proname(){
				this.localText()
			},
			proaddress(){
				this.localText()
			},
			prophone(){
				this.localText()
			},
			proappphone(){
				this.localText()
			},
		}
	}
	export default Newprofile
</script>

<style scoped>
	#newprofile_wrap{
		position: absolute;
		left: 0;right: 0;
		top: 0;bottom: 0;
		background: rgb(245,245,245)
	}
	.newprofile_nav{
		width: 100%;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		color: black;
		line-height: 0.5rem;
	}
	.newprofile_nav>i{
		color: white;
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	/*输入框*/
	.newprofile_center input{
		width: 90%;
		height: 0.4rem;
		border: 1px solid rgb(220,220,220);
		font-size: 0.15rem;
		padding-left:0.1rem ;
		margin-top: 0.1rem;
	}
	.newprofile_center>button{
		width: 94%;
		margin-top: 0.3rem;
	}
</style>


