<template>
	<div id="orderGivePath">
        <div id="orderGivePath_top">
        	<!--搜索-->
        	<a @click="goBack()">  
		  		<i class="el-icon-arrow-left"></i>
		  	</a>
		  	<div class="orderGivePath_to_nav">
			  	<router-link to="/city">
			  		<span>{{msg}}</span>
			  	</router-link>
		  	</div>
		</div>
		<div id="orderGivePath_choose">
			<label class="orderGivePath_div">
				<div>是否需要发票</div>
				<input type="radio" name="inputs" value="online" @click="orderGivePath_oncli()">
			</label>
		</div>
		<div id="orderGivePath_sure" @click="orderGivePath_sure()">确定</div>
    </div>
</template>
<script>
	var orderGivePath = {
		name:"orderGivePath",
		data(){
			return {
				msg:"选择发票抬头",
				oncli:false
			}
		},
		methods:{
			// 返回历史
			goBack(){
				this.$router.go(-1)
			},
			orderGivePath_oncli(){
				this.oncli=!this.oncli
			},
			orderGivePath_sure(){
				var el=[]
				if (this.oncli) {
					el=["1"]
				}
				this.$store.commit("orderGivePath",el)
				this.$router.go(-1)
			}
		},
		created(){
			
		},
		watch:{
			
		}
	}
	export default orderGivePath
</script>
<style scoped>
	#orderGivePath{
		position: relative;
		top:0;
		left: 0;
		width: 100%;
		overflow: hidden;
	}
	/*标题信息*/
	#orderGivePath_top{
		line-height: 0.5rem;
		text-align: center;
		width: 100%;
		height: 0.5rem;
		font-size: 0.18rem;
		background-color: #3190e8;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 5;
	}
	.orderGivePath_to_nav{
		width: 1.5rem;
		margin: auto;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#orderGivePath_top a,i{
    	color: white;
	}
	#orderGivePath_top i{
		float: left;
		line-height: 0.5rem;;
		margin-left: 0.03rem;
	}
	#orderGivePath_sure{
		width: 95%;
		height:0.45rem;
		background-color: #4cd964;
		line-height: 0.45rem;
		font-size: 0.16rem;
		margin: 0 auto;
		color:#fff;
	}
	#orderGivePath_choose{
		margin-top: 0.6rem;
		padding-bottom: 0.2rem;
		background-color: #fff;
	}
	.orderGivePath_div{
		display: flex;
		overflow: hidden;
		padding: 0 0.15rem;
		justify-content:space-between;
		color:#666;
		height: 0.45rem;
		line-height: 0.45rem;
	}
	.orderGivePath_div input{
		margin-top: 0.15rem;
	}
</style>


