<template>
	<div id="profile_wrap">
		<!--导航条-->
		<div class="profile_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<span style="float: right;margin-right: 0.1rem;" @click="onblock()">编辑</span>
			<p>编辑地址</p>
		</div>
		<!--地址-->
		<div class="profile_wrap_none">
			<ul>
				<li v-for="data in address" @click="onceAddress(data)">
					<p class="profile_wrap_none_p1">{{data.name}}<span>{{data.phone}}</span> 
					<span class="profile_wrap_none_sp" @click="delob(data.name)" v-if="block">删除</span></p>
					<p class="profile_wrap_none_p2">{{data.address_detail}} <span style="width:2.8rem;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">{{data.address}}</span></p>
				</li>
			</ul>
		</div>
		<!--增加地址-->
		<div class="profile_dizhi">
			<router-link to="/logined/profile/address">
				<i class="el-icon-arrow-right"></i>
				<li class="profile_dizhi_li">新增地址</li>
			</router-link>
		</div>
	</div>
</template>

<script>
	var Profile = {
		name:'profile',
		data(){
			return {
				address:[],
				block:false,
				cli:0
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			},
			delob(el){
				// console.log(el)
				this.address = this.address.filter((val)=>val.name!==el)
				this.$store.commit("localAddress",this.address)
			},
			onblock(){
				this.block=!this.block
			},
			onceAddress(el){
				this.$store.commit("onceAddress",el)
			}
		},
		created(){
			var c = localStorage.getItem("personLocal")
			var user_id=1;
			if (c) {
				var msg =JSON.parse(c)
				user_id= msg[0].id
			}else{
				user_id= this.$store.state.personLocal[0].id;
			}
			var _this=this
			var url= "https://elm.cangdu.org/v1/users/"+user_id+"/addresses";
			if (this.$store.state.localAddress.length) {
				this.address=this.$store.state.localAddress
			}else{
				this.axios.get(url).then(function(el){
					_this.address=el.data
					_this.$store.commit("localAddress",el.data)
				})
			}
		},
		watch:{
			
		}
		
	}
	export default Profile
</script>

<style scoped>

	#profile_wrap{
		position: fixed;
		left: 0;right: 0;
		top: 0;bottom: 0;
		background: rgb(245,245,245);
	}
	.profile_nav{
		width: 100%;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		color: black;
		line-height: 0.5rem;
	}
	.profile_nav>p,.profile_nav>span {
		color: white;
	}
	.el-icon-arrow-left{
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
		color: white;
	}
	.profile_dizhi_li{
		background: white;
		height: 0.4rem;
		line-height: 0.4rem;
		color: black;
		text-align: left;
		padding-left: 0.1rem;
	}
	.el-icon-arrow-right{
		float: right;
		line-height: 0.4rem;
	}
	/*新增地址*/
	.profile_wrap_none{
		display:flex;
		flex-direction:column;
		overflow: hidden;
		width: 100%;
		margin-top: 0.05rem;
		overflow-y: scroll;	
	}
	.profile_wrap_none::-webkit-scrollbar {display:none}
	.profile_wrap_none>ul>li{
		text-align: left;
		font-size: 0.18rem;
		height: 0.6rem;
		line-height: 0.3rem;
		box-sizing: border-box;
		padding-left: 0.1rem;
		background: white;
		margin-bottom: 0.08rem;
		background: #FFF8C3 !important;
		font-size: 0.16rem;
		
	}
	.profile_wrap_none_p1{
		color:black;
		font-weight: bold;
		position: relative;
	}
	.profile_wrap_none_p1 span{
		font-size: 0.14rem;
		color:#666;
		margin-left: 0.1rem;
	}
	.profile_wrap_none_p2{
		font-size: 0.15rem;
		display: flex;
		justify-content:flex-start;
	}
	.profile_wrap_none_p2 span{
		color:#666;
		margin-left: 0.05rem;
	}
	.profile_wrap_none_sp{
		position: absolute;
		right: 0.15rem;
		top: 0.12rem;
		background-color: red;
		padding: 0 0.05rem;
		border-radius: 0.05rem;
		color:#fff !important;
		font-size: 0.12rem !important;
	}
</style>



