<template>
<div id="mui_wrap"> 
	<!--导航-->
	<div id="mui_wrap_nav">
		<Muitop></Muitop>
	</div>
	<!--登录-->
	<div id="mui_wrap_denglu">
		<Denglu></Denglu>
	</div>
	<!--积分-->
	<div id="mui_wrap_jifen">
		<Jifen></Jifen>
	</div>
	<!--商城-->
	<div id="mui_wrap_shangcheng">
		<Shangcheng></Shangcheng>
	</div>
	<!--服务-->
	<div id="mui_wrap_fuwu">
		<Fuwu></Fuwu>
	</div>
	<footer>
		<Mui_navbottom></Mui_navbottom>
	</footer>
</div>
</template>

<script>
	import img01 from '@/../static/imgs/商品.png'

	import Muitop from '@/components/mui/muitop'
	import Denglu from '@/components/mui/denglu'
	import Jifen from '@/components/mui/jifen'
	import Shangcheng from '@/components/mui/shangcheng'
	import Fuwu from '@/components/mui/fuwu'
	import Mui_navbottom from "@/components/home/navBottom"
	
	var Mui = {
		name:"mui",
		components:{
			Muitop,
			Denglu,
			Jifen,
			Shangcheng,
			Fuwu,
			Mui_navbottom
		},
		methods:{
			open() {
		    	this.$message('这是一条消息提示');
		    },
		}
	}
	export default Mui
</script>

<style scoped>
	#mui_wrap{
		position: absolute;
		left: 0;right: 0;
		top: 0;bottom: 0;
		background: rgb(245,245,245);
		text-align: left;
		width: 100%;
	}
	#mui_wrap_nav{
		position:fixed;
		top: 0;
		left: 0;
		z-index: 5;
		height: 0.5rem;
		background: #3190e8;
		width: 100%;
	}
	#mui_wrap_denglu{
		padding-top:0.5rem; 
		border-bottom:0.01rem solid #e4e4e4;
		background-color: #fff;
		width: 100%;
		
	}
	footer{
		position: fixed;
		height:0.5rem;
		bottom: 0;
		z-index: 6;
		left: 0;
		width: 100%;
		background: #fff;
		overflow: hidden;
		text-align: center;
	}
</style>


