<template>
	<div id="history_wrap">
		<div id="historyhongbao_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;">历史红包</p>
		</div>
		<!--内容-->
		<div class="history_center">
			<ul>
				<li v-for="(datasa,index) in datas" :key="index">
					<div class="hongbao_li">
						<div>
							<!--抵用券钱数-->
							<p><span style="font-size: 0.18rem;">¥</span>{{datasa.amount}}
							</p>
							<!--抵用券使用条件-->
							<p style="color: rgb(160,160,160);font-size: 0.138rem;">{{datasa.description_map
	.sum_condition}}</p>
						</div>
						<div>
							<!--红包名字-->
							<p style="font-size: 0.18rem;">{{datasa.name}}</p>
							<!--到期时间-->
							<p style="font-size: 0.138rem;color: rgb(160,160,160)">{{datasa.description_map
	.validity_periods}}</p>
							<!--收货手机号-->
							<p style="font-size: 0.138rem;color: rgb(160,160,160)">{{datasa.description_map
	.phone}}</p>
						</div>
						<!--剩余日期-->
						<div class="history_center_div">
							<span >已过期</span>				</div>
					
					</div>
					<p v-if="datasa.limit_map">{{datasa.limit_map.restaurant_flavor_ids}}</p>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	var Historyhongbao = {
		name:"historyhongbao",
		data(){
			return {
				datas:[]
				
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			}
			
		},
		created(){
			var _this = this
			var id = +this.$store.state.personLocal[0].id
			var api_history = "https://elm.cangdu.org/promotion/v2/users/"+id+"/expired_hongbaos?limit=20&offset=0"
			this.axios.get(api_history).then(function(el){
				_this.count = el.data.length
				_this.datas = el.data
				console.log(el)
				
			})
		}
	}
	export default Historyhongbao
</script>

<style scoped>
	#history_wrap{
		position: absolute;
		width: 100%;
		left: 0;right: 0;
		top: 0;bottom: 0;
		background: rgb(245,245,245);
		text-align: center;
		overflow: hidden;
	}
	#historyhongbao_nav{
		overflow: hidden;
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		position: fixed;
		left: 0;top: 0;
		right: 0;
		z-index: 10;
	}
	#historyhongbao_nav>i{
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
		color: white;
	}
	.history_center{
		padding: 0.1rem;
		box-sizing: border-box;
		widows:100%;
		height: 100%;
		margin-top: 0.5rem;

	}
	.history_center>ul{
		width: 100%;
		height:100%;
		overflow-y: scroll;
	}
	.history_center>ul::-webkit-scrollbar{
		display: none;
	}
	.history_center>ul>li>p{
		background: rgb(249,249,249);
		padding: 0.1rem;
		box-sizing: border-box;
		text-align: left;
		color: rgb(160,160,160);
	}
	.history_center>ul>li{
		margin-bottom: 0.1rem;
	}
	.hongbao_li{
		border-top: 3px dashed rgb(160,160,160);
		padding: 0.1rem;
		box-sizing: border-box;
		overflow: hidden;
		width: 100%;
		background:white;
		position: relative;
		border-radius:2% ;
	}
	/*过期红包*/
	.history_center_div{
		margin-top: 0.2rem;
		width: 0.5rem;
		height: 0.5rem;
		float: right;
		font-size:0.138rem ;
		transform: rotate(-45deg);
		line-height: 0.5rem;
		border: 1px solid rgb(160,160,160);
		border-radius: 50%;
	}
	.history_center_span>span{
		color:rgb(160,160,160);
		
	}
	.hongbao_li>span{
		position: absolute;
		right: 0.1rem;
		top: 50%;
		transform: translateY(-50%);
	}

	/*左div*/
	.hongbao_li>div:nth-child(1){
		float: left;
		width: 30%;
		height: 1rem;
		text-align: center;
		border-right:1px solid rgb(160,160,160) ;

	}
	.hongbao_li>div:nth-child(1)>p:nth-child(1){
		margin: 0.2rem 0 0.1rem;
		color: (160,160,160);
		font-size: 0.4rem;
	}
	
	/*右div*/
	.hongbao_li>div:nth-child(2){
		height: 1rem;
		float: left;
		width: 50%;
		padding: 0.1rem;
		box-sizing: border-box;
		margin-left: 0.1rem;
		text-overflow: ellipsis;
		white-space: nowrap;
		
	}
	.hongbao_li>div:nth-child(2)>p:nth-child(1){
		margin: 0.1rem 0;
		
	}
	.hongbao_li>div:nth-child(2)>p{
		line-height: 0.2rem;
	}
</style>


