<template>
	<div id="city_hotcity">
		<!--热门城市-->
		<div>
			<p class="citylist_hostcity_hotp">热门城市</p>
			<ul>
				<el-row :gutter="24">
					<li v-for="datas in hot">
						<el-col :span="6" class="citylist_hotcity_el_col">
							<router-link :to="'/cutcity/'+datas.id" style="color: dodgerblue;">{{datas.name}}</router-link>
						</el-col>
					</li>
				</el-row>
			</ul>
		</div>
	</div>
</template>

<script>

import { Loading } from 'element-ui';
	var Hotcity = {
		name: "hotcity",
		data() {
			return {
				hot: [],
			}
		},
		created() {
		var loadingInstance = Loading.service({ fullscreen: true });
			var _this = this;
			//热门城市
			this.hotCity(function(data) {
				_this.hot = data
				loadingInstance.close();
			})
		}

	}

	export default Hotcity
</script>

<style scoped>
	/*热门城市*/
	#city_hotcity{
		width: 100%;
		overflow: hidden;
	}
	.citylist_hostcity_hotp{
		font-size: 0.15rem;
		height: 0.45rem;
		line-height: 0.45rem;
		border-top: 0.1px solid rgb(229,229,229);
		border-bottom:0px ;
		padding-left: 0.1rem;
	}
	.citylist_hotcity_el_col{
		border: 0.1px solid rgb(200,200,200);
		height: 0.45rem;
		text-align: center;
		line-height: 0.45rem;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	
</style>


