<template>	
	<div id="shop_shopActive">
		<div id="shopActive_center">
			<p class="shopActive_center_p1">{{msg.name}}</p>
			<div class="shopActive_center_p2">优惠信息</div>
			<ul>
				<li v-for="(val,ind) in msg.activities" v-if="val.icon_name">
					<div class="shop_lastMsg_ul_li_div1">{{val.icon_name}}</div>
					<div class="shop_lastMsg_ul_li_div2">{{val.description}}(app专享)</div>
				</li>
			</ul>
			<div class="shopActive_center_p2">商家公告</div>
			<p class="shopActive_center_p4">{{msg.promotion_info}}</p>
		</div>
		<div id="shopActive_none" @click="isHidden()">
			<i class="el-icon-close"></i>
		</div>
	</div>
</template>
<script>
	var shopActive={
		name:'shopActive',
		data(){
			return {
				msg:{},
				shopId:0,
			}
		},
		props:["hidd"],
		created(){
			this.shopId = this.$store.state.shopId
			var _this = this
			this.StoreMsg(this.shopId,function(el){
				_this.msg = el
			})
		},
		methods:{
			isHidden(){
				this.hidd()
			}
		}

	}
	export default shopActive

</script>
<style scoped>
	#shop_shopActive{
		width: 100%;
		height: 100%;
		color:#fff;
		text-align: left;
		position: relative;
	}
	#shopActive_center ul{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		overflow: hidden;
		margin-top: 0.12rem;
		margin-bottom: 0.13rem;
		padding-left: 0.18rem;
	}
	#shopActive_center ul li{
		display: flex;
		flex-direction:start;
		justify-content:flex-start;
		padding-left: 0.14rem;
		height:0.43rem;
		line-height: 0.38rem;
	}
	.shop_lastMsg_ul_li_div1{
		background-color: rgb(240, 115, 115);
		height: 0.16rem;
		line-height: 0.16rem;
		text-align: center;
		padding: 0.01rem 0.02rem;
		margin-top: 0.1rem;
		margin-right: 0.05rem;
		font-size: 0.12rem;
		font-weight: 600;
		color:#fff;
	}
	.shop_lastMsg_ul_li_div2{
		font-size: 0.13rem;
		width:3.2rem;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
	}
	.shopActive_center_p1{
		text-align: center;
		height:0.25rem;
		font-size: 0.18rem;
		font-weight: 500;
		line-height: 0.25rem;
		margin-top: 0.3rem;
		margin-bottom: 0.5rem;
	}
	.shopActive_center_p2{
		width:0.65rem;
		margin: 0 auto;
		font-size: 0.12rem;
		border-radius: 0.1rem;
		border:0.005rem solid #555;
		height:0.22rem;
		line-height: 0.22rem;
		text-align: center;
	}
	.shopActive_center_p4{
		font-size: 0.13rem;
		width:3.2rem;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
		margin-top: 0.25rem;
		padding-left: 0.3rem;
	}
	#shopActive_none{
		position: absolute;
		bottom: 0.55rem;
		left: 50%;
		transform:translateX(-50%);
		border: 1px solid #555;
		border-radius: 50%;
		text-align: center;
		width:0.45rem;
		height:0.45rem;
		line-height: 0.54rem;
	}
	#shopActive_none i{
		font-size: 0.28rem;
		line-height: 0.28;
		text-align: center;
		color:#555;
	}
</style>


