<template>
	<div id="orderBody">
		<nav>
			<Order_Top></Order_Top>
		</nav>
		<div id="orderFood">
			<div id="AllorderMsg">
				<ul>
					<li id="AllorderMsg_li" v-for="(val,ind) in AllorderMsg" :key="ind">
						<div class="AllorderMsg_li_div1">
							<div class="AllorderMsg_li_div1_p1">
								<img src="../home/sign/buit01.png" alt="">
								<span>{{val.restaurant_name}} ></span>
							</div>
							<div class="AllorderMsg_li_div1_p2">交易完成</div>
						</div>
						<ul class="AllorderMsg_li_div2" v-if="val.basket" @click="loopItem(val)">
							<li v-for="(item,index) in val.basket.group[0]" style="margin-top:0.2rem;border-bottom:0.001rem solid #aaa;padding-bottom:0.1rem;">
								<div class="AllorderMsg_li_div2_p1">
									<img src="../home/sign/buit01.png" alt="">
									<span>{{item.name}}</span>
								</div>
								<div class="AllorderMsg_li_div2_p2">
									<span>单价: {{item.price}}</span><span>数量: * {{item.quantity}}</span>
								</div>
							</li>
						</ul>
						<div class="AllorderMsg_li_div3">
							<div class="AllorderMsg_li_div3_p1">{{val.formatted_created_at}}</div>
							<div class="AllorderMsg_li_div3_p2">合计: <span>100</span></div>
						</div>
						<div class="AllorderMsg_li_div4">
							<!-- <div class="AllorderMsg_li_div4_p1" @click="delOrder(ind)">删除订单</div> -->
							<div class="AllorderMsg_li_div4_p2" @click="goBuyAlign(val.id)">再来一单</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
	
</template>
<script>
import Vue from 'vue'  ;
import Order_Top from "@/components/order/ordertop"
import Order_Msg from "@/components/order/orderMsg"
import Order_navbottom from "@/components/home/navBottom"
	var AllorderMsg ={
		name:"AllorderMsg",
		data(){
			return {
				AllorderMsg:[]
			}
		},
		created(){
			var _this= this
			var userId = this.$store.state.personLocal[0].id
			var url = "https://elm.cangdu.org/bos/v2/users/1/orders?limit=10&offset=0"
			if (this.$store.state.flowOrderMsg.length) {
				this.AllorderMsg = this.$store.state.flowOrderMsg
			}else{
				this.axios.get(url).then(function(el){
					_this.AllorderMsg = el.data
					_this.$store.commit("AllOrderMsg",el.data)
					_this.$store.commit("flowOrderMsg",el.data)
				})
			}
		},
		methods:{
			delOrder(ind){
				this.AllorderMsg.splice(ind,1)
				this.$store.commit("flowOrderMsg",this.AllorderMsg)
				console.log(this.$store.state.flowOrderMsg)
			},
			goBuyAlign(el){
				this.$store.commit("shopIds",el)
				this.$router.push({name:'shop'})
			},
			loopItem(el){
				console.log(el)
			}
		},
		components:{
		   Order_navbottom,
		   Order_Top,
		   Order_Msg
		},
	}
	export default AllorderMsg
</script>
<style scoped>
#orderBody{
		height: 100%;
		background-color: #f5f5f5;
	}
	nav{
		position:fixed;
		top: 0;
		left: 0;
		z-index: 5;
		height: 0.5rem;
		background: #3190e8;
		width: 100%;
	}
	#orderFood{
		height: 1.9rem;
		padding-top:0.5rem; 
		border-bottom:0.01rem solid #e4e4e4;
		background-color: #fff;
	}
	footer{
		position: fixed;
		height:0.5rem;
		bottom: 0;
		z-index: 6;
		left: 0;
		width: 100%;
		background: #fff;
		overflow: hidden;
	}
	#AllorderMsg{
		width: 100%;
		overflow: hidden;
		position: relative;
		background-color: #f5f5f5;
		text-align: left;
		margin-bottom: 0.5rem;

	}
	#AllorderMsg>ul{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
	}
	#AllorderMsg_top{
		padding:0 0.15rem;
		background-color: #fff;
		display: flex;
		justify-content:space-between;
		font-size: 0.18rem;
		color:#555;
		line-height: 0.46rem;
		height:0.46rem;
	}
	.AllorderMsg_top_div1{
		position: relative;
	}
	.AllorderMsg_top_div1 img{
		width: 0.28rem;
		position: absolute;
		left: 0;
		top:50%;
		transform:translateY(-50%);
	}
	.AllorderMsg_top_div1 span{
		margin-left: 0.34rem;
		line-height: 0.48rem;
	}
	.AllorderMsg_top_div2{
		font-size: 0.14rem;
		color:#aaa;
	}
	#AllorderMsg_li{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		padding: 0 0.15rem;
		background-color: #fff;
		margin-top: 0.1rem;
	}
	.AllorderMsg_li_div1{
		height:0.35rem;
		line-height: 0.35rem;
		font-size: 0.13rem;
		display: flex;
		justify-content:space-between;
	}
	.AllorderMsg_li_div1_p1{
		position: relative;
	}
	.AllorderMsg_li_div1_p1 img{
		position: absolute;
		width: 0.2rem;
		left: 0;
		top: 50%;
		transform:translateY(-60%);
	}
	.AllorderMsg_li_div1_p1 span{
		margin-left: 0.25rem;
	}
	.AllorderMsg_li_div1_p2{
		font-size: 0.14rem;
	}
	img{
		width: 0.18rem;
	}
	.AllorderMsg_li_div2{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		margin-top: 0.05rem;

	}
	.AllorderMsg_li_div2 li{
		display: flex;
		justify-content:space-between;
		height:0.7rem;
		margin-top: 0.05rem;
	}
	.AllorderMsg_li_div2_p1{
		height: 100%;
		position: relative;
	}
	.AllorderMsg_li_div2_p1 img{
		height: 100%;
		width: 0.7rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.AllorderMsg_li_div2_p1 span{
		font-size: 0.15rem;
		color:#666;
		margin-left: 0.76rem;

	}
	.AllorderMsg_li_div2_p2{
		display: flex;
		flex-direction:column;
		justify-content:space-between;
		align-items:flex-end;
		font-size: 0.16rem;
	}
	.AllorderMsg_li_div3{
		display: flex;
		justify-content:space-between;
		height:0.4rem;
		border-bottom: 0.005rem solid #aaa;
	}
	.AllorderMsg_li_div3_p1{
		font-size: 0.12rem;
		color:#aaa;
		line-height:0.4rem; 

	}
	.AllorderMsg_li_div3_p1 span{
		margin-left: 0.05rem;
	}
	.AllorderMsg_li_div3_p2{
		font-size: 0.14rem;
		line-height:0.4rem;
		margin-right: 0.05rem;
	}
	.AllorderMsg_li_div4{
		display: flex;
		justify-content:flex-end;
		height: 0.5rem;
		margin:0.02rem;
		font-size: 0.12rem;
	}
	.AllorderMsg_li_div4_p1{
		width: 0.8rem;
		border: 0.005rem solid #555;
		text-align: center;
		height:0.32rem;
		text-align: center;
		line-height: 0.32rem;
		margin-top: 0.09rem;


	}
	.AllorderMsg_li_div4_p2{
		width: 0.8rem;
		border: 0.005rem solid #3190E8;
		color: #3190E8;
		height:0.32rem;
		text-align: center;
		line-height: 0.32rem;
		margin-top: 0.09rem;
		margin-left: 0.15rem;

	}
</style>


