<template>
	<div id="home_nb_navD">
		<ul id="home_nb_nav">
			<li v-for="(val,ind) in homeBot" @click="gitBtns(val,ind)">
				<router-link :to="'/'+val.clr" id="home_nb_navliA">
					<div>
						<img :src="val.img" :alt="val.name" v-if="val.cli">
						<img :src="val.img2" :alt="val.name" v-if="!val.cli">
					</div>
					<p>{{val.name}}</p>
				</router-link>
			</li>
		</ul>
	</div>
</template>
<script>
	import Vue from 'vue';
	import img01 from "@/components/home/sign/buit01.png"
	import img02 from "@/components/home/sign/buit02.png"
	import img03 from "@/components/home/sign/buit03.png"
	import img04 from "@/components/home/sign/buit04.png"
	import img05 from "@/components/home/sign/buit05.png"
	import img06 from "@/components/home/sign/buit06.png"
	import img07 from "@/components/home/sign/buit07.png"
	import img08 from "@/components/home/sign/buit08.png"
	var NavBottom = {
		name:"NavBottom",
		data(){
			return {
				homeBot:[
					{name:"外卖",img:img01,img2:img05,cli:true,clr:"home"},
					{name:"搜索",img:img02,img2:img06,cli:true,clr:"search"},
					{name:"订单",img:img03,img2:img07,cli:true,clr:"order"},
					{name:"我的",img:img04,img2:img08,cli:true,clr:'mui'},
				],
			}
		},
		methods:{
			gitBtns(val,ind){
				this.homeBot.map(function(value,index){
					value.cli=true;
				})
				val.cli=false;
			}
		}

	}
	export default NavBottom

</script>
<style scoped>
	#home_nb_navD{
		width: 100%;
		height: 100%;
	}
	#home_nb_nav{
		display: flex;
		flex-wrap:nowrap;
		justify-content:space-around;
		width: 100%;
		height: 100%;
	}
	#home_nb_nav li{
	}
	#home_nb_navliA{
		display: flex;
		flex-wrap:nowrap;
		flex-direction:column;
		justify-content:flex-start;
		color:#666;
		height: 100%;
	}
	#home_nb_navliA p{
		margin-top: 0.03rem;
		font-size: 0.14rem;
	}
	#home_nb_navliA div{
		margin-top: 0.01rem;
	}
	#home_nb_navliA img{
		width: 0.20rem;
	}

</style>


