<template>
	<div id="denglu_wrap">

			<img src="../../../static/imgs/商品.png" alt="" style="width:0.5rem;" />
			
			<router-link to="/login" v-if="isId==''">
				<div class="denglu_wrap_div">
					<span>{{title}}</span>
					<i class="el-icon-mobile-phone">暂无绑定手机号</i>
					<i class="el-icon-arrow-right"></i>
				</div>
			</router-link>
			<router-link to="/logined" v-else>
				<div class="denglu_wrap_div">
					<span>{{title}}</span>
					<i class="el-icon-mobile-phone">暂无绑定手机号</i>
					<i class="el-icon-arrow-right"></i>
				</div>
			</router-link>
	</div>
</template>

<script>
	var Denglu = {
		name:"denglu",
		data(){
			return {
				title:"",
				isId:""
				
			}
		},
		created(){
//			console.log(localStorage.getItem("personLocal"))
			if(localStorage.getItem("personLocal")!=null){
				this.$store.commit("getLocal","personLocal")
			}
			this.title = this.$store.state.personLocal[0].title
			this.isId = this.$store.state.personLocal[0].id
		}
		
	}
	export default Denglu
</script>

<style scoped>
	#denglu_wrap{
		border-top:1px solid rgb(245,245,245);
		background: rgb(49,144,232);
		overflow: hidden;
		height: 0.7rem;
		position: relative;
		font-size: 0.138rem;
		width: 100%;
	}
	#denglu_wrap img{
		float: left;
		margin: 0.1rem;
	}
	#denglu_wrap span{
		float: left;
		margin-top: 0.2rem;
	}
	.el-icon-mobile-phone{
		position: absolute;
		left: 0.7rem;
		top: 65%;
		transform: translateY(-50%);
	}
	.el-icon-arrow-right{
		position: absolute;
		right: 0.1rem;
		top: 50%;
		transform: translateY(-50%);
	}
	span,i{
		color:white ;
	}
	.denglu_wrap_div{
		height: 100%;
	}
</style>


