<template>
	<div id="clubpass_wrap">
		<!--导航-->
		<div class="clubpass_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;line-height: 0.5rem;">兑换会员</p>
		</div>
		<!--内容-->
		<div class="clubpass_center"> 
			<p>成功兑换后将关联到当前账号:
				<span style="font-weight: bolder;">{{title}}</span>
			</p>
			<input type="text"	placeholder="请输入10位卡号"/>
			<input type="text" placeholder="请输入6位卡密"/>
			<el-button type="success" class="clubpass_center_btn">兑换</el-button>
		</div>
		<!--底部-->
		<div class="clubpass_bottom">
			<p>---温馨提示---</p>
			<ul>
				<li>新兑换的会员服务，权益以「会员说明」为准。</li>
				<li>月卡：30次减免配送费。</li>
				<li>季卡：90次减免配送费。</li>
				<li>年卡：360次减免配送费。</li>
				<li>＊仅限蜂鸟专送订单，每日最多减免3单，每单最高减免4元（一个月按31天计算）</li>
			</ul>
		</div>
		
	</div>
</template>

<script>
	var Clubpass = {
		name:"clubpass",
		data(){
			return {
				title:""
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		created(){
			this.title = this.$store.state.personLocal[0].title
		}
	}
	export default Clubpass
</script>

<style scoped>
	#clubpass_wrap{
		position: absolute;
		left: 0;right: 0;
		top: 0;bottom: 0;
		background: rgb(245,245,245);
		text-align: left;
	}
	.clubpass_nav{
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
	}
	.clubpass_nav>i{
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
		color: white;
	}
	.clubpass_center>p{
		padding: 0.1rem;
		box-sizing: border-box;
		height: 0.5rem;
		line-height: 0.3rem;
	}
	.clubpass_center>input{
		width: 100%;
		height: 0.5rem;
		background: white;
		padding: 0.05rem;
		box-sizing: border-box;
	}
	/*按钮*/
	.clubpass_center_btn{
		width: 90%;
		margin-left: 5%;
		margin-top: 0.2rem;
	}
	/*温馨提示*/
	.clubpass_bottom{
		width: 70%;
		margin: 0.2rem auto ;
		text-align: left;
		font-size: 0.15rem;
		color: rgb(160,160,160);
	}
	.clubpass_bottom>p{
		line-height: 0.4rem;
		text-align: center;
	}
	.clubpass_bottom>ul>li{
		line-height: 0.2rem;
	}
</style>


