<template>
	<div id="dangqianyue_wrap">
		<span>当前余额</span>
		<router-link to="/balance/issoe" style="float: right;font-size:0.12rem;">
			<i class="el-icon-question"></i>
			<span>余额说明</span>
		</router-link>
		<h1>0.00元</h1>
		<div  class="dangqianyue_wrap_but">
			<el-button type="success" @click="centerDialogVisible = true">提现</el-button>
		</div>
		<el-dialog title="提示" :visible.sync="centerDialogVisible" width="75%" style="text-align: center;">
				<p style="font-size: 0.7rem;font-weight: bolder;color: red;">!</p>
				<span style="font-size: 0.2rem;">请在饿了么APP中打开</span>
				<span slot="footer" class="dialog-footer">
					<el-button type="primary" @click="returnMui()" style="width: 100%;">确定</el-button>
				</span>
		</el-dialog>
	</div>
</template>

<script>
	var Dangqianyue={
		name:"dangqianyue",
		data(){
			return{
				centerDialogVisible: false,
			}
		},
		methods:{
			returnMui(){
				this.centerDialogVisible = false;
				
			},
		}
		
	}
	export default Dangqianyue
</script>

<style scoped>
	#dangqianyue_wrap{
		width: 100%;
		background: white;
		padding: 0.05rem;
		border-radius: 0.05rem;
		text-align: left;
		box-sizing: border-box;
	}
	h1{
		margin: 0.2rem 0;
	}
	#dangqianyue_wrap a{
		color:#666;
	}
	#dangqianyue_wrap a span{
		font-size: 0.14rem;
		font-weight: 400;
	}
	.dangqianyue_wrap_but{
		width:100%;
		height: 0.4rem;
		text-align: center;
	}
	.dangqianyue_wrap_but>button{
		width: 96%;
		margin: 0 auto;
	}
</style>


