<template>
	<div id="dangqianjifen_wrap">
		<span>当前积分</span>
		<router-link to="/points/issoe" style="float: right;font-size:0.12rem;">
			<i class="el-icon-question"></i>
			<span>积分说明</span>
		</router-link>
		<h1>0  分</h1>
		<div  class="dangqianjifen_wrap_but">
			<el-button type="danger" @click="centerDialogVisible = true">积分兑换商品</el-button>
		</div>
		<el-dialog title="提示" :visible.sync="centerDialogVisible" width="75%" style="text-align: center;">
				<p style="font-size: 0.7rem;font-weight: bolder;color: red;">!</p>
				<span style="font-size: 0.2rem;">请在饿了么APP中打开</span>
				<span slot="footer" class="dialog-footer">
					<el-button type="primary" @click="returnMui()" style="width: 100%;">确定</el-button>
				</span>
		</el-dialog>
	</div>
</template>

<script>
	var Dangqianjifen={
		name:"dangqianjifen",
		data(){
			return{
				centerDialogVisible: false,
			}
		},
		methods:{
			returnMui(){
				this.centerDialogVisible = false;
				
			},
		}
		
	}
	export default Dangqianjifen
</script>

<style scoped>
	#dangqianjifen_wrap{
		background: white;
		padding: 0.05rem;
		border-radius: 0.05rem;
		text-align: left;
	}
	h1{
		margin: 0.2rem 0;
		color: red;
	}
	.dangqianjifen_wrap_but{
		width:100%;
		height: 0.4rem;
		text-align: center;
	}
	.dangqianjifen_wrap_but>button{
		width: 96%;
		margin: 0 auto;
	}
</style>


