<template>
	<div >
		<!--导航-->
		<div class="wodejifen_wrap_nav">
			<router-link to="">
				<i class="el-icon-arrow-left" @click="sear()"></i>
			</router-link>
			<p>我的积分</p>
		</div>
		<!--当前积分-->
		<div class="wodejifen_wrap_dangqian">
			<Dangqianjifen></Dangqianjifen>
		</div>
		<!--积分记录-->
		<div>
			<Jifenjilu></Jifenjilu>
		</div>
	</div>
</template>

<script>
	import Dangqianjifen from '@/components/myJU/wodejifens/dangqianjifen'
	import Jifenjilu from '@/components/myJU/wodejifens/jifenjilu'
	var Wodejifen = {
		name:"wodejifen",
		components:{
			Dangqianjifen,
			Jifenjilu
		},
		methods:{
			sear(){
				this.$router.go(-1)
			}
		}
	}
	export default Wodejifen
</script>

<style scoped>
	.wodejifen_wrap_nav {
		width: 100%;
		line-height: 0.5rem;
		text-align: center;
		background: #3190E8;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
	}
	p,i,h3{
		color: white;
	}
	i {
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	.wodejifen_wrap_dangqian{
		background: rgb(49,144,232);
		padding: 0.05rem;
		border-top:1px solid rgb(245,245,245) ;
	}
</style>


