<template>
	<div id="orderMsg">
		<ul>
			<div id="orderMsg_top" @click="allorderMsg()">
				<div class="orderMsg_top_div1">
					<img src="../home/sign/buit01.png" alt="">
					<span>饿了吗订单</span>
				</div>
				<div class="orderMsg_top_div2">点击查看 ></div>
			</div>
			<li id="orderMsg_li" v-for="(val,ind) in orderMsg" :key="ind">
				<div class="orderMsg_li_div1">
					<div class="orderMsg_li_div1_p1">
						<img src="../home/sign/buit01.png" alt="">
						<span>{{val.restaurant_name}} ></span>
					</div>
					<div class="orderMsg_li_div1_p2">交易完成</div>
				</div>
				<ul class="orderMsg_li_div2" v-if="val.basket" @click="loopItem(val)">
					<li v-for="(item,index) in val.basket.group[0]" style="margin-top:0.2rem;border-bottom:0.001rem solid #aaa;padding-bottom:0.1rem;">
						<div class="orderMsg_li_div2_p1">
							<img src="../home/sign/buit01.png" alt="">
							<span>{{item.name}}</span>
						</div>
						<div class="orderMsg_li_div2_p2">
							<span>单价: {{item.price}}</span><span>数量: * {{item.quantity}}</span>
						</div>
					</li>
				</ul>
				<div class="orderMsg_li_div3">
					<div class="orderMsg_li_div3_p1">{{val.formatted_created_at}}</div>
					<div class="orderMsg_li_div3_p2">合计: <span>100</span></div>
				</div>
				<div class="orderMsg_li_div4">
					<div class="orderMsg_li_div4_p1" @click="delOrder(ind)">删除订单</div>
					<div class="orderMsg_li_div4_p2" @click="goBuyAlign(val.id)">再来一单</div>
				</div>
			</li>
		</ul>
	</div>
</template>
<script>
	var orderMsg ={
		name:"orderMsg",
		data(){
			return {
				orderMsg:[]
			}
		},
		created(){
			var _this= this
			var userId = this.$store.state.personLocal[0].id
			var url = "https://elm.cangdu.org/bos/v2/users/1/orders?limit=10&offset=0"

			if (this.$store.state.flowOrderMsg.length) {
				this.orderMsg = this.$store.state.flowOrderMsg
			}else{
				this.axios.get(url).then(function(el){
					_this.orderMsg = el.data
					_this.$store.commit("AllOrderMsg",el.data)
					_this.$store.commit("flowOrderMsg",el.data)
				})
			}

		},
		methods:{
			delOrder(ind){
				this.orderMsg.splice(ind,1)
				this.$store.commit("flowOrderMsg",this.orderMsg)
				console.log(this.$store.state.flowOrderMsg)
			},
			goBuyAlign(el){
				this.$store.commit("shopIds",el)
				this.$router.push({name:'shop'})
			},
			loopItem(el){
			},
			allorderMsg(){
				this.$router.push({name:'allorderMsg'})
			}
		}
	}
	export default orderMsg
</script>
<style scoped>
	#orderMsg{
		width: 100%;
		overflow: hidden;
		position: relative;
		background-color: #f5f5f5;
		text-align: left;
		margin-bottom: 0.5rem;

	}
	#orderMsg>ul{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
	}
	#orderMsg_top{
		padding:0 0.15rem;
		background-color: #fff;
		display: flex;
		justify-content:space-between;
		font-size: 0.18rem;
		color:#555;
		line-height: 0.46rem;
		height:0.46rem;
	}
	.orderMsg_top_div1{
		position: relative;
	}
	.orderMsg_top_div1 img{
		width: 0.28rem;
		position: absolute;
		left: 0;
		top:50%;
		transform:translateY(-50%);
	}
	.orderMsg_top_div1 span{
		margin-left: 0.34rem;
		line-height: 0.48rem;
	}
	.orderMsg_top_div2{
		font-size: 0.14rem;
		color:#aaa;
	}
	#orderMsg_li{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		padding: 0 0.15rem;
		background-color: #fff;
		margin-top: 0.1rem;
	}
	.orderMsg_li_div1{
		height:0.35rem;
		line-height: 0.35rem;
		font-size: 0.13rem;
		display: flex;
		justify-content:space-between;
	}
	.orderMsg_li_div1_p1{
		position: relative;
	}
	.orderMsg_li_div1_p1 img{
		position: absolute;
		width: 0.2rem;
		left: 0;
		top: 50%;
		transform:translateY(-60%);
	}
	.orderMsg_li_div1_p1 span{
		margin-left: 0.25rem;
	}
	.orderMsg_li_div1_p2{
		font-size: 0.14rem;
	}
	img{
		width: 0.18rem;
	}
	.orderMsg_li_div2{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		margin-top: 0.05rem;

	}
	.orderMsg_li_div2 li{
		display: flex;
		justify-content:space-between;
		height:0.7rem;
		margin-top: 0.05rem;
	}
	.orderMsg_li_div2_p1{
		height: 100%;
		position: relative;
	}
	.orderMsg_li_div2_p1 img{
		height: 100%;
		width: 0.7rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.orderMsg_li_div2_p1 span{
		font-size: 0.15rem;
		color:#666;
		margin-left: 0.76rem;

	}
	.orderMsg_li_div2_p2{
		display: flex;
		flex-direction:column;
		justify-content:space-between;
		align-items:flex-end;
		font-size: 0.16rem;
	}
	.orderMsg_li_div3{
		display: flex;
		justify-content:space-between;
		height:0.4rem;
		border-bottom: 0.005rem solid #aaa;
	}
	.orderMsg_li_div3_p1{
		font-size: 0.12rem;
		color:#aaa;
		line-height:0.4rem; 

	}
	.orderMsg_li_div3_p1 span{
		margin-left: 0.05rem;
	}
	.orderMsg_li_div3_p2{
		font-size: 0.14rem;
		line-height:0.4rem;
		margin-right: 0.05rem;
	}
	.orderMsg_li_div4{
		display: flex;
		justify-content:flex-end;
		height: 0.5rem;
		margin:0.02rem;
		font-size: 0.12rem;

	}
	.orderMsg_li_div4_p1{
		width: 0.8rem;
		border: 0.005rem solid #555;
		text-align: center;
		height:0.32rem;
		text-align: center;
		line-height: 0.32rem;
		margin-top: 0.09rem;

	}
	.orderMsg_li_div4_p2{
		width: 0.8rem;
		border: 0.005rem solid #3190E8;
		color: #3190E8;
		height:0.32rem;
		text-align: center;
		line-height: 0.32rem;
		margin-top: 0.09rem;
		margin-left: 0.15rem;
	}
</style>


