<template>	
	<div class="Home_GT_DD" @swipeLeft="swipeLeft()" @swipeRight="swipeRight()">
		<div class="Home_GT_div1">
			<ol class="Home_GT_ol">
			<slider :options="options">
				<slideritem>
				<li v-for="(val,ind) in msg" class="Home_GT_Oli" v-if="val.id<100" @click="homeTiBtns(val.id,val.title)">
					<router-link :to="{name:'searchIf'}">
						<img :src="'https://fuss10.elemecdn.com'+val.image_url" alt="美食">
						<p>{{val.title}}</p>
					</router-link>
				</li>
				</slideritem>
				<slideritem>
				<li v-for="(val,ind) in msg" class="Home_GT_Oli" v-if="val.id>100" @click="homeTiBtns(val.id,val.title)">
					<router-link :to="{name:'searchIf'}">
						<img :src="'https://fuss10.elemecdn.com'+val.image_url" alt="美食">
						<p>{{val.title}}</p>
					</router-link>
				</li>
				</slideritem>
			</slider>
			</ol>
		</div>
	</div>
</template>
<script>

import { slider, slideritem } from 'vue-concise-slider'
import { Loading } from 'element-ui';
	var guideTitle={
		name:"guideTitle",
		data(){
			return {
				msg:[],
				options: {
		          	direction:'horizontal',
		        }
			}
		},
		created(){
			var loadingInstance = Loading.service({ fullscreen: true });
			var _this = this;
			this.foodClass(function(el){
				_this.msg=el
				loadingInstance.close();
			})
		},
		methods:{
			homeTiBtns(el,bl){
				var guideTitleMsg={id:el,title:bl}
				this.$store.commit("guideTitle",guideTitleMsg)
				this.$router.push({name:'searchIf'})
			}
		},
		components: {
		    slider,
		    slideritem
	    },
	}
	export default guideTitle
</script>
<style scpoed>
	
	.slider-item{
		display: flex;
		flex-direction:row;
		flex-wrap:wrap;
	}
	.slider-pagination-bullets{
		bottom: 0.01rem !important;
	}
	.slider-pagination-bullet-active{
		background-color:#007aff !important;
	}
	.Home_GT_Oli{
		width:0.93rem;
		height:0.85rem;
	}
	.Home_GT_Oli img{
		width: 0.38rem;
		margin-top: 0.05rem;
	}
	.Home_GT_Oli p{
		font-size: 0.16rem;
		margin-top: 0.1rem;
	}
	.Home_GT_DD{
		position: absolute;
		height:1.90rem;
	}
	.Home_GT_btns {
		/*width: 100%;*/
		height:0.2rem;
		position: absolute;
		bottom: 0.05rem;
		display: flex;
		/*主轴方向*/
		justify-content: center;
		/*侧轴方向*/
		align-items: center;
		margin:0 auto;
		left:0;
		right:0;
	}
	.Home_GT_btns li {
		background: #ccc;
		margin: 0 0.05rem;
		cursor: pointer;
		width: 0.08rem;
		height: 0.08rem;
		border-radius: 50%;
	}
	li.Home_GT_active{
		background: #007aff;
	}
	.Home_GT_DD a{
		color:#666;
	}
</style>


