<template>
	<div id="shop_sb">
		<div id="shop_sb_car" @click="seeBuy()">
			<img src="./shopImg/imgs10.jpg" alt="">
		</div>
		<div id="shop_sb_body">
			<div class="shop_sb_left" @click="seeBuy()">
				<!--价格-->
				<p class="shop_sb_p1">{{moneys}}</p>
				<!--配送费-->
				<p class="shop_sb_p2">{{mmp}}</p>
			</div>
			<!--结算-->
			<div :class="{shop_sb_right:true,change_active:change_active}" @click="payMoney(omg)">{{omg}}</div>
		</div>
		<div id="shop_sb_bug" v-if="goCheck&&playBuy.length">
			<div class="shop_sb_bug_div1">
				<div class="shop_sb_bug_div1_left">购物车</div>
				<div class="shop_sb_bug_div1_right" @click="delThis()">
					<i class="el-icon-delete"></i>
					<span>清空</span>
				</div>
			</div>
			<ol class="shop_sb_bug_div2">
				<li v-for="(val,ind) in playBuy" v-if="val.lens">
					<div class="shop_sb_bug_div2_left">
						<p>{{val.name}}</p>
						<p>{{val.msg}}</p>
					</div>
					<div class="shop_sb_bug_div2_cent">{{val.prise}}</div>
					<div class="shop_sb_bug_div2_right">
						<i class="el-icon-remove" style="color:#999;font-size:0.2rem" @click="delfrult(ind,val.id)"></i>
						{{val.lens}}
						<i class="el-icon-circle-plus" style="color:#3190e8;font-size:0.2rem" @click="addfrult(ind,val.id)"></i>
					</div>
				</li>
			</ol>
		</div>
		<div id="backgroundColor" @click="hiddenMe()" v-if="goCheck&&playBuy.length"></div>
	</div>
</template>
<script>
	var shopBug={
		name:"shopBug",
		data(){
			return {
				msg:{},
				omg:'',
				mmp:'',
				// 商铺食物
				shopfoods:[],
				// 食物
				foods:[],
				// 左边li单击时,让我知道
				isClick:-1,
				onceClick:0,
				// 为下边儿准备的点
				playBuy:[],
				goCheck:false,
				change_active:false,
			}
		},
		computed:{
			// 计算属性
			// 计算当前money的值,于store的allPrise绑定
			moneys(){
				var playBuys = this.$store.state.playBuy
				var playBuy=[]
				var _this = this
//				console.log(playBuys)
				playBuys.forEach(items=>{
					_this.foods.forEach(function(value){
						value.foods.forEach(val=>{
							if (val._id == items.id) {
								playBuy.push(items)
							};
						})
					})
				})
				var prise=0;
				playBuy.forEach(val=>{
					prise += val.lens*val.prise
				})
				return prise
			},
		},
		props:["cliAdd","cliDel","cliClear"],
		created(){
			//我要获取到当前餐厅id
			//当开始创建的会后,要查询到连个值,一个是当前的内容,一个是查询到当前起送价格,然后赋值到不同组里面,有用 msg omg mmp
			var shopid= this.$store.state.shopId
			var _this=this
			this.StoreMsg(shopid,function(el){
				_this.msg=el
				_this.omg="还差"+el.float_minimum_order_amount+"元起送"
				if (el.piecewise_agent_fee) {
					_this.mmp=el.piecewise_agent_fee.tips
				};
			});
			// 获取到当前餐厅的食物,让我可以编辑shopfood
			this.shopfooder(shopid,function(el){
				_this.shopfoods=el
				_this.foods = el
			})
		},
		updated(){
			if(this.moneys>=20){
				this.omg="去结算"
				this.change_active=true
			}
		},
		methods:{
			// 这是一个随时调用的内容
			cleckfoods(){
				
			},
			// 隐藏背景大框
			hiddenMe(){
				this.goCheck=false;
			},

			
			// 点我点击查看购物车的时候,如果有内容,我就显示,如果没有内容,我就不显示
			seeBuy(){
				// 把购物车整理了
				var playBuys = this.$store.state.playBuy
				var playBuy=[]
				var _this = this
				playBuys.forEach(items=>{
					_this.foods.forEach(function(value){
						value.foods.forEach(val=>{
							if (val._id == items.id) {
								playBuy.push(items)
							};
						})
					})
				})
				this.playBuy=playBuy
				// 进行判断
				if (this.goCheck) {
					this.goCheck=false
				}else{
					this.goCheck=true
				}
				if(!this.$store.state.playBuy.length){
					this.goCheck=false
				}
			},
			// 当点击删除的时候,就要所有的清空,并且使显示购物车的环境消失
			delThis(){
				this.$store.commit("delPriceAll")
				this.goCheck=false
				this.cliClear()
			},
			// 点击+或者-时的操作
			// 传过去当前index,让公用方法可以查询到当前信息,让它的数据点在于conts的增加,这个样子,在我计算的时候,可以更好地计算
			addfrult(el,id){
				this.$store.commit("addfrult",el)
				this.cliAdd(id)
			},
			delfrult(el,id){
				this.$store.commit("delfrult",el)
				this.cliDel(id)
			},

			payMoney(el){
				if (el == "去结算") {
					this.$router.push({name:"payMoney"})
				};
			}
		},
		watch:{
			moneys(news){
			// 思维 -- 当钱数为0的时候,就让购物车消失,当20-钱数>0说明不足起送价格,就不弄让你选择,
			//中间穿插true和false代表可选,和不可选
				if(this.moneys==0){
					this.goCheck=false;
					this.$store.commit("delPriceAll")
				}
				if(this.msg.float_minimum_order_amount-this.moneys>0){
					var a=this.msg.float_minimum_order_amount-this.moneys
					this.omg="还差"+a+"元起送"
					this.change_active=false
				}else{
					this.omg="去结算"
					this.change_active=true
				}
			}
		}
	}
	export default shopBug
</script>
<style scpoed>
	/*蒙版*/
	#backgroundColor{
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0,0.3);
		position: fixed;
		z-index: 1;
		left: 0;
		top: 0;
	}
	#shop_sb{
		position: relative;
		background: #3d3d3f;
		height:100%;
		color:#fff;
	}
	#shop_sb_car{
		position: absolute;
		left: 0.14rem;
		top: -0.2rem;
		width:0.52rem;
		height:0.52rem;
		border-radius: 50%;
		text-align: center;
		line-height: 0.65rem;
		border:1px solid #555;
		background: #3d3d3f;
		opacity: 1;
		z-index: 5;
	}
	#shop_sb_car img{
		width: 0.25rem;
		height: 0.25rem;
	}
	#shop_sb_body{
		width: 100%;
		height: 100%;
		display: flex;
		justify-content:space-between;
	}
	.shop_sb_left{
		margin-left: 0.8rem;
		text-align: left;
	}
	.shop_sb_p1{
		font-size: 0.22rem;
		font-weight: bold;
		margin-top: 0.03rem;
	}
	.shop_sb_p2{
		margin-top: 0.06rem;
		font-size: 0.12rem;
	}
	.shop_sb_right{
		height:100%;
		line-height: 0.5rem;
		background: #555;
		padding-left: 0.15rem;
		padding-right: 0.15rem;
		color:white;
		font-weight: 700;
	}
	#shop_sb_bug{
		position: absolute;
		bottom:0.48rem;
		width:100%;
		z-index: 3;
		background: #eceff1;
		display: flex;
		justify-content:flex-start;
		flex-direction:column;
		align-items:center;
		color:#666;
	}
	.shop_sb_bug_div1{
		width:3.45rem;
		display: flex;
		justify-content:space-between;
		flex-direction:row;
		height:0.46rem;
		line-height: 0.46rem;
	}
	.shop_sb_bug_div1_left{
		font-size:0.16rem 
	}
	.shop_sb_bug_div1_right{
		font-size:0.16rem 
	}
	.shop_sb_bug_div1_right i{
		font-size: 0.14rem;
	}
	.shop_sb_bug_div1_right span{
		margin-left: 0.03rem;
	}
	.shop_sb_bug_div2{
		width:100%;
		background: #fff;
		display: flex;
		justify-content:flex-start;
		flex-direction:column;
		align-items:center;
	}
	.shop_sb_bug_div2 li{
		width:3.45rem;
		display: flex;
		justify-content:space-between;
		flex-direction:row;
		height:0.65rem;
		line-height: 0.65rem;
		color:#666;
		background: #fff;
		position: relative;
	}
	.shop_sb_bug_div2_left{
		height:100%;
		display: flex;
		justify-content:flex-start;
		flex-direction:column;
		flex-wrap:nowrap;
		position: relative;
	}
	.shop_sb_bug_div2_left p{
		height:0.2rem;
		font-size: 0.16rem;
		font-weight: 800;
		text-align: left;
	}
	.shop_sb_bug_div2_left p+p{
		height:0.10rem;
		font-size: 0.12rem;
		font-weight: 400;
	}
	.shop_sb_bug_div2_cent{
		position: absolute;
		right: 30%;
		top: 50%;
		transform:translateY(-50%);
		color:#f60;
		font-size: 0.16rem;
		font-weight: 700;
	}
	.shop_sb_bug_div2_cent::before{
		content:'¥';
		margin-right: 0.03rem;
	}
	.shop_sb_bug_div2_right{
		width:0.68rem;
		height:0.36rem;
		margin-top: 0.16rem;
		display: flex;
		justify-content:space-between;
		flex-direction:row;
		align-items:center;
		line-height:0.36rem !important; 
		font-size: 0.16rem;
	}
	.change_active{
		color:white;
		font-weight: 700;
		background: #4cd964;
	}
</style>


