<template>
	<div id="muitop_to">
		
			<router-link to="/home">
				<i class="el-icon-arrow-left" @click="sear()"></i>
			</router-link>
			<p>我的</p>
		
	</div>
</template>

<script>
	var Muitop = {
		name: "muitop",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		}
	}
	export default Muitop
</script>

<style scoped>
	#muitop_to {
		width: 100%;
		line-height: 0.5rem;
		text-align: center;
		width: 100%;
		height: 0.5rem;
	font-size: 0.15rem;

	}
	p,i{
    color: white;
		
	}
	i{
	float: left;
	line-height: 0.5rem;
	margin-left: 0.03rem;
	

}
</style>


