<template>
	<div id="adddetail_wrap">
		<!--导航条-->
		<div class="adddetail_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;">搜索地址</p>
		</div>
		<!--搜搜内容-->
		<div class="adddetail_center">
			<input type="text" placeholder="输入小区/学校/楼" v-model="details" />
			<el-button type="primary" @click="searchAddress()">确定</el-button>
			<p>为了满足商家的送餐要求,建议从列表中选择地址</p>
		</div>
		<!--空白-->
		<div class="adddetail_bottom" v-if="!allAddress.length">
			<p>找不到地址?</p>
			<p>请尝试输入小区,写字楼,学校名</p>
			<p>还找不到?别买了</p>
		</div>
		<div class="adddetail_bottom_ul" v-else>
			<ul>
			<li v-for="(val,ind) in allAddress" :key="ind" >
				<a @click="chuanshu(val.address)">
					<div>{{val.name}}<span>{{val.address}}</span></div>
				</a>
			</li>
			</ul>
		</div>

	</div>
</template>

<script>
	var Adddetail = {
		name:"adddetail",
		data(){
			return {
				details:"",
				allAddress:[]
			}
		},
		props:["cli"],
		methods:{
			sear(){
				this.$router.go(-1)
			},
			searchAddress(){
				var _this=this
				var cityId=this.$store.state.cityMsg.id
				this.SearchStore(cityId,this.details,function(el){
					console.log(el)
					_this.allAddress = el
				})
			},
			chuanshu(el){
				this.$store.commit("addressText",el)
				this.$router.push({name:"newprofile"})
			}
		}
	}
	export default Adddetail
</script>

<style scoped>
	#adddetail_wrap{
		position: absolute;
		left: 0;right: 0;
		top: 0;bottom: 0;
		background: rgb(245,245,245);
	}
	.adddetail_nav{
		width: 100%;
		background:#3190E8;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		color: black;
		line-height: 0.5rem;
	}
	.adddetail_nav>i{
		color: white;
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	/*搜索框*/
	.adddetail_center{
		background: white;
	}
	.adddetail_center>input{
		margin-top: 0.2rem;
		width: 70%;
		height: 0.4rem;
		font-size: 0.15rem;
		padding-left: 0.05rem;
		border: 1px solid rgb(220,220,220);
		
	}
	.adddetail_center>button{
		height: 0.43rem;
		width: 20%;
	}
	.adddetail_center>p{
		margin-top:0.05rem ;
		background: #FFF6E4;
		color: #FFAC75;
		height: 0.3rem;
		line-height: 0.3rem;
		font-size: 0.14rem;
	}
	.adddetail_bottom{
		margin-top: 1rem;
	}
	.adddetail_bottom>p{
		line-height: 0.4rem;
		color: darkgray;
	}
</style>
<style>
	.adddetail_bottom_ul ul{
		width: 100%;
		display: flex;
		justify-content:flex-start;
		flex-direction:column;
		text-align: left;
	}
	.adddetail_bottom_ul ul li{
		width:100%;
		height:0.43rem;
		background-color: #fff;
		border-bottom: 0.005rem solid #e4e4e4;
	}
	.adddetail_bottom_ul ul li a{
		height:100%;
		line-height: 0.42rem;
		color:#333;
	}
	.adddetail_bottom_ul ul li div{
		margin-left: 0.15rem;
		width:3.2rem;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
	}
	.adddetail_bottom_ul ul li div span{
		font-size: 0.12rem;
		color:#666;
		margin-left: 0.08rem;	
	}
</style>


