const Plugin1 = {
	// 调用vue.use(plugin1)之后,他会自动回调这个onstall函数并传递vue对象过来
    install(Vue, b, c) {
        // 定位city       //--1
        // https://elm.cangdu.org/v1/cities?type=guess
         Vue.prototype.positionCity = function(success){
            let api="https://elm.cangdu.org/v1/cities?type=guess";
            // promise写法
            var _this=this
            this.axios.get(api).then(function(response){
                success&&success(response.data)
            })
        }
        // 所有城市  //--1
        // https://elm.cangdu.org/v1/cities?type=group
        Vue.prototype.citys = function(success){
            let api="https://elm.cangdu.org/v1/cities?type=group";
            // promise写法
            var _this=this
            this.axios.get(api).then(function(response){
                success&&success(response.data)
            })
        }
        // 热门城市  // --1
        // https://elm.cangdu.org/v1/cities?type=hot
        Vue.prototype.hotCity = function(success){
            let api="https://elm.cangdu.org/v1/cities?type=hot";
            // promise写法
            var _this=this
            this.axios.get(api).then(function(response){
                success&&success(response.data)
            })
        }
        // 所选城市信息   传城市id    // --2
        // https://elm.cangdu.org/v1/cities/1
        Vue.prototype.citymassage = function(el,success){
            let api="https://elm.cangdu.org/v1/cities";
            // promise写法
            var _this=this
            var id=el;
            this.axios.get(api,{
                params:{
                  id:el
                }
            }).then(function(response){
                success&&success(response.data)
            })
        }


         // 搜索地址 -->给我城市id,地址名  // --3
        Vue.prototype.SearchStore = function(el,ol,success){
            let api="https://elm.cangdu.org/v1/pois?";
            // promise写法
            var _this=this
            var city_id=el;
            var keyword=ol;
            this.axios.get(api,{
                params:{
                   city_id:el,
                   keyword:ol,
                   type:"search",
                }
            }).then(function(response){
                success&&success(response.data)
            })
        }
        // 匹配搜索 -->给我经纬度+匹配方式 //--6
        // 1：起送价、2：配送速度、3:评分、4: 智能排序(默认)、5:距离最近、6:销量最高
        Vue.prototype.SearchAround = function(bl,cl,el,success){
            let api="https://elm.cangdu.org/shopping/restaurants?";
            // promise写法
            var _this=this
            var latitude=bl;
            var longitude=cl;
            var order_by=el;
            this.axios.get(api,{
                params:{
                  latitude:bl,
                  longitude:cl,
                  order_by:el
                }
            }).then(function(response){
                success&&success(response.data)
            })
        }
        // 全城模糊搜索 // 给我经纬度
        // https://elm.cangdu.org/shopping/restaurants?latitude=31.22967&longitude=121.4762&order_by=4&limit=300  
        Vue.prototype.SearchAllShop = function(bl,cl,success){
            // promise写法
            var latitude=bl;
            var longitude=cl;
            let api="https://elm.cangdu.org/shopping/restaurants?latitude="+latitude+"&longitude="+longitude+"&order_by=4&limit=100";
            this.axios.get(api).then(function(response){
                success&&success(response.data)
            })
        }
        // 全城匹配,模糊搜索  // 给我关键字  返回店铺id
        Vue.prototype.getSearchShop = function(val,success){
            var msg = this.$store.state.AllShop
            var reg=new RegExp(val,'g')
            var allHave=[]
            for (var i = 0; i < msg.length; i++) {
                if(msg[i].name.search(reg) != -1){
                    allHave.push(i)
                }
            }
            success&&success(allHave)
        }




        // 食品分类         //--5
        // https://elm.cangdu.org/v2/index_entry
        Vue.prototype.foodClass = function(success){
            let api="https://elm.cangdu.org/v2/index_entry";
            // promise写法
            this.axios.get(api).then(function(response){
                success&&success(response.data)
            })
        }
        // 搜索餐馆  -->给我经纬+食物关键词   //--7
            // 如: geohash=31.22967,121.4762&keyword=肯德基
         Vue.prototype.forAround = function(bl,cl,success){
            let api="https://elm.cangdu.org/v4/restaurants?";
            // promise写法
            var geohash=bl;
            var keyword=cl;
            this.axios.get(api,{
                params:{
                  geohash:bl,
                  keyword:cl,
                }
            }).then(function(response){
                success&&success(response.data)
            })
        }
        // 搜索所有餐馆  -->给我经纬    //--8
            // 不给就返回全部餐厅的分类信息
         Vue.prototype.Arounds = function(bl,cl,success){
            let api="https://elm.cangdu.org/shopping/v2/restaurant/category";
            // promise写法
            var latitude =bl;
            var longitude =cl;
            this.axios.get(api,{
                params:{
                  latitude:bl,
                  longitude:cl,
                }
            }).then(function(response){
                success&&success(response.data)
            })
        }
        //餐厅详情  -->给我餐厅id    //--11
        // https://elm.cangdu.org/shopping/restaurant/1
        Vue.prototype.StoreMsg = function(bl,success){
            let api="https://elm.cangdu.org/shopping/restaurant/"+bl;
            // promise写法
            this.axios.get(api).then(function(response){
                success&&success(response.data)
            })
        }

        // 餐厅食物  -->给我餐厅id    // --16
        // https://elm.cangdu.org/shopping/v2/menu?restaurant_id=1
        Vue.prototype.shopfooder = function(el,success){
            let api="https://elm.cangdu.org/shopping/v2/menu";
            // promise写法
            var restaurant_id=el;
            this.axios.get(api,{
                params:{
                  restaurant_id:el,
                }
            }).then(function(response){
                success&&success(response.data)
            })
        }

        // 评价信息 -->给我餐厅id 我给你20条数据
        // https://elm.cangdu.org/ugc/v2/restaurants/1/ratings?offset=0&limit=10
        Vue.prototype.restMsg = function(el,success){
            let api="https://elm.cangdu.org/ugc/v2/restaurants/"+el+"/ratings?offset=0&limit=20";
            // promise写法
            this.axios.get(api).then(function(response){
                success&&success(response.data)
            })
        }

        // 评分数 -->给我餐厅id
        // https://elm.cangdu.org/ugc/v2/restaurants/1/ratings/scores
        Vue.prototype.restMark = function(el,success){
            let api="https://elm.cangdu.org/ugc/v2/restaurants/"+el+"/ratings/scores";
            this.axios.get(api).then(function(response){
                success&&success(response.data)
            })
        }
        // 评价类型 -->给我餐厅id
        // https://elm.cangdu.org/ugc/v2/restaurants/1/ratings/tags
        Vue.prototype.restType= function(el,success){
            let api="https://elm.cangdu.org/ugc/v2/restaurants/"+el+"/ratings/tags";
            this.axios.get(api).then(function(response){
                success&&success(response.data)
            })
        }


        
        //---------------------------------------------------------------
    },
};

export { Plugin1};


