<template>
	<div id="tuijian_wrap">
		<div id="tuijian_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;">推荐有奖</p>
		</div>
		<!--内容-->
		<div class="tuijian_center">
			<img src="@/../static/imgs/tuijian.png" alt="" />
		</div>
		<!--邀请-->
		<div class="tuijian_yaoqing">
			<ul>
				<li>
					<div @click="centerDialogVisible = true">
						<img src="@/../static/imgs/商品.png" alt="" />
						<p>邀请微信好友</p>
					</div>
				</li>
				<li>
					<div @click="centerDialogVisible = true">
						<img src="@/../static/imgs/商品.png" alt="" />
						<p>邀请QQ好友</p>
					</div>
				</li>
				<li>
					<div @click="centerDialogVisible = true">
						<img src="@/../static/imgs/商品.png" alt="" />
						<p>面对面邀请</p>
					</div>
				</li>
			</ul>
			<el-dialog title="提示" :visible.sync="centerDialogVisible" width="75%">
				<p style="font-size: 0.7rem;font-weight: bolder;color: red;">!</p>
				<span style="font-size: 0.2rem;">请在饿了么APP中打开</span>
				<span slot="footer" class="dialog-footer">
					<el-button type="primary" @click="returnMui()" style="width: 100%;">确定</el-button>
				</span>
			</el-dialog>
		</div>
		<!--收益,邀请-->
		<div class="tuijian_shouyi">
			<ul>
				<li style="border-right: 1px solid rgb(220,220,220);">
					<div>
						<p>累计收益</p>
						<p>0元</p>
					</div>
				</li>
				<li>
					<div>
						<p>成功邀请</p>
						<p>0人</p>
					</div>
				</li>
			</ul>
			<p>--收益明细--</p>
			<p style="color: rgb(160,160,160);">还不赶紧去邀请好友</p>
		</div>
		
	</div>
</template>

<script>
	var Tuijian = {
		name:"tuijian",
		data(){
			return {
				centerDialogVisible: false,
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			},
			returnMui(){
				this.centerDialogVisible = false;
				
			},
		},
		created(){
			
		}
	}
	export default Tuijian
</script>

<style scoped>
	#tuijian_wrap{
		position: absolute;
		width: 100%;
		left: 0;right: 0;
		top: 0;bottom: 0;
		background: rgb(245,245,245);
		text-align: center;
		overflow: hidden;
	}
	#tuijian_nav{
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		position: fixed;
		left: 0;top: 0;
	}
	#tuijian_nav>i{
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
		color: white;
	}
	.tuijian_center{
		margin-top: 0.5rem;
		margin-bottom: 0.1rem;
	}
	.tuijian_center>img{
		width: 100%;
		vertical-align: top;
	}
	.tuijian_yaoqing>ul>li>div>img{
		width: 30%;
		margin: 0.1rem 0;
	}
	.tuijian_yaoqing>ul{
		display: flex;
		justify-content: center;
		background: white;
		padding: 0.1rem;
		box-sizing: border-box;
	}
	.tuijian_yaoqing>ul>li>div{
		font-size: 0.15rem;
	}
	/*收益*/
	.tuijian_shouyi>ul{
		display: flex;
		justify-content: center;
	}
	.tuijian_shouyi>ul>li{
		width: 50%;
		height: 0.6rem;
	}
	.tuijian_shouyi>ul>li>div{
		padding: 0.2rem;
		box-sizing: border-box;
	}
	.tuijian_shouyi>p{
		margin-top: 0.3rem;
		
	}
</style>


