<template>
	<div id="hometop_to">
		<div>
			<router-link to="">
				<i class="el-icon-arrow-left" @click="sear()"></i>
			</router-link>
			<p>订单列表</p>
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
	var Ordertop = {
		name: "ordertop",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		}
	}

	export default Ordertop
</script>
<style scoped>
	#hometop_to {
		width: 100%;
		line-height: 0.5rem;
		text-align: center;
		width: 100%;
		height: 0.5rem;
	font-size: 0.15rem;
		
	}
	p,i{
		color: white;
	}
	i{
	float: left;
	line-height: 0.5rem;
		margin-left: 0.03rem;
	
}
</style>


