<template>
    <div id="hometop_to">
        <div>
        	<!--搜索-->
        	<router-link to="/search" class="hometop_i">  
		  		<i class="el-icon-search"></i>
		  	</router-link>
		  	<div class="hometop_to_nav">
			  	<router-link to="/city">
			  		<span>{{msg}}</span>
			  	</router-link>
		  	</div>

			<div id="homepage_div">
				<div>
					<router-link to="/login" class="homepage_div_a" v-if="!loginOK">
						<span>{{personLocal.title}}</span>
					</router-link>
					<router-link to="/mui" v-else>
						<img src="@/../static/imgs/用户中心.png" class="homepage_div_img"/>
					</router-link>
				</div>
			</div>
		</div>

    </div>
</template>
<script>
import Cutcity from '@/components/citys/cutcity'

	var Hometop = {
		name:"hometop",
		data(){
			return {
				msg:"",
				loginOK:false,
				personLocal:{}
				
			}
		},
		created(){
			var a = this.$store.state.cityTitleMsg
			this.msg=a.address
			
			var personLocal = this.$store.state.personLocal
			var c = localStorage.getItem("personLocal")
			if(c == null){
				localStorage.setItem("personLocal",JSON.stringify(personLocal))
				
			}
			this.$store.commit("getLocal","personLocal")
			this.personLocal = this.$store.state.personLocal[0]
			if(this.personLocal.id){
				this.loginOK = true
			}
		},
		
	}
    export default Hometop
</script>
<style scoped>
	#hometop_to{
	line-height: 0.5rem;
	text-align: center;
	width: 100%;
	height: 0.5rem;
	font-size: 0.14rem;
	}
	.homepage_div_img{
		width: 0.3rem;
		height: 0.3rem;
		position: absolute;
		right:0.1rem;
		top: 50%;
		transform: translateY(-50%);
	}
	.homepage_div_a{
		position: absolute;
		right: 0.1rem;
		top: 0;
	}
	.hometop_to_nav{
		width: 1.5rem;
		margin: 0 auto;
		text-align: center;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.hometop_to_nav>a{
		font-size: 0.15rem;
	}
	a,i{
    color: white;
		
	}
	.hometop_i{
		color: white;
		position: absolute;
		left: 0.1rem;
		top: 50%;
		transform: translateY(-50%);
}
</style>




