<template>
	<div id="daijinquan_wrap">
		<div class="daijinquan_float">
			<router-link to="/benef/daijinquansm">
				<i class="el-icon-question"></i>
				<span>商家代金券说明</span>
			</router-link>
		</div>
			
		<div class="daijinquan_xiazai">
			<h1>无法使用代金券</h1>
			<h4>非客户端或客户端版本过低</h4>
			<router-link to="/mui/download">
				<el-button type="success">下载或升级客户端</el-button>
			</router-link>
		</div>
	</div>
</template>

<script>
	var Daijinquan = {
		name:"daijinquan"
	}
	export default Daijinquan
</script>

<style scoped>
	#daijinquan_wrap{
		
		background: white;
	}
.daijinquan_float{
	width: 100%;
	text-align: right;
	padding: 0 0.1rem;
	box-sizing: border-box;
	font-size: 0.15rem;
	overflow: hidden;
}

.daijinquan_float>a{
	color: #3190E8;
}
.daijinquan_float>a>span{
	display: inline-block;
		height: 0.4rem;
		line-height: 0.4rem;
		font-size: 0.15rem;
}
.daijinquan_xiazai{
	margin-top: 1.3rem;
	text-align: center;
}
.daijinquan_xiazai>h1{
	line-height: 0.4rem;
}
.daijinquan_xiazai>h4{
	line-height: 0.4rem;
}
</style>


