<template>
	<div id="hometop_to">
		<router-link :to="{name:'home'}">
			<i class="el-icon-arrow-left"></i>
		</router-link>
		<p>{{msg.title}}</p>
	</div>
</template>

<script>
	var Navs = {
		name: "navs",
		data(){
			return {
				msg:{id:9,title:'鲜花蛋糕'}
			}
		},
		created(){
			var guideTitleMsg = this.$store.state.guideTitleMsg
			this.msg=guideTitleMsg
			console.log(guideTitleMsg)
		},
		methods:{
			sear(){
				this.$router.go(-1)
			}
		}
	}
	export default Navs
</script>

<style scoped>
	#hometop_to {
		width: 100%;
		text-align: center;
		height: 100%;
		position: relative;
	}
	p,i{
		font-size: 0.17rem;
		color: white;
		line-height: 0.52rem;
	}
	i {
		position: absolute;
		left: 0;
		top: 0;
		font-size: 0.24rem;
		float: left;
		margin-left: 0.09rem;
		line-height: 0.45rem;
	}
</style>


