<template>
	<div id="sousuo_wrap">
		<div class="sousuo_wrap_btn">
			<form action="">
			<input type="search" class="sousuo_input" placeholder="输入学校,商务楼,地址" v-model="title"  />
			</form>
			<el-row>
			 <el-button type="primary" class="sousuo_wrap_but" @click="btl()">提交</el-button>
			 </el-row>
		 </div>
		 <div>
		 	<ul>
		 		<router-link to="/home" >
		 			<li v-for="title in datas"  v-if="datas.length" @click="goPub(title.name,title.address)">
		 			<h3>{{title.name}}</h3>
		 			<h4>{{title.address}}</h4>
		 			</li>
		 		</router-link>	
		 		<li v-if="!datas.length&&ifBtl" class="sousuo_wrap_li">未搜索到内容</li>
		 	</ul>
		 </div>
	</div>
</template>

<script>
	import Cutcity from './cutcity'
	var Sousuo = {
		name:"sousuo",
		data(){
			return {
				title:"",
				datas:[],
				ifBtl:false
			}
		},
		methods:{
			btl(){
				var _this = this
				var api_city = "https://elm.cangdu.org/v1/pois?city_id="+_this.$route.params.id+"&keyword="+this.title;
			 	this.axios.get(api_city).then(cityid => {
	      			//成功后的回调；
	      			_this.datas = cityid.data
    			})
			 	this.ifBtl=true
			},
//			上传信息到公共属性 1
			goPub(el,bl){
				var obj = {
					name:el,
					address:bl
				}
				this.$store.commit("cityTitleMsg",obj)
				
			}
			
		},
		
	}
	
	export default Sousuo
</script>

<style scoped>
	#sousuo_wrap{
		background-color: white;
		text-align: center;
	}
	.sousuo_input{
		width: 90%;
		height: 0.5rem;
		border: 1px solid rgb(245,245,245);
		margin-bottom: 0.1rem;
		font-size: 0.18rem;
	}
	.sousuo_wrap_btn{
		border-bottom: 1px solid rgb(245,245,245);
		padding: 0.1rem;
		box-sizing: border-box;
	}
	.sousuo_wrap_but{
		width: 90%;
		height: 0.4rem;
		line-height: 0.4rem;
		padding: 0;
	}
	.sousuo_wrap_li{
		height: 0.4rem;
		line-height: 0.2rem;
		font-size: 0.18rem;
		text-align: left;
		padding: 0.1rem;
		box-sizing: border-box;
	}
	li{
		padding: 0.05rem;
		background: white;
		height: 0.4rem;
		line-height: 0.2rem;
		text-align: left;
		border-bottom: 1px solid rgb(245,245,245);
		overflow: hidden;
	}
	h3{
		color: black;
		font-size: 0.138rem;
	}
	h4{
		font-size: 0.1rem;
		color: darkgray;
	}
</style>


