<template>
	<div id="yuewenti_wrap">
		<!--导航-->
		<div class="yuewenti_wrap_nav">
			<router-link to="">
				<i class="el-icon-arrow-left" @click="sear()"></i>
			</router-link>
			<p>余额说明</p>
		</div>
		<!--问题内容-->
		<div class="yuewenti_wrap_center">
			<Wentineirong></Wentineirong>
		</div>
	</div>
</template>

<script>
	import Wentineirong from './wentineirong'
	
	var Yuewenti = {
		name:"yuewenti",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		components:{
			Wentineirong
		}
	}
	export default Yuewenti
</script>

<style scoped>
	
	.yuewenti_wrap_nav {
		width: 100%;
		line-height: 0.5rem;
		text-align: center;
		background: #3190E8;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
	}
	p,i{
		color: white;
	}
	i {
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
</style>


