<template>
	<div id="paymoney">
        <div id="paymoney_top">
        	<!--搜索-->
        	<a @click="goBack()">  
		  		<i class="el-icon-arrow-left"></i>
		  	</a>
		  	<div class="paymoney_to_nav">
			  	<router-link to="/city">
			  		<span>{{msg}}</span>
			  	</router-link>
		  	</div>
			<div id="paymoney_top_div">
				<div v-if="!isName">
					<router-link to="/login" style="color: white;"><span>登录|注册</span></router-link>
				</div>
				<div v-else>
					<router-link to="/mui" style="color: white;"><img src="@/../static/imgs/用户中心.png" alt="" class="paymoney_div_img" /></router-link>
				</div>
			</div>
		</div>
		<div id="paymoney_address" @click="goAddressRouter()" v-if="onceAddress.id >= 0">
			<i class="el-icon-location-outline"></i>
			<p>{{onceAddress.address}}</p>
			<i class="el-icon-arrow-right"></i>
		</div>
		<div id="paymoney_address" v-else @click="goAddressRouter()">
			<i class="el-icon-location-outline"></i>
			<p>请添加一个收获地址</p>
			<i class="el-icon-arrow-right"></i>
		</div>
		<div id="paymoney_useTime">
			<div class="paymoney_useTime_div1">
				送达时间
			</div>
			<div class="paymoney_useTime_div2">
				<div>尽快送达<span>|</span>预计 <span>00:{{thisShop.float_minimum_order_amount}}</span></div>
				<div v-if="thisShop.delivery_mode">{{thisShop.delivery_mode.text}}</div>
			</div>
		</div>
		<div id="paymoney_payway">
			<ul>
				<li @click="choosePays()">
					<div>支付方式</div>
					<div>{{choosePay}}<span>></span></div>
				</li>
				<li>
					<div>红包</div>
					<div>暂时只在饿了么 APP 中支持</div>
				</li>
			</ul>
		</div>
		<div id="paymoney_buyMsg">
			<ul>
				<div class="paymoney_buyMsg_shop">
					<img :src="'https://elm.cangdu.org/img/'+thisShop.image_path" alt="图片">
					<span>二万五v</span>
				</div>
				<li v-for="(val,ind) in playBuy" :key="ind">
					<div>shuiguo</div> <div class="paymoney_buyMsg_shop_div1">*{{val.lens}} <span>¥ {{val.prise*val.lens}}</span></div>
				</li>
				<li>
					<div>餐盒</div> <div class="paymoney_buyMsg_shop_div2">¥ {{thisShop.float_delivery_fee}}</div>
				</li>
				<li>
					<div>配送费</div> <div class="paymoney_buyMsg_shop_div2">¥ {{thisShop.float_delivery_fee}}</div>
				</li>
				<li>
					<div>订单金额</div><div class="paymoney_buyMsg_shop_div3">待支付<span> ¥{{prise}}</span></div>
				</li>
			</ul>
		</div>
		<div id="paymoney_OtherMsg">
			<ul>
				<li @click="Shop_orderOtherMsg()">
					<div>订单备注</div><div><span>{{otherOrderMsg}}</span> ></div>
				</li>
				<li @click="Shop_orderGivePath()">
					<div>发票抬头</div><div>{{orderGivePath}} ></div>
				</li>
			</ul>
		</div>
		<div id="paymoney_pay">
			<div class="paymoney_pay_div1">待支付<span>¥{{allPrise}}</span></div>
			<div class="paymoney_pay_div2" @click='onBuy()'>确定下单</div>
		</div>
		<div id="paymoney_payway_pays" v-if="isChoosePay">
			<div class="paymoney_payway_pays_div1">支付方式</div>
			<label class="paymoney_payway_pays_div2">
				<div>货到付款 <span>（商家不支持货到付款）</span></div>
				<input type="radio" name="inputs" value="upline" disabled="none">
			</label>
			<label class="paymoney_payway_pays_div3">
				<div>在线支付</div>
				<input type="radio" name="inputs" value="online" @click="choosePayAway($event)">
			</label>
		</div>
		<div id="backgroundColor" @click="hiddenMe()" v-if="isbackground"></div>
    </div>
</template>
<script>
	var payMoney = {
		name:"payMoney",
		data(){
			return {
				msg:"确定订单",
				isName:false,
				thisShop:{},
				playBuy:[],
				// 订单金额
				prise:0,
				// 待支付
				allPrise:0,
				choosePay:"在线支付",
				isChoosePay:false,
				isbackground:false,
				otherOrderMsg:"偏好,口味等",
				orderGivePath:"不需要开发票",
				personLocal:{},
				onceAddress:{}
			}
		},
		methods:{
			// 返回历史
			goBack(){
				this.$router.go(-1)
			},
			// 隐藏背景大框
			hiddenMe(){
				this.isChoosePay=false;
				this.isbackground=false;
			},
			//选择支付方式
			choosePays(){
				this.isChoosePay=true;
				this.isbackground=true;
			},
			// 确认选择支付方式
			choosePayAway(el){
				var bbt = el.path[0].defaultValue
				if (bbt=="online") {
					this.choosePay = "货到付款"
				};
				this.isChoosePay=false;
				this.isbackground=false;
			},
			Shop_orderOtherMsg(){
				this.$router.push({name:"orderOtherMsg"})
			},
			Shop_orderGivePath(){
				this.$router.push({name:"orderGivePath"})
			},
			onBuy(){
				this.$router.push({name:'clubbuy'})
			},
			goAddressRouter(){
				this.$router.push({name:'profile'})
			},
		},
		created(){
			var playBuy = this.$store.state.playBuy
			this.playBuy = playBuy
			console.log(playBuy)
			var shopid= this.$store.state.shopId
			var _this = this
			this.StoreMsg(shopid,function(el){
				_this.thisShop=el
			});
			var allprise=this.$store.state.allPrise

			this.prise = allprise
			this.allPrise = allprise

			if (this.$store.state.orderOtherMsg.length) {
				this.otherOrderMsg = this.$store.state.orderOtherMsg[0].quick.join(',')
			};
			if (this.$store.state.orderGivePath.length) {
				this.orderGivePath = "需要开发票"
			};
			this.personLocal = this.$store.state.personLocal[0]
			if(this.personLocal.id){
				this.isName = true
			}
			this.onceAddress = this.$store.state.onceAddress
			console.log(this.onceAddress)
		},
		watch:{
			thisShop(news){
				this.allPrise += news.float_delivery_fee*2
			}
		}
	}
	export default payMoney
</script>
<style scoped>
	#paymoney{
		position: relative;
		top:0;
		left: 0;
		width: 100%;
		background:#f5f5f5;
		overflow: hidden;
	}
	/*登录用户中心*/
	.paymoney_div_img{
		width: 0.3rem;
		margin-top: 0.1rem;
	}
	/*标题信息*/
	#paymoney_top{
		line-height: 0.5rem;
		text-align: center;
		width: 100%;
		height: 0.5rem;
		font-size: 0.18rem;
		background-color: #3190e8;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 5;
	}
	#paymoney_top_div{
		position: absolute;
		right:0.15rem;
		top: 0;
		font-size: 0.14rem;
	
	}
	.paymoney_to_nav{
		width: 1.5rem;
		margin: auto;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#paymoney_top a,i{
    	color: white;
	}
	#paymoney_top i{
		float: left;
		line-height: 0.5rem;;
		margin-left: 0.03rem;
	}

	/*收货地址*/
	#paymoney_address{
		margin-top: 0.56rem;
		font-size: 0.16rem;
		display: flex;
		justify-content:space-between;
		flex-wrap:nowrap;
		padding:0 0.15rem;
		height: 0.72rem;
		line-height: 0.72rem;
		background:url(data:image/png;base64,) 0 100% repeat-x;
		background-size: auto 0.03rem;
		background-color: #fff;
	}
	#paymoney_address p{
		text-align: left;
		margin-left: 0.08rem;
		width: 100%;
		color:#666;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
	}
	#paymoney_address i{
		margin-top: 0.26rem;
		color:#3190e8; 
	}
	#paymoney_address .el-icon-arrow-right{
		color:#666; 
	}

	/*送达时间*/
	#paymoney_useTime{
		margin-top: 0.1rem;
		background-color: #fff;
		width: 100%;
		height: 0.9rem;
		border-left: 0.05rem solid #3190e8;
		position: relative;
		overflow: hidden;
	}
	.paymoney_useTime_div1{
		font-size: 0.18rem;
		font-weight: 700;
		position: absolute;
		left: 0.2rem;
		top: 50%;
		transform:translateY(-50%);
	}
	.paymoney_useTime_div2{
		position: absolute;
		right: 0;
		top: 50%;
		transform:translateY(-50%);
		height: 0.6rem;
		display: flex;
		flex-direction:column;
		justify-content:space-around;
		align-items:flex-end;
		margin-right: 0.18rem;
		color:#3190e8;
	}
	.paymoney_useTime_div2 div span{
		margin-left: 0.05rem;
		margin-right: 0.05rem;
	}
	.paymoney_useTime_div2 div span+span{
		margin-right: 0rem;
	}
	.paymoney_useTime_div2 div+div{
		background-color: #3190e8;
		color:#fff;
		width: 0.5rem;
		padding: 0.03rem 0.02rem;
		line-height: 0.16rem;
		border-radius: 0.02rem;
		text-align: center;
		font-size: 0.12rem;
	}
	/*支付方式*/
	#paymoney_payway{
		margin-top: 0.1rem;
		width: 100%;
		overflow: hidden;
	}
	#paymoney_payway ul{
		padding: 0 0.14rem;
		background-color: #fff;
	}
	#paymoney_payway ul li{
		height:0.45rem;
		width: 100%;
		font-size: 0.14rem;
		line-height: 0.45rem;
		display: flex;
		flex-wrap:nowrap;
		justify-content:space-between;
		color:#aaa;
		margin-top: 0.01rem;
	}
	#paymoney_payway ul li:first-child{
		border-bottom: 0.005rem solid #f5f5f5;
	}
	#paymoney_payway ul li div span{
		margin-left: 0.05rem;
	}

	/*订单信息*/
	#paymoney_buyMsg{
		margin-top: 0.1rem;
		background: #fff;
	}
	#paymoney_buyMsg ul{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		padding: 0 0.15rem;
		text-align: left;
		padding-bottom: 0.4rem;
	}
	#paymoney_buyMsg ul li{
		height:0.42rem;
		margin-top: 0.03rem;
		display: flex;
		justify-content:space-between;
		line-height: 0.42rem;
		font-size: 0.14rem;
		color:#666;
	}
	.paymoney_buyMsg_shop_div1{
		color:#f60;
	}
	.paymoney_buyMsg_shop_div1 span{
		color:#666;
		margin-left: 0.4rem;
	}
	.paymoney_buyMsg_shop_div3{
		color:#f60;
		width: 0.5rem;
		margin-right: 0.3rem;
	}
	.paymoney_buyMsg_shop{
		position: relative;
		width: 100%;
		height:0.6rem;
		line-height: 0.6rem;
	}
	.paymoney_buyMsg_shop img{
		position: absolute;
		left: 0;
		top: 50%;
		transform:translateY(-50%);
		width: 0.28rem;
	}
	.paymoney_buyMsg_shop span{
		margin-left: 0.35rem;
	}
	
	#paymoney_OtherMsg{
		margin-top: 0.05rem;
		overflow: hidden;
		width: 100%;
		margin-bottom: 0.48rem;
	}
	#paymoney_OtherMsg ul{
		display: flex;
		flex-direction:column;
		padding: 0 0.15rem;
		justify-content:flex-start;
		background-color: #fff;
	}
	#paymoney_OtherMsg ul li{
		display: flex;
		justify-content:space-between;
		height:0.42rem;
		line-height: 0.42rem;
		margin-bottom: 0.02rem;
	}
	#paymoney_OtherMsg ul li div{
		color:#666;
		font-size: 0.15rem;
	}
	#paymoney_OtherMsg ul li div+div{
		color:#aaa;
	}
	#paymoney_pay{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		background-color: #3c3c3c;
		height:0.48rem;
		color:#fff;
		display: flex;
		justify-content:space-between;
		line-height: 0.48rem;
		font-size: 0.17rem;
	}
	.paymoney_pay_div1{
		line-height: 0.5rem;
		margin-left: 0.15rem;
	}
	.paymoney_pay_div1 span{
		margin-left: 0.05rem;
	}
	.paymoney_pay_div2{
		text-align: center;
		width: 1rem;
		height: 100%;
		background-color: #56d176;

	}
	/*支付状态*/
	
	#paymoney_payway_pays{
		position: fixed;
		left: 0;
		bottom: 0;
		width:100%;
		height:2.4rem;
		z-index: 10;
		text-align: left;
		font-size: 0.175rem;
		background-color: #fff;
	}
	.paymoney_payway_pays_div1{
		text-align: center;
		height: 0.45rem;
		line-height: 0.45rem;
	}
	.paymoney_payway_pays_div2{
		display: flex;
		overflow: hidden;
		padding: 0 0.15rem;
		justify-content:space-between;
		color:#666;
		height: 0.45rem;
		line-height: 0.45rem;
	}
	.paymoney_payway_pays_div3{
		display: flex;
		overflow: hidden;
		padding: 0 0.15rem;
		justify-content:space-between;
		height: 0.45rem;
		line-height: 0.45rem;
	}


	#backgroundColor{
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0,0.3);
		position: fixed;
		z-index: 1;
		left: 0;
		top: 0;
	}
</style>


