<template>
<div id="jifen_wrap">	
	<!--我的余额-->
	<router-link to="/balance"  class="jifen_yue">
		<div>
			<p>0.00元</p>
			<p @click="jifen($event)">我的余额</p>
		</div>
	</router-link>
	<!--我的优惠-->
	<router-link to="/benef" class="jifen_yue">
		<div>
			<p>{{count}}个</p>
			<p>我的优惠</p>
		</div>
	</router-link>
	<!--我的积分-->
	<router-link to="/points" class="jifen_yue">
		<div>
			<p>0分</p>
			<p>我的积分</p>
		</div>
	</router-link>
	
</div>
</template>

<script>
	import { Loading } from 'element-ui';
	var Jifen = {
		name:"denglu",
		data(){
			return {
				msg:"",
				count:""
			}
		},
		methods:{
			jifen(event){
				this.msg = event.srcElement.innerHTML
			}
		},
		created(){
			let loaddingInstance1 = Loading.service({
				fullscreen: true
			});
			var _this = this
			var id = +this.$store.state.personLocal[0].id
			var api_youhui = "https://elm.cangdu.org/promotion/v2/users/"+id+"/hongbaos?limit=20&offset=0"
			this.axios.get(api_youhui).then(function(el){
				_this.count = el.data.length
				if(!id){
					_this.count = 0
				}
			})
			
			loaddingInstance1.close();
			
			
		}
	}
	export default Jifen
</script>

<style scoped>
	#jifen_wrap{
		margin-top:0.15rem;
		background: white;
		display: flex;
		justify-content: center;
		clear: both;
		width:100%;
		height: 0.7rem;
	}
	.jifen_yue{
		border-right: 1px solid rgb(245,245,245);
		width: 33%;
		text-align: center;

	}
	.jifen_yue>div>p:nth-child(1){
		font-size: 0.2rem;
		color: orangered;	
		margin-top: 0.15rem;
	}
	.jifen_yue>div>p:nth-child(2){
		margin-top: 0.1rem;
		font-size: 0.138rem;
		color: #000000;
	}
	.jifen_youhui{
		border-right: 1px solid rgb(245,245,245);

		width: 33%;
	}
	.jifen_wodejifen{

		width: 33%;

	}
</style>


