<template>
	<div>
		<!--导航条-->
		<div class="userinfo_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;">修改用户名</p>
		</div>
		<!--内容部分-->
		<div class="userinfo_center">
			<input type="text" placeholder="输入用户名" v-model="userinfos" /> 
			<p>用户名只能修改一次(5-24字符之间)</p>
			<el-row>
				<el-button type="primary" class="userinfo_center_button" @click="el_btn(userinfos)">确认修改</el-button>
			</el-row>
		</div>
	</div>
</template>

<script>
	var Userinfo = {
		name:"userinfo",
		data(){
			return {
				userinfos:"",
				id:""
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			},
			el_btn(el){
				var _this = this
				var avatar = this.$store.state.personLocal[0].avatar
				var obj = {
					name:"personLocal",
					str:{
						title:el,
						id:_this.$store.state.personLocal[0].id,
						avatar:avatar
					}
				}
				this.$store.commit("localHist",obj)
				if (el!=avatar&&el!=''){
					alert("修改成功")
					this.$router.push({name:"mui"});
				} else{
					alert("修改失败!请重试")
				}
				
			},
		},
		created(){
			
		},
	}
	export default Userinfo
</script>

<style scoped>
	.userinfo_nav{
		width: 100%;
		background:#3190E8;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		color: black;
		line-height: 0.5rem;
	}
	.userinfo_nav>i {
		color: white;
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	.userinfo_center{
		width: 100%;
		text-align: left;
		padding: 0.1rem;
		box-sizing:border-box
	}
	.userinfo_center>input{
		margin-top: 0.10rem;
		border: 1px solid rgb(220,220,220);
		width: 96%;
		height: 0.4rem;
		padding-left: 0.05rem;
		font-size: 0.15rem;
	}
	.userinfo_center>p{
		font-size: 0.14rem;
		margin: 0.15rem 0;
		
	}
	.userinfo_center_button{
		width: 96%;
		font-size: 0.15rem;
		
	}
</style>


