<template>
	<div id="hometop_to">
		<div>
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p>搜索</p>
		</div>
	</div>
</template>
<script>
	var Serchsearch = {
		name: "serchsearch",
		methods:{
			sear(){
				this.$router.go(-1);
			}
		}
	}
	export default Serchsearch
</script>
<style scoped>
	#hometop_to {
		width: 100%;
		line-height: 0.5rem;
		text-align: center;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
	}
	p,
	i {
		color: white;
	}
	
	i {
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
</style>


