<template>
	<div id="search_body">
		<!-- 这里面有两个内容,input框和提交按钮 -->
		<input type="text" placeholder="请输入商家或商铺名称" class="search_body_span" v-model="oomsg" @input="kong()">
		<router-link to="###">
			<el-button type="primary" @click="fountHistory()">提交</el-button>
		</router-link>
		<div class="search_body_qing">
			<i class="el-icon-close" @click="qing()"></i>
		</div>
	</div>
</template>
<script>
	var searchText = {
		name:"searchText",
		data(){
			return {
				oomsg:"",
				isdenglu:false,
				okdenglu:0,
			}
		},
		components:{
		    
		},
		methods:{
			fountHistory(){
				if (this.oomsg=="") {
					this.oomsg = ""
				}else{
					var el = {
						name:"searchLocalHistory",
						str:this.oomsg
					}
					this.$store.commit("localHist",el)
					this.cli(this.oomsg)
				}
			},
			qing(){
				this.oomsg=""
				this.clo()
			},
			kong(){
				if (this.oomsg=="") {
					this.clo()
				};
			}
		},
		// cli是点击提交的时的添加的
		// clo是点击*的时候 删除的操作
		// cd是从历史记录点击时拿过来的
		props:["cli","clo","cd"],
		// 请求演示
		created(){
			
		},
		watch:{
			cd(news){
				// 更改input的内容
				this.oomsg = news.value
				// this.fountHistory()
			}
		}
	}
	export default searchText

</script>
<style scoped>
	#search_body{
		width: 100%;
		height:100%;
		display: flex;
		justify-content:center;
		flex-wrap:nowrap;
		line-height: 0.58rem;
		position: relative;
	}
	.search_body_qing{
		position: absolute;
		top: 50%;
		transform:translateY(-45%);
		left: 68%;
	}
	.search_body_span{
		width:2.5rem;
		height:0.32rem;
		background-color: #f5f5f5;
		color:#333;
		font-size: 0.15rem;
		font-weight: 600;
		padding: 0.03rem 0.08rem;
		margin-top: 0.1rem;
		border-radius: 0.05rem;
		border: 0.01rem solid #e4e4e4;
	}
	.search_body_span:focus{
		outline:none;
		border:0.01rem solid skyblue;
	}
	#search_body a{
		line-height: 0.58rem;
		height:0.39rem;
		font-size: 0.15rem;
		line-height: 0.16rem;
		margin-top: 0.1rem;
		margin-left: 0.08rem;
		background-color: #3190e8;
		color:#fff;
		border-radius: 0.1rem;
	}
	.el-button{
		font-size: 0.15rem;
		height:0.39rem;
		font-weight:bold;
		line-height: 0.16rem;
		background-color: #3190e8;
	}

</style>


