<template>
	<div id="cutcity_wrap">
		<div id="hometop_to">
				<i class="el-icon-arrow-left" @click="sear()"></i>
			<strong style="color: white;">{{msg}}</strong>
			<div  id="cutcity_link">
				<router-link to="/city" style="color: white;" >切换城市</router-link>
			</div>
			</div>
			<!--搜索区域-->
			<div id="cutcity_sousuo">
				<Sousuo></Sousuo>
			</div>
	</div>
</template>

<script>
	import Sousuo from './sousuo'
	var Cutcity = {
		name:"cutcity",
		data(){
			return {
				msg:""
			}
		},
		components:{
			Sousuo
		},
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		created(){	
			var api4 = "https://elm.cangdu.org/v1/cities/"+this.$route.params.id;
			 this.axios.get(api4).then(cityid => {
      			//成功后的回调；
      			this.msg = cityid.data.name;
    });

		}
		
	}
	export default Cutcity
</script>

<style scoped>
	/*搜索区域*/
	#cutcity_sousuo{
		margin-top: 0.1rem;
		margin-bottom: 0.1rem;
		
	}
	#cutcity_wrap{
		position: absolute;
		left: 0;right: 0;
		top: 0;bottom: 0;
		background-color: rgb(245,245,245);
		width: 100%;
		
	}
	#hometop_to {
		width: 100%;
		line-height: 0.5rem;
		text-align: center;
		background: #3190E8;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
		overflow: hidden;
		position: relative;
	}
	#hometop_to>i{
		color: white;
		position: absolute;
		left: 0.1rem;
		top: 50%;
		transform: translateY(-50%);
	}
	#cutcity_link{
		position: absolute;
		right: 0.1rem;
		top: 50%;
		transform: translateY(-50%);
	}
	
</style>


