<template>
	<div id="wodeyue_wrap">
		<!--导航-->
		<div class="wodeyue_wrap_nav">
			<router-link to="">
				<i class="el-icon-arrow-left" @click="sear()"></i>
			</router-link>
			<p>我的余额</p>
		</div>
		<!--当前余额-->
		<div class="wodeyue_wrap_dangqian">
			<Dangqianyue></Dangqianyue>
		</div>
		<!--交易明细-->
		<div class="wodeyue_wrap_jiaoyi">
			<Jiaoyimingxi></Jiaoyimingxi>
		</div>
	</div>
</template>

<script>
	import Dangqianyue from '@/components/myJU/wodeyues/dangqianyue'
	import Jiaoyimingxi from '@/components/myJU/wodeyues/jiaoyimingxi'
	var Wodeyue = {
		name:"wodeyue",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		components:{
			Dangqianyue,
			Jiaoyimingxi,
		}
	}
	export default Wodeyue
</script>

<style scoped>
	#wodeyue_wrap{
		position: absolute;
		left: 0;right: 0;
		top: 0;bottom: 0;
		background: rgb(245,245,245);
	}
	.wodeyue_wrap_nav {
		width: 100%;
		line-height: 0.5rem;
		text-align: center;
		background: #3190E8;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
	}
	p,i,h3{
		color: white;
	}
	i {
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	.wodeyue_wrap_dangqian{
		background: rgb(49,144,232);
		padding: 0.05rem;
		border-top:1px solid rgb(245,245,245) ;
	}
</style>


