<template>
	<div id="searchif_GF">
		<div id="searchif_GF_title">
			<div @click="handle1()" v-if="!first" class="searchif_GF_title_true">
				<span>{{firstMsg}}</span>
				<i class="el-icon-caret-bottom"></i>
			</div>
			<div @click="handle1()" v-else class="searchif_GF_title_false">
				<span>分类</span>
				<i class="el-icon-caret-top"></i>
			</div>
			<div @click="handle2()" v-if="!second" class="searchif_GF_title_true">
				<span>{{secondMsg}}</span>
				<i class="el-icon-caret-bottom"></i>
			</div>
			<div @click="handle2()" v-else class="searchif_GF_title_false">
				<span>排序</span>
				<i class="el-icon-caret-top"></i>
			</div>
			<div @click="handle3()" v-if="!third" class="searchif_GF_title_true">
				<span>{{ThirdMsg}}</span>
				<i class="el-icon-caret-bottom"></i>
			</div>
			<div @click="handle3()" v-else class="searchif_GF_title_false">
				<span>筛选</span>
				<i class="el-icon-caret-top"></i>
			</div>
		</div>
		<div id="searchif_GF_choose" v-if="first">
			<div class="searchif_GF_choose_left">
				<ol>
					<li v-for="(val,ind) in allFood" :key="ind" @click="choosebtns(val.id)">
						<div class="searchif_GF_choose_left_div1">
							<img :src="'https://fuss10.elemecdn.com/'+val.image_url+'.png'" alt="">
							<span>{{val.name}}</span>
						</div>
						<div class="searchif_GF_choose_left_div2">
							<div>{{val.count}}</div>
							<span><i class="el-icon-arrow-right"></i></span>
						</div>
					</li>
				</ol>
			</div>
			<div class="searchif_GF_choose_right">
				<ul v-for="(val,ind) in allFood" :key="ind" v-if="onId==val.id">
					<li v-for="(value,index) in val.sub_categories" :key="index" v-if="value.count" @click="goodbtns(val.name,value.name)">
						<span style="float:left">{{value.name}}</span>
						<span style="float:right">{{value.count}}</span>
					</li>
				</ul>
			</div>
		</div>
		<div id="searchif_GF_orderBy" v-if="second">
			<ul>
			<li v-for="(val,ind) in orderBy" :key="ind" @click="goBtns(val.id,val.name)">
				<img :src="val.url" alt="">
				<div>{{val.name}}</div>
			</li>
			</ul>
		</div>
		<div id="searchif_GF_searchMsg" v-if="third">
			<div class="searchif_GF_searchMsg_div1">
				<p class="searchif_GF_searchMsg_p1">配送方式</p>
				<div v-for="(val,ind) in saiMsg" v-if="val.id==0" :class="{searchif_GF_searchMsg_li:true,lisnnop:val.id==ogg}" @click="selectBtns(val.id,val.name)">
					<img :src="saiMsgImgs[ind]" alt="" v-if="val.cli">
					<img :src="saiMsgImgs[ind+7]" alt="" v-if="!val.cli">
					<span>{{val.name}}</span>
				</div>
				<p class="searchif_GF_searchMsg_p1">商家属性(可以多选)</p>
				<ol>
					<li  v-for="(val,ind) in saiMsg" v-if="val.id>0" @click="selectBtns(val.id,val.name)"   
					:class="{searchif_GF_searchMsg_li:true,lisnnop:val.id==ogg}">
						<img :src="saiMsgImgs[ind]" alt="" v-if="val.cli">
						<img :src="saiMsgImgs[ind+7]" alt="" v-if="!val.cli">
						<span>{{val.name}}</span>
					</li>
				</ol>
			</div>
			<div class="searchif_GF_searchMsg_div2">
				<ol>
					<li @click="delAllSelectBtns()">清空</li>
					<li @click="sureSelectBtns()">确定</li>
				</ol>
			</div>
		</div>
	</div>
</template>
<script>
	import img01 from "@/components/home/sign/buit01.png"
	import img02 from "@/components/home/sign/buit05.png"
	var guideFood={
		name:"guideFood",
		data(){
			return {
				// 筛选里面的信息
				saiMsgImgs:[img01,img01,img01,img01,img01,img01,img01,img02,img02,img02,img02,img02,img02,img02],
				saiMsg:[
					{url:img01,name:"蜂鸟专送",id:0,cli:true},
					{url:img01,name:"品牌商家",id:1,cli:true},
					{url:img01,name:"外卖保",id:2,cli:true},
					{url:img01,name:"准时达",id:3,cli:true},
					{url:img01,name:"新店",id:4,cli:true},
					{url:img01,name:"在线支付",id:5,cli:true},
					{url:img01,name:"开发票",id:6,cli:true},
				],
				allFood:[],
				onId:207,
				firstMsg:"分类",
				first:false,
				secondMsg:"排序",
				second:false,
				ThirdMsg:"筛选",
				third:false,
				// 筛选下面的东西
				ogg:-1,
				// 排序里面的信息
				orderBy:[
					{url:img01,id:4,name:"智能排序"},
					{url:img01,id:5,name:"距离最近"},
					{url:img01,id:6,name:"销量最高"},
					{url:img01,id:6,name:"起送价最低"},
					{url:img01,id:2,name:"配送速度最快"},
					{url:img01,id:3,name:"评分最高"},
				],

			}
		},
		computed:{

		},
		props:["cd","kd","od"],
		created(){
			var _this=this
			this.Arounds('','',function(el){
				_this.allFood=el
			})
		},
		methods:{
			handle1(){
				this.first=!this.first;
				this.second=false;
				this.third=false;
				this.cd()
			},
			handle2(){
				this.second=!this.second
				this.first=false;
				this.third=false;
			},
			handle3(){
				this.third=!this.third
				this.second=false;
				this.first=false;
			},
			// 分类需求
			choosebtns(el){
				this.onId=el
			},
			goodbtns(el,bl){
				this.first=false;
				var cd=[el+'/'+bl]
				this.$store.commit("goodSelectMsg",cd)
				this.kd()
			},
			// 排序需求
			goBtns(el,bl){
				var orderBy={
					id:el,
					name:bl
				}
				this.$store.commit("orderBy",orderBy)
				this.second=false
				this.cd()
			},
			// 筛选需要
			selectBtns(el,bl){
				var selectMsg={
					id:el,
					name:bl
				}
				this.$store.commit("selectMsg",selectMsg);
				this.ogg=el;
				if (this.saiMsg[el].cli) {
					this.saiMsg[el].cli=false
				}else{
					this.saiMsg[el].cli=true
				}
				if(this.ogg!=-1){
					this.ogg=-1
				}
			},
			delAllSelectBtns(){
				this.saiMsg.forEach(function(val){
					val.cli=true
				})
				this.ogg=-1;
				this.$store.commit("delAllSelectMsg")
			},
			  //点击确定
			sureSelectBtns(){
				// 如果有了 筛选条件就打开
				this.od()
				this.third=false;
			}
		}
	}
	export default guideFood

</script>
<style scoped>
	.lisnnop{
		color:#3190e8;
	}
	#searchif_GF{
		position: relative;
		height: 100%;
	}
	#searchif_GF_title{
		width: 100%;
		height: 100%;
		display: flex;
		flex-wrap:nowrap;
		flex-direction:row;
		justify-content:space-around;
		background: white;
	}
	.searchif_GF_title_true,.searchif_GF_title_false{
		width:100%;
		height:0.28rem;
		line-height: 0.3rem;
		text-align: center;
		font-size: 0.13rem;
	}
	.searchif_GF_title_true{
		color:#333;
	}
	.searchif_GF_title_false{
		color:#3190e8;
	}
	.searchif_GF_title_true:nth-child(even){
		border-right: 0.01rem solid #e4e4e4;
		border-left: 0.01rem solid #e4e4e4;
	}
	.searchif_GF_title_false:nth-child(even){
		border-right: 0.01rem solid #e4e4e4;
		border-left: 0.01rem solid #e4e4e4;
	}
	#searchif_GF_choose{
		position: absolute;
		top: 0.325rem;
		left: 0;
		width: 100%;
		display: flex;
		justify-content:space-between;
		background: white;
	}
	.searchif_GF_choose_left{
		height:100%;
		width: 1.8rem;
		background:#f5f5f5;
	}
	.searchif_GF_choose_left ol{
		width: 100%;
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		overflow-y: scroll;
	}
	.searchif_GF_choose_left ol::-webkit-scrollbar {display:none}
	.searchif_GF_choose_left ol li{
		height:0.42rem;
		display: flex;
		flex-direction:row;
		justify-content:space-between;
	}
	.searchif_GF_choose_left_div1{
		height:100%;
		font-size: 0.12rem;
		color:#666;
		overflow: hidden;
		position: relative;
	}
	.searchif_GF_choose_left_div1 img{
		width: 0.19rem;
		height: 0.19rem;
		position: absolute;
		left: 0.11rem;
		top: 0.07rem;

	}
	.searchif_GF_choose_left_div1 span{
		display: inline-block;
		height:0.2rem;
		margin-left: 0.38rem;
		padding-top: 0.11rem;
	}
	.searchif_GF_choose_left_div2{
		width:0.45rem;
		display: flex;
		flex-direction:row;
		justify-content:flex-end;
		line-height: 0.4rem;
	}
	.searchif_GF_choose_left_div2 div{
		font-size: 0.13rem;
		margin-top: 0.11rem;
		line-height: 0.19rem;
		margin-right: 0.05rem;
		background: #ccc;
		color:#fff;
		border-radius: 0.2rem;
		height:0.19rem;
		padding: 0.01rem 0.03rem 0;
	}
	.searchif_GF_choose_left_div2 span i{
		font-size: 0.13rem;
		color:#ccc;
		margin-right: 0.08rem;
	}
	.searchif_GF_choose_right{
		width:1.85rem;
		text-align: center;
		background: #fff;
	}
	.searchif_GF_choose_right ul{
		width: 1.7rem;
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		overflow-y:scroll;
	}
	.searchif_GF_choose_right ul::-webkit-scrollbar {display:none}
	.searchif_GF_choose_right ul li{
		height:0.41rem;
		display: flex;
		flex-direction:row;
		justify-content:space-between;
		border-bottom: 0.01rem solid #e4e4e4;
		line-height: 0.42rem;
		font-size: 0.13rem;
		color:#666;
	}
	#searchif_GF_orderBy{
		width:100%;
		text-align: center;
		background: #fff;
	}
	#searchif_GF_orderBy ul{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
	}
	#searchif_GF_orderBy ul li{
		position: relative;
		text-align: left;
		height:0.585rem;
	}
	#searchif_GF_orderBy ul li img{
		width:0.16rem;
		height:0.16rem;
		position: absolute;
		left: 0.18rem;
		top: 50%;
		transform:translateY(-48%);
	}
	#searchif_GF_orderBy ul li div{
		height:100%;
		line-height: 0.6rem;
		font-size: 0.15rem;
		margin-left: 0.5rem;
		padding-left: 0.1rem;
		color:#333;
		border-bottom: 0.005rem solid #e4e4e4;
	}
	#searchif_GF_searchMsg{
		width:100%;
		background: #fff;
	}
	.searchif_GF_searchMsg_div1{
		width:93%;
		margin: 0 auto;
		text-align: left;
		font-size: 0.14rem
	}
	.searchif_GF_searchMsg_p1{
		height:0.36rem;
		line-height: 0.36rem;
		margin-top: 0.05rem;
	}
	.searchif_GF_searchMsg_div1 ol{
		display: flex;
		flex-wrap:wrap;
		justify-content:space-around;
		margin-bottom: 0.13rem;

	}
	.searchif_GF_searchMsg_li{
		position: relative;
		height: 0.32rem;
		font-size: 0.14rem;
		line-height: 0.32rem;
		background: #fff;
		border-radius: 0.1rem;
		border: 0.005rem solid #eee;
		width:1.05rem;
		margin-top: 0.05rem;

	}
	.searchif_GF_searchMsg_li img{
		width:0.16rem;
		position: absolute;
		left: 0.06rem;
		top: 50%;
		transform:translateY(-50%);
	}
	.searchif_GF_searchMsg_li span{
		margin-left: 0.3rem;
	}
	.searchif_GF_searchMsg_div2{
		background: #f1f1f1;
		height:0.58rem;
		width:100%;
	}
	.searchif_GF_searchMsg_div2 ol{
		display: flex;
		flex-direction:row;
		flex-wrap:nowrap;
		justify-content:space-around;
	}
	.searchif_GF_searchMsg_div2 ol li{
		width: 1.70rem;
		height:0.45rem;
		line-height: 0.45rem;
		border-radius: 0.1rem;
		background: #fff;
		margin-top: 0.065rem;
	}
	.searchif_GF_searchMsg_div2 ol li:nth-child(2){
		background: #56d176;
		color:#fff;
	}
</style>


