import Vue from 'vue'
import Router from 'vue-router'
//==================杨冰冰====================
// 主页面的跳转
import Home from "@/components/home"
import Shop from "@/components/module/shop"
import SearchIf from "@/components/module/searchIf"
import Order from "@/components/module/order"

// 店铺内的跳转
import ShopMsg from "@/components/shop/shopMsg"
import Shopfood from "@/components/shop/shopFood"
import AroundStore from "@/components/home/AroundStore"
import ShopLastMsg from "@/components/shop/shopLastMsg"
import FoodMsg from "@/components/shop/foodMsg"
import Shop_payMoney from "@/components/shop/payMoney"
import Shop_orderOtherMsg from "@/components/shop/orderOtherMsg"
import Shop_orderGivePath from "@/components/shop/orderGivePath"
import AllorderMsg from "@/components/order/AllorderMsg"



//-------------------王明凯---------------------------------
// 城市界面
import City from '@/components/module/city'
// 城市搜索
import Cutcity from '@/components/citys/cutcity'
// 搜索界面
import Search from '@/components/module/search'

// home中登录界面
import Homelogin from '@/components/logins/login'
import Resetpass from '@/components/logins/resetpass'

// 我的信息界面
import Mui from '@/components/module/mui'

//我的信息登录后账户信息
import Logined from '@/components/logineds/logined'
import Userinfo from '@/components/logineds/userinfo'
import Profile from '@/components/logineds/profile'
import Newprofile from '@/components/logineds/newprofile'
import Adddetail from '@/components/logineds/adddetail'

//会员中心
import ClubCenter from '@/components/myJU/clubCard/clubCenter'
import Clubstate from '@/components/myJU/clubCard/clubstate'
import Clubbuy from '@/components/myJU/clubCard/clubbuy'
import Clubpass from '@/components/myJU/clubCard/clubpass'
import Clubinvoice from '@/components/myJU/clubCard/clubinvoice'

//我的优惠
import Historyhongbao from '@/components/myJU/wodeyouhuis/historyhongbao'
import Duihuanhongbao from '@/components/myJU/wodeyouhuis/duihuanhongbao'
import Tuijian from '@/components/myJU/wodeyouhuis/tuijian'
import DaijinquanSm from '@/components/myJU/wodeyouhuis/daijinquanSm'
import HongbaoSm from '@/components/myJU/wodeyouhuis/hongbaoSm'



// 信息界面部分配置跳转路由
import Wodeyue from '@/components/module/wodeyue'
import Wodejifen from '@/components/module/wodejifen'
import Wodeyouhui from '@/components/module/wodeyouhui'
import Yuewenti from '@/components/myJU/wodeyues/yuewenti'
import Jifenwenti from '@/components/myJU/wodejifens/jifenwenti'


//--------------邢玉祥---------------------
import Download from '@/components/myJU/server/download'
import ServiceCenter from '@/components/myJU/server/service'
import ServeCenterQuest from '@/components/myJU/server/serviceContents/serverContents'

 	

Vue.use(Router)

export default new Router({
	routes: [
		//====================杨冰冰======================
		{
			path: '/',
			component: City
		},
		{
			path: '/home',
			name: 'home',
			component: Home,
		},
		{
			path: '/shop',
			name: 'shop',
			component: Shop,
			redirect: "/shop/shopfood",
			children: [{
					path: "/shop/shopfood",
					name: "shopfood",
					component: Shopfood
				},
				{
					path: "/shop/shopmsg",
					name: "shopmsg",
					component: ShopMsg
				},
			]
		},
		{
			path: '/shop/shopLastMsg',
			name: 'shopLastMsg',
			component: ShopLastMsg,
		},
		{
			path: '/order',
			name: 'order',
			component: Order
		},
		{
			path: '/order/allorderMsg',
			name: 'allorderMsg',
			component: AllorderMsg,
		},
		{
			path: '/searchIf',
			name: 'searchIf',
			component: SearchIf,
			children: [{
				path: '/searchIf/aroundstore',
				name: 'aroundstore',
				component: AroundStore
			}]
		},
		{
			path: '/shop/shopfood/foodMsg',
			name: 'foodMsg',
			component: FoodMsg,
		},
		{
			path: '/shop/shopfood/payMoney',
			name: 'payMoney',
			component: Shop_payMoney,
		},
		{
			path: '/shop/shopfood/payMoney/orderOtherMsg',
			name: 'orderOtherMsg',
			component: Shop_orderOtherMsg,
		},
		{
			path: '/shop/shopfood/payMoney/orderGivePath',
			name: 'orderGivePath',
			component: Shop_orderGivePath,
		},


		//---------------王明凯-----------------------
		{
			path: '/city',
			name: 'city',
			component: City
		},
		{
			path: '/cutcity/:id?',
			name: 'cutcity',
			component: Cutcity
		},

		{
			path: '/resetpass',
			name: 'resetpass',
			component: Resetpass
		},
		{
			path: '/login',
			name: 'homelogin',
			component: Homelogin
		},
		{
			path: '/search',
			name: 'search',
			component: Search
		},
		{
			path: '/mui',
			name: 'mui',
			component: Mui
		},
		{
			path: '/balance',
			name: 'wodeyue',
			component: Wodeyue,

		},
		{
			path: '/balance/issoe',
			name: 'yuewenti',
			component: Yuewenti,
		},
		{
			path: '/points',
			component: Wodejifen,

		},
		{
			path: '/points/issoe',
			name: 'jifenwenti',
			component: Jifenwenti,
		},
		{
			path: '/benef',
			name: 'wodeyouhui',
			component: Wodeyouhui
		},
		{
			path: '/benef/historyHb',
			name: 'historyhongbao',
			component: Historyhongbao
		},
		{
			path: '/benef/exchange',
			name: 'duihuanhongbao',
			component: Duihuanhongbao
		},
		{
			path: '/benef/commend',
			name: 'tuijian',
			component: Tuijian
		},
		{
			path: '/benef/hongbaosm',
			name: 'hongbaoSm',
			component: HongbaoSm
		},
		{
			path: '/benef/daijinquansm',
			name: 'daijinquanSm',
			component: DaijinquanSm
		},
		{
			path: '/order',
			name: 'order',
			component: Order
		},

		{
			path: '/logined',
			name: 'logined',
			component: Logined,

		},
		{
			path: '/logined/userinfo',
			name: 'userinfo',
			component: Userinfo
		},
		{
			path: '/logined/profile',
			name: 'profile',
			component: Profile
		},
		{
			path: '/logined/profile/address',
			name: 'newprofile',
			component: Newprofile
		},
		{
			path: '/logined/profile/address/adddetail',
			name: 'adddetail',
			component: Adddetail
		},
		{
			path: '/clubcenter',
			name: 'clubCenter',
			component: ClubCenter
		},
		{
			path: '/clubcenter/clubstate',
			name: 'clubstate',
			component: Clubstate
		},
		{
			path: '/clubcenter/clubbuy',
			name: 'clubbuy',
			component: Clubbuy
		},
		{
			path: '/clubcenter/clubpass',
			name: 'clubpass',
			component: Clubpass
		},
		{
			path: '/clubcenter/clubinvoice',
			name: 'clubinvoice',
			component: Clubinvoice
		},
		

	//------------------邢玉祥--------------------------
		{
			path: '/mui/download',
			name: 'download',
			component: Download
		},
		{
			path: '/mui/serverCenter',
			name: 'serverCenter',
			component: ServiceCenter
		},
	    {
	      path: '/mui/serverCenter/content',
	      name: 'ServeContent',
	      component: ServeCenterQuest
	    },

	]
	// mode:"history"
})


