<template>
	<div id="clubinvoice_wrap">
		<!--导航-->
		<div class="clubinvoice_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;">购买记录</p>
		</div>
		
		
		
		
	</div>
</template>

<script>
	var Clubinvoice = {
		name:"clubinvoice",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		}
	}
	export default Clubinvoice
</script>

<style scoped>
	.clubinvoice_nav{
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center
	}
	.clubinvoice_nav>i{
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
		color: white;
	}
</style>


