<template>
	<div id="hometop_to">
		<router-link :to="{name:gogogo}">
			<i class="el-icon-arrow-left"></i>
		</router-link>
	</div>
</template>

<script>
	var gohistory = {
		name: "gohistory",
		data(){
			return {
				urlName:""
			}
		},
		props:["gogogo"],
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		
	}
	export default gohistory
</script>

<style scoped>
	#hometop_to {
		width: 100%;
		text-align: center;
		height: 100%;
		position: relative;
	}
	i {
		color: white;
		position: absolute;
		left: 0;
		top: 0;
		font-size: 0.24rem;
		float: left;
		margin-left: 0.09rem;
		line-height: 0.45rem;
	}
</style>


