<template>
	<div id="shop_SF">
		<!-- 订单左边的信息,处理右边视频的内容 -->
		<div id="shop_SF_left">
			<ol class="shop_SF_lol">
				<li v-for="(val,ind) in shopfoods" :key="ind" :class="{shop_SF_lli:true,shop_msg_tiaozi:isClick==ind,shop_msg_once:onceClick==ind}" @click="colorSelector($event,ind)"> 

					<img :src='"https://fuss10.elemecdn.com/"+val.icon_url+".jpeg"' alt="" style="width:0.18rem;">
					
					<a @click="goEv($event,ind)">{{val.name}}</a>
				</li>
			</ol>
		</div>
		<!-- 商铺食物的右边的信息,订单的内容从这里处理 -->
		<div id="shop_SF_right">
			<ul id="shop_SF_rul">
				<li v-for="(val,ind) in foods" :key="ind" class="shop_SF_rli" >
					<!-- 食品标题,用于分栏 -->
					<div class="shop_SF_rli_div1" :id="'mmp'+ind">
						<span class="shop_SF_rli_span1">{{val.name}}</span>
						<span class="shop_SF_rli_span2">{{val.description}}</span>
						<span class="shop_SF_rli_span3"></span>
					</div>
					<!-- 食品内容 -->
					<div v-for="(value,index) in val.foods" :key="index" class="shop_SF_rli_div2">
						<!-- 食品的图片以及是不是新品 -->
						<div v-for="vs in value.attributes" v-if="vs.icon_name=='新'" class="ssrld2d1" @click="gotoFoodMsg(value)">新品</div>
						<div class="shop_SF_rli_div2_left" @click="gotoFoodMsg(value)" v-if="value.image_path">
							<img :src="'https:elm.cangdu.org/img/'+value.image_path" alt="" style="width:0.48rem;height:0.48rem">
						</div>
						<div class="shop_SF_rli_div2_right">
							<div class="shop_SF_rli_div2_p1" @click="gotoFoodMsg(value)"><span>{{value.name}}</span>
								<div v-for="vs in value.attributes" v-if="vs.icon_name=='招牌'">招牌</div>
							</div>
							<p v-if="value.activity" class="shop_SF_rli_div2_p2" @click="gotoFoodMsg(value)">{{value.activity.image_text}}</p>
							<p class="shop_SF_rli_div2_p3" @click="gotoFoodMsg(value)">
								<span class="shop_SF_rli_div2_p3_span1">月销售{{value.month_sales}}份</span>
								<span class="shop_SF_rli_div2_p3_span2">好评率{{value.satisfy_rate}}%</span>
							</p>
							<p class="shop_SF_rli_div2_p4" v-if="value.description!=''" @click="gotoFoodMsg(value)">{{value.description}}</p>
							<div class="shop_SF_rli_div2_p5">
								<span @click="gotoFoodMsg(value)">{{value.specfoods[0].price}}</span><span>起</span>
								<div v-if="value.isDel" class="shop_SF_rli_div2_p3_div2" @click="priceDel(value._id)" style="transform:translateX(-0.6rem);top:0.04rem;padding:0 0.05rem;background:#fff;color:#666;border:1px solid #666">-</div>

								<div v-if="value.isDel" class="shop_SF_rli_div2_p3_div2" @click="priceDel(value._id)" style="transform:translateX(-0.3rem);top:0.05rem;font-size:0.14rem;background:white;color:#666;border:0">{{value.count}}</div>

								<div v-if="value.specfoods[0].specs==[]" class="shop_SF_rli_div2_p3_div1">选规格</div>
								<div v-if="value.specfoods[0].specs!=[]" class="shop_SF_rli_div2_p3_div2" @click="priceAdd(value.name,value.specfoods[0].price,'默认',value._id,value.count)">+</div>
							</div>
						</div>
					</div>
				</li>
			</ul>
		</div>
		<div class="shopBuy">
			<Shop_shopBug :cliAdd="cleanAdd" :cliDel="cleanCut" :cliClear="cleanBoth"></Shop_shopBug>
		</div>
	</div>
</template>
<script>
	// "cliAdd","cliDel","cliClear"
	import Vue from 'vue'
	import { Loading } from 'element-ui';
	import Shop_shopBug from "@/components/shop/shopBug"
	var shopFood = {
		name:"shopfood",
		data(){
			return {
				msg:{},
				// 左边的导航
				shopfoods:[],
				// 右边的食物内容
				foods:[],
				// 每次点击,我都可以更改值,来和index相同,来达到true的判断,致使我的class可以为单独的内容添加
				isClick:-1,
				// 当网页刚穿件成功时,用一个一次性的,让其可以达到想要的状态
				onceClick:0,
				isDel:true,
				count:0
			}
		},
		components:{
			Shop_shopBug
		},
		created(){
			var loadingInstance = Loading.service({ fullscreen: true });
			// 获取路由传值过来的id
			var shopid= this.$store.state.shopId
			var _this=this
			// 获取商铺信息
			this.StoreMsg(shopid,function(el){
				_this.msg=el
			});
			// 获取所有事物信息
			this.shopfooder(shopid,function(el){
				el.forEach(function(val){
					val.foods.forEach(function(value){
						// 用set方法,设置对象中添加新属性
						Vue.set(value,"count",0)
						Vue.set(value,"isDel",false)
					})
				})
				// 左边的导航
				_this.shopfoods=el
				// 右边的食物内容
				_this.foods = el
				loadingInstance.close();
				_this.$store.commit("AllBuyMsg",el)
			});
		},
		methods:{
			goEv(ev,ind){
				var heg=0;
				var lent=0;
				if (ind>0) {
					for (var i = 0; i < ind; i++) {
						lent += this.shopfoods[i].foods.length
					};
				};
				heg = 120*lent+48
				var ul = document.getElementById("shop_SF_right")
				ul.scrollTop = heg

			},
			// 监听加减号的触发事件
			cleanAdd(id){
				this.foods.forEach(function(value){
					value.foods.forEach(function(items){
						if (items._id == id) {
							items.count+=1
							items.isDel=true
						};
					})
				})
				this.$store.commit("AllBuyMsg",this.foods)
			},
			cleanCut(id){
				var _this =this
				this.foods.forEach(function(value){
					value.foods.forEach(function(items){
						if (items._id == id) {
							if (items.count) {
								items.count-=1
								if (items.count ==0) {
									items.isDel=false
								};
							}else{
								items.count = 0
								items.isDel=false
							}
						};
					})
				})
				this.$store.commit("AllBuyMsg",this.foods)
			},
			cleanBoth(){
				var _this= this
				this.foods.forEach(function(val){
					val.foods.forEach(function(value){
						// 用set方法,设置对象中添加新属性
						value.count=0;
						value.isDel=false
					})
				})
				// 左边的导航
				_this.shopfoods=_this.foods
				this.$store.commit("AllBuyMsg",this.foods)
			},

			// 左边的位置,点击时让我内容改变,同时取消初始的样式
			colorSelector(ev,index){
				this.isClick = index;
				this.onceClick=-1;
			},


			// 每次点击,让我可以把数据储存起来,并且可以更改金额
			// 在这里我做了两件事:1.把共有属性做出更改	2.把我当前的私有属性根据共有属性的变化,进行改变
			priceAdd(al,bl,cl,dl,el){
				// 处理公共数据
				var el={
					name:al,
					prise:bl,
					msg:cl,
					id:dl,
					count:el
				}
				this.$store.commit('priceAdd',el)


				// 处理私有属性
				this.cleanAdd(dl)
				
			},

			// 点击-号的时候,可以让我成功的删除数据的count的值,方便我的操作
			// 在这里我做了两件事:1.把共有属性做出更改	2.把我当前的私有属性根据共有属性的变化,进行改变
			priceDel(el){
				// 处理公共数据
				this.$store.commit('priceDel',el)

				// 处理私有数据
				var _this =this
				this.cleanCut(el)
			},

			// 点击食品,先储存食品信息,然后在跳转
			gotoFoodMsg(el){
				this.$store.commit("foodMsg",el)
				this.$router.push({name:'foodMsg'})
			}
		},
		watch:{
			shopfoods(newVal,oldVal){
				// this.foods = newVal
				// 做一件事: 查询到当前的购物车有没有id的信息,有的话,代表有内容,然后就给你
				var playBuy = this.$store.state.playBuy
				var prise=0;
				var _this=this;
				console.log(newVal)
				playBuy.forEach(function(val){
					prise += val.prise*val.lens
					if (prise>= 20) {
						_this.foods.forEach(function(value){
							value.foods.forEach(function(items){
								if (items._id == val.id) {
									items.count= val.lens
									items.isDel=true
								};
							})
						})
					}
				})
			},
			foods(news){
				
			}
		},
		

	}
	export default shopFood
</script>
<style scoped>

	#shop_SF{
		position: relative;
		width: 100%;
		height:100%;
	}
	.shopBuy{
		position: fixed;
		height:0.48rem;
		bottom: 0;
		z-index: 10;
		width: 100%;
	}
	#shop_SF_left{
		position: absolute;
		left: 0;
		top: 0;
		width: 0.9rem;
		z-index: 1;
		height:100%;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
		overflow-y: scroll;
	}
	#shop_SF_left::-webkit-scrollbar {display:none}
	.shop_SF_lol{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		background-color: #f5f5f5;
		height: 100%;
	}
	.shop_SF_lli{
		width: 0.82rem;
		height: 0.65rem;
		border-bottom: 0.01rem solid #fff;
		line-height: 0.65rem;
		position: relative;
		text-align: left;
	}
	.shop_msg_tiaozi{
		border-left:0.04rem solid #3190e8;
		font-weight: 800;
		background-color: #fff;
	}
	.shop_msg_once{
		border-left:0.04rem solid #3190e8;
		background-color: #fff;
	}
	.shop_SF_lli img{
		position: absolute;
		left: 0.05rem;
		top: 50%;
		transform:translateY(-50%);
	}
	.shop_SF_lli a{
		color: #666;
		font-size: 0.16rem;
		margin-left: 0.28rem;
	}
	#shop_SF_right{
		width:2.86rem;
		position: absolute;
		right: 0;
		top: 0;
		height:100%;
		overflow: hidden;
		overflow-y:scroll; 
	}
	#shop_SF_right::-webkit-scrollbar {display:none}
	.shop_SF_rul{
		width: 100%;
		padding-bottom: 0.54rem;
	}
	.shop_SF_rli{
		display: flex;
		justify-content:flex-start;
		flex-direction:column;
		background: #f5f5f5;
	}
	.shop_SF_rli_div1{
		height: 0.48rem;
		position: relative;
		text-align: left;
	}
	.shop_SF_rli_span1{
		font-size: 0.2rem;
		margin-left: 0.2rem;
		line-height: 0.55rem;
		color:#666;
		font-weight: 700;
	}
	.shop_SF_rli_span2{
		font-size: 0.14rem;
		margin-left: 0.02rem;
		line-height: 0.55rem;
		color:#999;
	}
	.shop_SF_rli_span3{
		background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAUCAYAAACXtf2DAAAAAXNSR0IArs4c6QAAAFhJREFUOBFjYBgFoyEwGgKMs2bN8gQGwyxoUKQBwXZswUKuOhaQ4f///5cBGcrIyAiySBbExgLIUseExSCqCoEsSAO6/AkIg9h4TKe2OjxWjUqNhsDwCgEACvMiGUpibN4AAAAASUVORK5CYII=);
		width: 0.2rem;
		height:0.05rem;
		background-size:100% .2rem;
		background-repeat:no-repeat;
		background-position:0;
		position: absolute;
		right:0.1rem;
		top: 0.2rem;
	}
	
	.ssrld2d1{
		position: absolute;
		left: -0.05rem;
		top: 0.06rem;
		width: 0.35rem;
		font-size: 0.08rem;
		color:white;
		background: red;
		transform:rotateZ(-45deg);
		z-index: 1;
	}
	.shop_SF_rli_div2{
		display: flex;
		justify-content:space-between;
		flex-direction:row;
		background: white;
		height: 1rem;
		overflow: hidden;
		position: relative;
		padding: 0.1rem 0.07rem;
	}
	.shop_SF_rli_div2_left{
		width: 0.5rem;
		height:0.5rem;
		position: absolute;
		top: 0.14rem;
		left: 0.06rem;
		z-index: 0;
	}
	.shop_SF_rli_div2_left img{
		margin-top: 0.01rem;
		margin-left: 0.05rem;
	}
	.shop_SF_rli_div2_right{
		width: 2.11rem;
		text-align: left;
		margin-left: 0.6rem;
	}
	.shop_SF_rli_div2_right>p{
		margin-top: 0.01rem;
	}
	.shop_SF_rli_div2_right>div{
		margin-top: 0.02rem;
	}
	.shop_SF_rli_div2_p1{
		height: 0.2rem;
		color:black;
		position: relative;
		line-height: 0.2rem;
	}
	.shop_SF_rli_div2_p1 div{
		position: absolute;
		right: 0;
		top: 0;
		font-weight: 400;
		font-size:0.08rem;
		color:rgb(240, 115, 115);
		line-height: .14rem;
		height:0.12rem;
		border: 1px solid rgb(240, 115, 115);
		border-radius:5px;
		width:0.28rem;
		text-align: center;

	}
	.shop_SF_rli_div2_p1 span{
		font-size: 0.16rem;
		font-weight:bold;
	}
	.shop_SF_rli_div2_p2{
		font-size: 0.08rem;
		color:#999;
		height:0.14rem;
		line-height: 0.14rem;
	}
	.shop_SF_rli_div2_p3{
		height:0.22rem;
		font-size: 0.14rem;
		color:#333;
		line-height: 0.22rem;
		
	}
	.shop_SF_rli_div2_p3 span{
		font-weight: bold;
	}
	.shop_SF_rli_div2_p3_span2{
		margin-left: 0.03rem;
	}
	.shop_SF_rli_div2_p4{
		display: inline-block;
		font-size: 0.08rem;
		height:0.12rem;
		line-height: 0.14rem;
		text-align: center;
		border: 1px solid rgb(240, 115, 115);
		color:rgb(240, 115, 115);
		border-radius: 0.05rem;
		padding-left: 0.04rem;
		padding-right: 0.04rem;
	}
	.shop_SF_rli_div2_p5{
		height:0.2rem;
		width: 100%;
		position: relative;

	}
	.shop_SF_rli_div2_p5 span{
		font-size: 0.16rem;
		line-height: 0.2rem;
		color:#f60;
		font-weight:700;
	}
	.shop_SF_rli_div2_p5 span+span{
		color:#666;
		font-size: 0.15rem;
		line-height: 0.21rem;
		margin-left: 0.04rem;
		font-weight:400;
	}
	.shop_SF_rli_div2_p5 div{
		position: absolute;
		top: 50%;
		transform:translateY(-40%);
		right: 0.05rem;
		padding: 0.02em 0.04rem;
		background:#3190e8; 
		border: 1px solid #3190e8;
		border-radius: 0.08rem;
		color: #fff;
		font-size: 0.12rem;
		line-height: 0.12rem;
		height:0.14rem;
		text-align: center;
	}

	.shop_SF_rli_div2_p3_div2{
		border-radius: 50% !important;
		line-height: 0.13rem !important;
	}
</style>


