<template>
<div id="shop_lastMsg">
	<div class="shop_lastMsg_top">
		<i class="el-icon-arrow-left" @click="sear()"></i>
		<span>{{title}}</span>
	</div>
	<div class="shop_lastMsg_active">
		<ul>
			<p>活动与属性</p>
			<li v-for="(val,ind) in msg.activities" v-if="val.icon_name">
				<div class="shop_lastMsg_ul_li_div1">{{val.icon_name}}</div>
				<div class="shop_lastMsg_ul_li_div2">{{val.description}}(app专享)</div>
			</li>
			<li v-for="(val,ind) in msg.supports" v-if="val.icon_name">
				<div class="shop_lastMsg_ul_li_div1">{{val.icon_name}}</div>
				<div class="shop_lastMsg_ul_li_div2">{{val.description}}(app专享)</div>
			</li>
		</ul>
	</div>
	<div class="shop_lastMsg_safe">
		<div class="shop_lastMsg_safe_div1">食品监督安全公式</div>
		<div class="shop_lastMsg_safe_div2">
			<img :src="src.img01" alt="" v-if="msg.recent_order_num>=500">
			<img :src="src.img02" alt="" v-if="msg.recent_order_num<500">
			<div>
				<p v-if="msg.recent_order_num>=500">监督检查结果:<span>优</span></p>
				<p v-if="msg.recent_order_num<500">监督检查结果:<span>差</span></p>
				<p v-if="msg.identification">检查日期;{{msg.identification.licenses_date}}</p>
			</div>
		</div>
		<div class="shop_lastMsg_safe_div3"></div>
	</div>
	<div class="shop_lastMsg_shopMsg">
		<ul style="text-align:left">
			<li>商家信息</li>
			<li>{{msg.name}}</li>
			<li style="width:90%;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">地址:{{msg.address}}</li>
			<li>营业时间:{{msg.opening_hours}}</li>
			<li>营业执照</li>
			<li>餐饮服务许可证</li>
		</ul>
	</div>
</div>
</template>
<script>
import img01 from "@/components/shop/shopImg/imgs11.jpg"
import img02 from "@/components/shop/shopImg/imgs12.jpg"
	var shopLastMsg = {
		name:"shopLastMsg",
		data(){
			return {
				msg:{},
				title:"商家详情",
				src:{
					img01:img01,
					img02:img02
				}
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		created(){
			var shopId = this.$store.state.shopId
			var _this = this
			this.StoreMsg(shopId,function(el){
				_this.msg=el
			})
			var title= this.$router.params
		}
	}
	export default shopLastMsg
</script>
<style scoped>
	#shop_lastMsg{
		width: 100%;
		text-align: left;
		height: 100%;
		position: relative;
		background: #f5f5f5;
		padding-top: 0.53rem;
	}
	.shop_lastMsg_top{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height:0.45rem;
		background-color: #3190e8;
		font-size: 0.2rem;
		font-weight: 600;
		text-align: center;
		line-height: 0.45rem;
		color: #fff;
	}
	.shop_lastMsg_top i{
		color: white;
		position: absolute;
		left: 0;
		top: 0;
		font-size: 0.24rem;
		float: left;
		margin-left: 0.09rem;
		line-height: 0.45rem;
	}
	.shop_lastMsg_active{
		background-color: #fff;
		width: 100%;
	}
	.shop_lastMsg_active ul{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		overflow: hidden;
	}
	.shop_lastMsg_active ul p{
		height:0.43rem;
		line-height: 0.43rem;
		padding-left: 0.14rem;
		border-bottom: 0.01rem solid #e3e3e3;
		font-size: 0.17rem;
	}
	.shop_lastMsg_active ul li{
		display: flex;
		flex-direction:start;
		justify-content:flex-start;
		padding-left: 0.14rem;
		height:0.43rem;
		line-height: 0.43rem;
	}
	.shop_lastMsg_ul_li_div1{
		background-color: skyblue;
		height: 0.16rem;
		line-height: 0.18rem;
		text-align: center;
		padding: 0.02rem 0.03rem;
		margin-top: 0.1rem;
		margin-right: 0.1rem;
		font-size: 0.14rem;
		font-weight: 600;
		color:#fff;
	}
	.shop_lastMsg_ul_li_div2{
		font-size: 0.15rem;
		width:3.2rem;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
	}
	.shop_lastMsg_safe{
		width: 100%;
		background-color: #fff;
		margin-top: 0.1rem;
		overflow: hidden;
	}
	.shop_lastMsg_safe_div1{
		height:0.43rem;
		line-height: 0.43rem;
		position: relative;
		font-size: 0.17rem;
		padding-left: 0.14rem;
	}
	.shop_lastMsg_safe_div2{
		padding-left: 0.14rem;
		position: relative;
		width: 100%;
		height:0.9rem;
	}
	.shop_lastMsg_safe_div2 img{
		width:0.48rem;
		height:0.48rem;
		position: absolute;
		left: 0.14rem;
		top: 0.18rem;
	}
	.shop_lastMsg_safe_div2 div{
		height: 100%;
		margin-left: 0.64rem;
		display:flex;
		flex-direction:column;
		justify-content:center;
	}
	.shop_lastMsg_safe_div2 div p{
		font-size: 0.15rem;
	}
	.shop_lastMsg_safe_div2 div p span{
		color:red;
	}
	.shop_lastMsg_safe_div2 div p+p{
		margin-top:0.1rem;
	}
	.shop_lastMsg_shopMsg{
		width: 100%;
		background-color: #fff;
		margin-top: 0.1rem;
		overflow: hidden;
	}
	.shop_lastMsg_shopMsg ul{
		display: flex;
		flex-direction:column;
		justify-content:flex-start;
		font-size: 0.15rem;
	}
	.shop_lastMsg_shopMsg li{
		padding-left: 0.14rem;
		height:0.48rem;
		line-height: 0.43rem;
		color:#666;
		border-bottom: 0.005rem solid #f5f5f5;
	}
	.shop_lastMsg_shopMsg li:nth-child(1){
		color:#2c3e50;
		font-size: 0.17rem;
	}
</style>


