<template>
	<div id="shopGuide">
		<el-menu
		  :default-active="activeIndex2"
		  class="el-menu-demo"
		  mode="horizontal"
		  backround-color="white"
		  text-color="#666"
		  active-text-color="#3190e8">
		  <el-menu-item index="1">
		  <router-link :to="{name:'shopfood'}">商品</router-link>
		  <!--to="###"-->
		  </el-menu-item>
		  <el-menu-item index="2">
		 	 <router-link :to="{name:'shopmsg'}">评价</router-link>
		 	 <!-- to="###"-->
		  </el-menu-item>
		</el-menu>
	</div>
</template>
<script>
	
	var shopGuide = {
		name:"shopGuide",
		data(){
			return {
				msg:{},
				activeIndex2: '1'
			}
		},
		created(){
			var _this=this
			var shopid= this.$store.state.shopId
			this.StoreMsg(shopid,function(el){
				_this.msg=el
			});
		},
		methods:{
		},
		watch:{
			
		},
		

	}
	export default shopGuide
</script>
<style scoped>
	#shopGuide{
		width: 100%;
		height: 0.5rem;
	}
	.el-menu-demo{
		height:0.5rem;
		display: flex;
		justify-content:space-around;
	}
	.el-menu-item{
		font-size: 0.17rem;
		width:0.4rem;
		height: 0.38rem !important;
		line-height: 0.4rem !important;
		padding: 0 !important;
	}
	#shopGuide a{
		margin-top: -0.05rem;
		font-size: 0.17rem;
		width:0.4rem;
		height: 0.38rem;
		line-height: 0.4rem;
		padding: 0 !important;
	}
</style>


