<template>
	<div >
		<!--定位城市-->
		<div class="citylist_position">
			<div style="border-bottom: 1px solid rgb(229,229,229) ;">
				<span style="font-size: 0.15rem;margin-left: 0.1rem;">当前定位城市:</span>
				<span style="float: right;font-size: 0.1rem;margin-right: 0.1rem;">定位不准时,请在城市列表中选择</span>
			</div>
			
			<router-link :to="'/cutcity/'+positions.id" id="citylist_router">
				<div >
					<span style="margin-left: 0.1rem;">{{positions.name}}</span>    
					<i class="el-icon-arrow-right"></i>
				</div>
			</router-link>
		</div>
	</div>
</template>

<script>

	import { Loading } from 'element-ui';
	var Positionscity = {
		name:"positionscity",
		data() {
			return {
				positions: []
			}
		},
		created(){
			var loadingInstance = Loading.service({ fullscreen: true });
			var _this = this
			//定位城市
			this.positionCity(function(el) {
				_this.positions = el
				var cityMsg={
					id:el.id,
					latitude:el.latitude,
					longitude:el.longitude
				}
				_this.$store.commit("cityMsg",cityMsg)  
				loadingInstance.close();

			})
		}
	}
	export default Positionscity
</script>

<style scoped>
/*定位城市*/
	.citylist_position{
		height: 0.8rem;
	}
	.citylist_position>div{
		height: 0.4rem;
		line-height: 0.4rem;
	}
	.el-icon-arrow-right{
		float: right;
		line-height: 0.4rem;
		margin-right: 0.1rem;
	}
	#citylist_router{
		line-height: 0.4rem;
		color: dodgerblue;
	}
</style>


