<template>
  <div class="content">
    <div class="content-nav">
        <img @click='prev' src="../img/left.png">
        <span class="content-span">{{title}}</span>
      </div>
      <div class="content-div">
        <ul>
          <li v-for="val in data">
            <p v-for="value in val">{{value}}</p>
          </li>
        </ul>
      </div>
  </div>
</template>

<script>
export default {
  name: 'serverContents',
  data(){
    return{
      title:"",
      data: [],
      datas:[],
      nums:0
    }
  },
  methods: {
    prev() {
      this.$router.go(-1);
    }
  },
  created() {
   this.title=this.$route.params.title;
   this.tapo = this.$route.params.tapo;

    let api = "https://elm.cangdu.org/v3/profile/explain";
    this.$http.get(api).then(data => {
        this.data = data.data;
        for(let k in this.data){
          this.datas.push(this.data[k]);
        }
        this.datas.splice(24,1);
        let data1 = [];
        for(let i=0; i <= this.datas.length; i++){
            if(i % 2 == 0){
              data1.push(this.datas[i]);
            }
        }
        data1.splice(9,2);
        data1.splice(21,1);
        this.data = data1;
        this.nums+=1
    });
  },
  watch:{
    nums(news){
      var son =this.data[this.tapo].split("###")
      son.shift()
      this.data = []
      for (var i = 0; i < son.length; i++) {
        var dum = son[i].split("*")
        this.data[i]=(dum)
      };
      console.log(this.data)

    }
  }
};
</script>

<style scoped>
.content {
  background: white;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.content-nav {
  width: 100%;
  height: 0.5rem;
  font-size: 0.22rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
}
.content-nav > img {
  height: 0.26rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 0.15rem;
  transform: translateY(-50%);
}
.content-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.54rem;
}
.content-div{
  margin-top: 0.57rem;
  padding: 0.14rem;
  padding-top: 0rem;
  font-size: 0.24rem;
  text-align: left;
}
.content-div ul{
  padding-left: 0.3rem;
}
.content-div ul li{
  list-style:decimal;
}
.content-div ul li p{
  text-indent: 0.3rem;
}
.content-div ul li p:nth-child(1){
  text-indent: 0rem;
  margin: 0.05rem 0;
}
</style>



