<template>
	<div id="clubCenter_wrap" class='clubCenter_wrap'>
		<!--导航条-->
		<div class="clubCenter_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p>会员中心</p>
		</div>
		<div class="clubCenter_club">
			<p v-if="isid==''">为账户 <router-link to="/login" style="color: black;">
				<span style="font-weight: bolder;">{{title}}</span>
				</router-link>
				 购买会员</p>
			<p v-else>为账户<span style="font-weight: bolder;">{{title}}</span>购买会员</p>
				 
		</div>
		
		<div class="clubCenter_prer">
			<router-link to="/clubcenter/clubstate">
				<div class="clubCenter_tequan">
					<span>会员特权</span>
					<div>
						<span style="color: rgb(180,180,180);">会员说明</span>
						<i class="el-icon-arrow-right"></i>
					</div>
				</div>
			</router-link>
			<div class="clubCenter_jianmian">
				<img src="@/../static/imgs/buit15.png" alt=""/>
				<p>减免配送费</p>
				<p >每月减免30单,每日可减免3单,每单最高减4元</p>
				<p style="font-size: 0.138rem;">蜂鸟专送专享</p>
			</div>
			<div class="clubCenter_jianmian">
				<img src="@/../static/imgs/buit15.png" alt=""/>
				<p>减免配送费</p>
				<p>每月减免30单,每日可减免3单,每单最高减4元</p>
				<p>蜂鸟专送专享</p>
			</div>
		</div>
		<div class="clubCenter_dred">
			<p>开通会员</p>
			<div>
				<span>1个月</span>
				<span>¥20</span>
				<router-link to="/clubcenter/clubbuy">
					<span>购买</span>
				</router-link>
			</div>
		</div>
		<router-link to="/clubcenter/clubpass">
			<div class="clubCenter_tequan clubCenter_duihuan">
				<span>兑换会员</span>
				<div>
					<span style="color: rgb(180,180,180);">使用卡号卡密</span>
					<i class="el-icon-arrow-right"></i>
				</div>
			</div>
		</router-link>
		<router-link to="/clubcenter/clubinvoice">
			<div class="clubCenter_tequan clubCenter_duihuan">
				<span >购买记录</span>
				<div>
					<span style="color: rgb(180,180,180);">开发票</span>
					<i class="el-icon-arrow-right"></i>
				</div>
			</div>
		</router-link>
	</div>
</template>

<script>
	var ClubCenter = {
		name:"clubCenter",
		data(){
			return {
				title:"",
				isid:""
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		created(){
			this.title = this.$store.state.personLocal[0].title
			this.isid = this.$store.state.personLocal[0].id
			
		}
	}
	export default ClubCenter
</script>

<style scoped>
	#clubCenter_wrap{
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgb(245, 245, 245);
		width: 100%;
		overflow: hidden;
		text-align: left;
		overflow-y:scroll;
	}
	#clubCenter_wrap::-webkit-scrollbar {display:none}
	.clubCenter_wrap{
		font-size: 0.14rem;
	}
	.clubCenter_nav{
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 3;
	}
	.clubCenter_nav>i{
		float: left;
		line-height: 0.5rem;
		margin: 0.03rem;
	}
	.clubCenter_nav>p,.clubCenter_nav>i{
		color: white;
	}
	.clubCenter_club{
		text-align: left;
		box-sizing: border-box;
		padding: 0.1rem;
		height: 0.5rem;
		line-height: 0.3rem;
		margin-top: 0.5rem;

	}
	.clubCenter_prer{
		background: white;
		padding: 0.1rem 0 0.1rem 0.1rem;
		box-sizing: border-box;
	}
	/*会员特权*/
	.clubCenter_tequan{
		height: 0.4rem;
		line-height: 0.3rem;
		box-sizing: border-box;
		background: white;
	}
	.clubCenter_tequan>div{
		float: right;
	}
	/*减免配送*/
	.clubCenter_jianmian{
		/*height: 0.7rem;*/
		padding: 0.1rem 0;
		border-top: 1px solid rgb(220,220,220);
	}
	.clubCenter_jianmian>p:nth-child(2){
		font-size: 0.18rem;
		margin-top: 0.1rem;
	}
	.clubCenter_jianmian>p:nth-child(3){
		font-size: 0.138rem;
		line-height: 0.2rem;
	}
	/*开通会员*/
	.clubCenter_dred{
		background: white;
		/*height: 0.8rem;*/
		padding: 0.1rem 0 0 0.1rem;
		box-sizing: border-box;
		font-size: 0.18rem;
		margin-top: 0.1rem;
	}
	.clubCenter_dred>p{
		height: 0.4rem;
		line-height: 0.4rem;
		border-bottom: 1px solid rgb(220,220,220);
		
	}
	.clubCenter_dred>div{
		height: 0.6rem;
		line-height: 0.6rem;
	}
	
	.clubCenter_dred>div>span:nth-child(2){
		color: red;
	}
	.clubCenter_dred>div>a>span{
		box-sizing: border-box;
		margin-top: 0.15rem;
		margin-right: 0.1rem;
		border: 1px solid red;
		padding: 0 0.1rem;
		color: red;
		border-radius: 10%;
		float: right;
		height:0.3rem;
		line-height: 0.3rem;
	}
	.clubCenter_duihuan{
		margin-top: 0.1rem;
		padding: 0.05rem 0.1rem;
		box-sizing: border-box;
	}
	img{
		float: left;
		width: 0.5rem;
		margin: 0.1rem 0.2rem;
	}
</style>


