<template>
	<div id="wodeyouhui_wrap">
		<div class="wodeyouhui_wrap_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p>密码登录</p>
		</div>
		<!--红包-->
		<div class="wodeyouhui_center">
			<Wodeyouhuisa></Wodeyouhuisa>
		</div>


	</div>
</template>

<script>
	import Wodeyouhuisa from '@/components/myJU/wodeyouhuis/wodeyouhuisa'
	var Wodeyouhui = {
		name: "wodeyouhui",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		components:{
			Wodeyouhuisa
		}
	}
	export default Wodeyouhui
</script>

<style scoped>
	#wodeyouhui_wrap{
		/*position: absolute;
		left: 0;
		top: 0;
		bottom: 0;*/
		background: rgb(245, 245, 245);
		width: 100%;
		overflow: hidden;
	}
	.wodeyouhui_wrap_nav{
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		position: fixed;
		left: 0;top: 0;
	}
	.wodeyouhui_wrap_nav>i{
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	.wodeyouhui_center{
		margin-top: 0.5rem;
	}
	p,i{
		color: white;
	}
</style>


