<template>
	<div id="logined_wrap">
		<!--导航条-->
		<div id="logined_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p>账户信息</p>
		</div>
		<!--头像 用户名 收货地址-->
		<div class="logined_center">
			<ul>
				<li style="position: relative;">
					<span style="line-height: 0.7rem;">头像</span>
					<i class="el-icon-arrow-right" style="line-height: 0.7rem;"></i>
					<el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
						<img v-if="imageUrl" :src="imageUrl" class="avatar touxiang" @change="Preview($event)">
						<i v-else class="el-icon-plus avatar-uploader-icon">上传头像</i>
					</el-upload>
				</li>
				<router-link :to="{name:'userinfo'}">
					<li>
						<span>临时用户名</span>
						<i class="el-icon-arrow-right"></i>
						<span style="float: right;color: lightgray;">{{msg}}</span>
					</li>
				</router-link>

				<router-link to="/logined/profile">
					<li>
						<span>收货地址</span>
					</li>
				</router-link>

				<li style="background: rgb(245,245,245);font-size: 0.138rem;">账号绑定</li>
				<li @click="phone()" class="logined_ul_li">
					<img src="@/../static/imgs/商品.png" alt=""/>
					<span style="margin-left: 0.5rem;">手机</span>
					<i class="el-icon-arrow-right"></i>
				</li>

				<li style="background: rgb(245,245,245);font-size: 0.138rem;">安全设置</li>

				<router-link to="/resetpass">
					<li>
						<span>登录密码</span>
						<i class="el-icon-arrow-right "></i>
						<span style="float: right; color: lightgray;">修改</span>
					</li>
				</router-link>
			</ul>
		</div>
		<!--退出登录-->
		<div class="logined_wrap_out">
			<router-link to="">
				<el-button type="danger" @click="centerDialogVisible = true">退出登录</el-button>
			</router-link>
			<el-dialog title="提示" :visible.sync="centerDialogVisible" width="90%">
				<span style="font-size: 0.3rem;">确定退出登录吗</span>
				<span slot="footer" class="dialog-footer">
					<el-button @click="centerDialogVisible = false">再等等</el-button>
					<el-button type="primary" @click="returnMui()">确定退出</el-button>
				</span>
			</el-dialog>

		</div>

	</div>
</template>

<script>
	var Logined = {
			name: "logined",
			data() {
				return {
					msg: '',
					centerDialogVisible: false,
					imageUrl: '',
					status: "",
					image_path: ""

				}
			},
			methods: {
				sear() {
					this.$router.go(-1)
				},
				
				phone() {
					alert("请在手机APP中设置")
				},
				//头像上传
				handleAvatarSuccess(res, file) {
					this.imageUrl = URL.createObjectURL(file.raw);
					console.log(this.imageUrl)
				},
				beforeAvatarUpload(file) {
					const isJPG = file.type === 'image/jpeg';
					const isLt2M = file.size / 1024 / 1024 < 2;

					if(!isJPG) {
						this.$message.error('上传头像图片只能是 JPG 格式!');
					}
					if(!isLt2M) {
						this.$message.error('上传头像图片大小不能超过 2MB!');
					}
					return isJPG && isLt2M;
				},
				returnMui(){
					this.$router.push({name:"mui"});
					this.centerDialogVisible = false;
					this.$store.commit("removeAllHist","personLocal")
				}
				
			},
			watch:{
				imageUrl:function(newValue,oldValue){
					console.log('Imag变化了')
					console.log(newValue+'------'+oldValue)
					this.imageUrl = newValue
//					this.$store.commit("getLocal", "personLocal")
					this.imageUrl = this.$store.state.personLocal[0].avatar
				},
				
			},
		created() {
			if(localStorage.getItem("personLocal") != null) {
				this.$store.commit("getLocal", "personLocal")
			}
			this.msg = this.$store.state.personLocal[0].title
//			console.log(this.imageUrl)
		},
	}

	export default Logined
</script>

<style scoped>
	.el-icon-plus{
		font-size: 0.1rem;	
	}
	.logined_center {
		margin-top: 0.1rem;
	}
	
	.el-icon-arrow-left {
		color: white;
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	
	.el-icon-arrow-right {
		float: right;
		line-height: 0.5rem;
	}
	#logined_wrap {
		width: 100%;
		background: rgb(245, 245, 245);
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		text-align: left;
		font-size: 0.17rem;
		line-height: 0.5rem;
	}
	
	#logined_nav {
		width: 100%;
		background: #3190E8;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
	}
	
	p {
		color: white;
	}
	/*手机*/
	.logined_ul_li{
		height: 0.7rem;
		line-height: 0.7rem;
		position: relative;
	}
	.logined_ul_li>img{
		width: 0.3rem;
		position: absolute;
		left: 0.1rem;
		top: 50%;
		transform: translateY(-50%);
	}
	.logined_ul_li>i{
		position: absolute;
		right: 0.1rem;
		top: 50%;
		transform: translateY(-50%);
	}
	/*收货 头像用户名*/
	/*头像*/
	.avatar-uploader {
		width: 20%;
		float: right;
		height: 0.7rem;
		line-height: 0.7rem;
		text-align: center;
	}
	.touxiang {
		width: 0.5rem;
		margin: 0.1rem 0;
		border: 0.05px solid rgb(220, 220, 220);
		/*float: right;*/
		border-radius: 50%;
		overflow: hidden;
		/*position: absolute;
		top: 0;right: 0.4rem;*/
	}
	
	/*退出登录*/
	
	.logined_wrap_out {
		text-align: center;
		margin-top: 0.2rem;
	}
	
	.logined_wrap_out>a>button {
		width: 94%;
	}
	
	ul li {
		background: white;
		border-bottom: 2px solid rgb(245, 245, 245);
		padding: 0 0.05rem;
		overflow: hidden;
		box-sizing: border-box;
	}
	
	a {
		color: black;
	}
</style>


