<template>
	<div id="shopTitle">
		<div id="gohistory">
			<home_gohistory :gogogo="gogogo"></home_gohistory>
		</div>
		<div id="header">
			<img :src="'https://elm.cangdu.org/img/'+msg.image_path" alt="图片" class="home_AS_Oli_ECImg" id="shop_st_left">
		</div>
		<img :src="'https://elm.cangdu.org/img/'+msg.image_path" alt="图片" class="home_AS_Oli_ECImg" id="shop_st_left">
		<div id="shop_st_Right">
			<p class="shop_st_Right_p1">{{msg.name}}</p>
			<p v-for="(val,ind) in msg.supports" class="shop_st_Right_p2">
				<span v-if="val.name=='外卖保'">商家配送</span>
				<span v-if="val.name=='准时达'"><span class="shop_st_Right_p2_span_bbt">/</span>分钟送达</span>
				<span v-if="val.name=='开发票'"><span class="shop_st_Right_p2_span_bbt">/</span>{{msg.piecewise_agent_fee.tips}}</span>
			</p>
			<p class="shop_st_Right_p3" >
				<router-link :to="{name:'shopLastMsg'}">
				公告<span>:</span>{{msg.promotion_info}}
				</router-link>
			</p>
		</div>
		<div id="clr"></div>
		<div v-for="(val,ind) in msg.activities" v-if="msg.activities" id="shop_st_bottom">
			<div v-if="val.icon_name=='减'" class="shop_st_bottom_span1">{{val.icon_name}}</div>
			<div v-if="val.icon_name=='新'" class="shop_st_bottom_span5">
				<a @click="blockActive()">{{val.icon_name}}</a>
			</div>
			<span v-if="val.description=='满30减5，满60减8'" class="shop_st_bottom_span2">
				<a @click="blockActive()">{{val.description}}</a>
			</span>
			<span v-if="val.icon_name=='减'" class="shop_st_bottom_span3">( App专项 )</span>
			<div class="shop_st_bottom_span4">
				<a @click="blockActive()">	
				一个活动 <span>></span>
				</a>
			</div>
		</div>
		<div id="shop_goLastMsg">
			<router-link :to="{name:'shopLastMsg'}">
				<i class="el-icon-arrow-right"></i>
			</router-link>
		</div>
		
	</div>
</template>
<script>
	import home_gohistory from "@/components/publicMoudle/gohistory"
	var shopTitle = {
		name:"shopTitle",
		data(){
			return {
				msg:{},
				gogogo:""
			}
		},
		created(){
			var _this=this
			var shopid= this.$store.state.shopId
			this.StoreMsg(shopid,function(el){
				_this.msg=el
			});
			this.gogogo="searchIf"
		},
		components:{
			home_gohistory,
		},
		methods:{
			blockActive(){
				this.isBlock()
			}
		},
		props:["isBlock"],
		watch:{
			
		},
		

	}
	export default shopTitle
</script>
<style scoped>
	#clr{
		clear:both;
	}
	
	#shop_goLastMsg{
		position: absolute;
		right: 0.06rem;
		top: 0.46rem;
		z-index: 10;
	}
	#shop_goLastMsg i{
		color: #fff;
		font-size: 0.16rem;
	}
	#shopTitle{
		height:100%;
		width: 3.55rem;
		position: relative;
		padding: 0.1rem;
		overflow: scroll;
	}
	#shopTitle::-webkit-scrollbar {display:none}
	#gohistory{
		position: absolute;
		top: -0.08rem;
		left: -0.06rem;
		z-index: 10;
	}

	#header{
		position:fixed;
		top: -0.1rem;
		left: -0.1rem;
		width: 3.95rem;
		height: 1.2rem;
		overflow: hidden;
		background: #333;
		z-index: -1;
	}
	#header img{
		opacity: 0.5;
		width: 3.75rem;
		height:3.75rem;
	}
	#shop_st_left{
		position: absolute;
		left: 0.1rem;
		top: 0.1rem;
		width: 0.6rem;
		overflow: scroll;
	}

	#shop_st_left::-webkit-scrollbar {display:none}
	#shop_st_Right{
		margin-left: 0.7rem;
		text-align: left;
		height:0.7rem;
		overflow: scroll;
	}
	#shop_st_Right::-webkit-scrollbar {display:none}
	.shop_st_Right_p1{
		font-size: 0.18rem;
		font-weight:bold;
		height: 0.2rem;
		color:#fff;
		line-height: 0.2rem;
	}
	.shop_st_Right_p2{
		margin-top: 0.05rem;

		font-size: 0.14rem;
		height: 0.2rem;
		color:#fff;
		line-height: 0.2rem;
		display: inline-block;
	}
	.shop_st_Right_p2_span_bbt{
		margin-left:0.05rem;
		margin-right:0.05rem;
	}
	.shop_st_Right_p3{
		margin-top: 0.05rem;
		height: 0.16rem;
		line-height: 0.16rem;
		width: 90%;
		text-align: left;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
	}
	.shop_st_Right_p3 a{
		font-size: 0.14rem;
		color:#fff;
		line-height: 0.16rem;
	}
	.shop_st_Right_p3 span{
		margin-left:0.03rem;
		margin-right:0.03rem;
	}
	#shop_st_bottom{
		margin-top: 0.01rem;
		position: relative;
		text-align: left;
		line-height: 0.2rem;
		font-size: 0.13rem;
		color:#fff;
	}
	.shop_st_bottom_span1{
		display: inline-block;
		padding-left: 0.01rem;
		padding-right: 0.01rem;
		font-size: 0.12rem;
		height: 0.16rem;
		color:#fff;
		background: rgb(240, 115, 115);
		border:1px solid rgb(240, 115, 115);
	}
	.shop_st_bottom_span5{
		display: inline-block;
		padding-left: 0.01rem;
		padding-right: 0.01rem;
		font-size: 0.12rem;
		height: 0.16rem;
		color:#fff;
		background: rgb(112, 188, 70);
		border:1px solid rgb(112, 188, 70);
	}
	.shop_st_bottom_span2{
	}
	.shop_st_bottom_span3{
	}
	.shop_st_bottom_span4{
		position: absolute;
		right: 0;
		top: 0;
	}
	.shop_st_bottom_span2 a{
		line-height: 0.2rem;
		font-size: 0.13rem;
		color:#fff;
	}
	.shop_st_bottom_span4 a{
		line-height: 0.2rem;
		font-size: 0.13rem;
		color:#fff;
	}
</style>


