<template>
	<div id="login_wrap">
		<div id="hometop_to">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p>密码登录</p>
		</div>
		<div id="login_pass">
			<input type="text" placeholder="账号" v-model="username" />
			<input v-if="value1" type="password" placeholder="密码" v-model="password" />
			<input v-else type="text" placeholder="密码" v-model="password" />
			<el-switch v-model="value1" active-color="#13ce66" class="login_pass_el_switch"></el-switch>
			<input type="text" placeholder="验证码" v-model="codeNumer" />
			<div class="login_pass_yanzheng">
				<div>
					<img :src="code&&code" alt="" class="yanzheng_img" />
				</div>
				<div @click="kanbuqing()">
					<label for="huanyizhang" style="font-size: 0.138rem;">看不清</label>
					<p style="font-size: 0.138rem; color: dodgerblue;" >换一张</p>
				</div>
			</div>
		</div>
		<p class="login_pss_p">温馨提示:未注册过的账号,登录时将自动注册</p>
		<p class="login_pss_p">注册过的用户可凭账号密码登录</p>
		<el-row>
		 <el-button type="success" @click="up(username)" class="login_wrap_but">登录</el-button>
		 </el-row>
		 
		 <router-link to="/resetpass" style="float: right;margin: 0.3rem 0.15rem 0 0;">重置密码?</router-link>
	</div>
</template>
<script>
	import Vue from 'vue'
	var Homelogin = {
		name: "homelogin",
		data(){
			return {
				value1: true,
				code:"",
				username:"",
				password:"",
				id:"",
				codeNumer:"",
				title:"",
				imageUrl:""
			}
		},
		methods:{
			sear(){
				this.$router.push({name:"home"});
			},
			kanbuqing(){
				var _this = this
				var api_login = "https://elm.cangdu.org/v1/captchas";
				this.$http({
					method:'post',
					url:api_login,
					withCredentials: true,
				}).then(function(el){
					if(el.data.status == 200){}
          				_this.code = el.data.code
				})
			},
			up(el){
				var _this = this
				var api_up ="https://elm.cangdu.org/v2/login";
				this.$http({
					method:"post",
					url:api_up,
					withCredentials: true,
					data:{
						username:this.username,
						password:this.password,
						captcha_code:this.codeNumer,
						user_id:this.id
					}
				}).then(function(data){
					console.log(".....................")
					console.log(data)
					console.log(".....................")
					if(data.data.status == 0){
						alert(data.data.message)
						_this.kanbuqing();
						_this.username="";
						_this.password="";
						_this.codeNumer="";
					}else{
						alert("登录成功")
						_this.id = data.data.user_id
						_this.imageUrl = data.data.avatar
						
						//上传到公共属性 2 
						var obj = {
							name:"personLocal",
							str:{
								title:el,
								id:_this.id,
								avatar:_this.imageUrl
							}
						}
						_this.$store.commit("localHist",obj)
						_this.$router.push({name:"mui"});
					}
				});
			}
		},
		created(){
			// this.kanbuqing();
			var url = "https://elm.cangdu.org/v1/captchas"
			var _this=this
			this.$http({
					method:"post",
					url:url,
					withCredentials: true,
				}).then(function(val){
					console.log(val)
					if (val.status == 200) {
						_this.code=val.data.code
					}
			})
		}
	}
	export default Homelogin
</script>
<style scoped>
	#login_wrap {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgb(245, 245, 245);
		width: 100%;
		overflow: hidden;
	}
	
	#login_pass {
		margin-top: 0.15rem;
		position: relative;
	}
	/*开启密码查看按钮*/
	.login_pass_el_switch{
		position: absolute;
		right: 0.15rem;
		top:50%;
		transform: translateY(-50%)
	}
	
	#login_pass>input {
		width: 100%;
		height: 0.45rem;
		font-size: 0.15rem;
		padding-left: 0.15rem;
		border-bottom: 0.1px solid rgb(245, 245, 245);
	}
	/*验证码*/
	.login_pass_yanzheng{
		width: 0.5rem;
		position: absolute;
		right: 0.15rem;
		bottom:0.225rem;
		transform: translateY(50%)
	}
	/*验证图片*/
	.yanzheng_img{
		position: absolute;
		right: 0.6rem;
		bottom:0.15rem;
		transform: translateY(50%)
	}
	/*温馨提示*/
	.login_pss_p{
		color: red;
		font-size: 0.1rem;
		margin: 0.2rem;
		text-align: left;
	}
	#hometop_to {
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
	}
	/*提交按钮*/
	.login_wrap_but{
		width: 90%;
		height: 0.4rem;
		margin: 0 auto;
	}
	p,
	i {
		color: white;
	}
	
	i {
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
</style>


