<template>
	<div id="wodeyouhui_wrap">
		<div v-model="tabView" class="wodeyouhuisa_nav">
			<div  class="wodeyouhui_wrap_span">
				<span  @click="toSim('Hongbao')">红包</span>
			</div>
			<div  class="wodeyouhui_wrap_span">
				<span @click="toSim('Daijinquan')">商家代金券</span>
			</div>
		</div>
			<component :is="tabView"></component>
	</div>
</template>

<script>
	import Hongbao from "./hongbao"
	import Daijinquan from "./daijinquan"

	var Wodeyouhui = {
		name: "wodeyouhui",
		data() {
			return {
				tabView: "Hongbao"
			}
		},
		components: {
			Hongbao,
			Daijinquan
		},
		methods: {
			toSim(index) {
				this.tabView = index;
				

			}
		},

	}
	export default Wodeyouhui
</script>

<style scoped>
	.wodeyouhui_wrap{
		overflow: hidden;
	}
	.wodeyouhui_wrap_span{
		width: 50%;
		float: left;
		height: 0.4rem;
		line-height: 0.4rem;
		font-size: 0.18rem;
		background: white;
		/*border: 1px solid red;*/ 
		box-sizing: border-box;
	}
	.wodeyouhui_wrap_span>span:hover{
		border-bottom: 2px solid #3190E8;
		color: #3190E8;
	}
</style>


