<template>
	<div id="searchIfBody">
		<div id="searchIfTop">
			<searchIf_navs></searchIf_navs>
		</div>
		<div id="searchIfChoose">
			<!-- 这个传输方法 -->
			<searchIf_guideFood :cd="cdn" :kd="kdn" :od="odn"></searchIf_guideFood>
		</div>
		<div id="searchIfCenter">
			<!-- <router-view></router-view> -->
			<!-- 让周围这个监听内容 -->
			<searchIf_AroundStore :km="cbn" :kd="kbn" :od="obn"></searchIf_AroundStore>
		</div>
	</div>
</template>
<script>
	import Vue from 'vue';
	import searchIf_navs from "@/components/searchIf/navs"
	import searchIf_guideFood from "@/components/searchIf/guideFood"
	import searchIf_AroundStore from "@/components/home/AroundStore"
	var searchIf={
		name:'searchIf',
		components:{
			searchIf_navs,
			searchIf_guideFood,
			searchIf_AroundStore
		},
		data(){
			return {
				cbn:0,
				kbn:0,
				obn:0
			}
		},
		methods:{
			cdn(){
				this.cbn+=1
			},
			kdn(){
				this.kbn+=1
			},
			odn(){
				this.obn+=1
			}
		}
	}
	export default searchIf
</script>
<style scoped>
	#searchIfBody{
		background: #fff;
		position: relative;
		overflow-y: scroll;
	}
	#searchIfBody::-webkit-scrollbar {display:none}
	#searchIfTop{
		width:100%;
		height: 0.45rem;
		background: #3190e8;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 3;
	}
	#searchIfChoose{
		width:100%;
		height:0.32rem;
		border-bottom: 0.01rem solid #f1f1f1;
		position: fixed;
		top:0.45rem;
		left: 0;
		z-index:3;
		background-image: #fff;
	}
	#searchIfCenter{
		width: 100%;
		margin-top: 0.9rem;
	}
</style>


