<template>
	<div id="daijinquansm_wrap">
		<div class="daijinquansm_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;">代金券说明</p>
		</div>
		<!--内容-->
		<div class="daijinquansm_center">
			<h4>Q1: 什么是商家代金券？</h4>
			<p>商家代金券是指由商家自己发放代金券，只限在指定的商家使用，可根据条件抵扣相应金额。</p>
			<h4>Q2: 怎么获得商家代金券？</h4>
			<p>进入有「进店领券」或「下单返券」标示的商家即有机会获得代金券。</p>	
			<p>「下单返券」需要在指定商家完成满足返券金额要求的订单后才会返还，代金券可在下次下单时使用。</p>
		</div>
	</div>
</template>

<script>
	var DaijinquanSm={
		name:"daijinquanSm",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		}
	}
	export default DaijinquanSm
</script>

<style scoped>
	.daijinquansm_nav{
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		position: fixed;
		left: 0;top: 0;
	}
	.daijinquansm_nav>i{
		color: white;
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	.daijinquansm_center{
		margin-top: 0.5rem;
		text-align: left;
		padding: 0.1rem;
		box-sizing: border-box;
	}
	.daijinquansm_center>h4{
		font-size: 0.18rem;
		line-height: 0.5rem;
	}
	.daijinquansm_center>p{
		font-size: 0.15rem;
		line-height: 0.2rem;
	}
</style>


