<template>
	<div>
	<div id="hongbao_wrap">
			<div class="hongbao_float">
				<router-link to="/benef/hongbaosm">
					<i class="el-icon-question"></i>
					<span>红包说明</span>
				</router-link>
			</div>
			
			<!--红包-->
			<ul>
				<li v-for="(datasa,index) in datas" :key="index">
					<div class="hongbao_li">
						<div>
							<!--抵用券钱数-->
							<p><span style="font-size: 0.18rem;">¥</span>{{datasa.amount}}
							</p>
							<!--抵用券使用条件-->
							<p style="color: rgb(160,160,160);font-size: 0.138rem;">{{datasa.description_map
	.sum_condition}}</p>
						</div>
						<div>
							<!--红包名字-->
							<p style="font-size: 0.18rem;">{{datasa.name}}</p>
							<!--到期时间-->
							<p style="font-size: 0.138rem;color: rgb(160,160,160)">{{datasa.description_map
	.validity_periods}}</p>
							<!--收货手机号-->
							<p style="font-size: 0.138rem;color: rgb(160,160,160)">{{datasa.description_map
	.phone}}</p>
						</div>
						<!--剩余日期-->
						<span style="float: right;color: red;line-height: 0.3rem;">{{datasa.description_map
	.validity_delta}}</span>
					</div>
				</li>
			</ul>
			
			<div style="padding: 0.1rem;box-sizing: border-box;font-size: 0.15rem;background: rgb(220,220,220);">
				<p>限品类:快餐便当,特色菜系,小吃夜宵,甜品饮品,异国料理</p>
			</div>
			<div class="history_hongbao">
				<router-link to="/benef/historyHb">
					<p>查看历史红包></p>
				</router-link>
			</div>
	</div>
	<div class="hongbao_bottom">
			<router-link to="/benef/exchange"><span>兑换红包</span></router-link>
			<router-link to="/benef/commend"><span>推荐有奖</span></router-link>
	</div>
	</div>
</template>

<script>
	import { Loading } from 'element-ui';
	
	var Hongbao = {
		name:"hongbao",
		data(){
			return {
				count:"",
				datas:[],
				cmount:""
			}
		},
		created(){
			let loaddingInstance1 = Loading.service({
				fullscreen: true
			});
			var _this = this
			var id = +this.$store.state.personLocal[0].id
			console.log(id)
			var api_hongbao = "https://elm.cangdu.org/promotion/v2/users/"+id+"/hongbaos?limit=20&offset=0"
			this.axios.get(api_hongbao).then(function(el){
				_this.count = el.data.length
				_this.datas = el.data
				console.log(el)
				loaddingInstance1.close();
				_this.cmount = el.data.length
				_this.$store.commit("youhui",_this.cmount)
			})

			
		}
	}
	export default Hongbao
</script>

<style scoped>
	#hongbao_wrap{
		overflow: hidden;
		text-align: left;
		padding: 0 0.15rem;
		box-sizing: border-box;
	}
	/*红包到期*/
	#hongbao_wrap>p{
		height: 0.3rem;
		line-height: 0.3rem;
		font-size: 0.15rem;
	}
	.hongbao_float{
		float: right;
	}
	/*红包说明*/
	.hongbao_float>a{
		color: #3190E8;
	}
	.hongbao_float>a>span{
		display: inline-block;
		height: 0.4rem;
		line-height: 0.4rem;
		font-size: 0.15rem;
	}
	.hongbao_li{
		border-top: 3px dashed red;
		padding: 0.1rem;
		box-sizing: border-box;
		overflow: hidden;
		width: 100%;
		margin-top: 0.1rem;
		background: white;
	}
	/*左div*/
	.hongbao_li>div:nth-child(1){
		float: left;
		width: 30%;
		height: 1rem;
		text-align: center;
		border-right:1px solid rgb(200,200,200) ;

	}
	.hongbao_li>div:nth-child(1)>p:nth-child(1){
		margin: 0.2rem 0 0.1rem;
		color: red;
		font-size: 0.4rem;
	}
	
	/*右div*/
	.hongbao_li>div:nth-child(2){
		height: 1rem;
		float: left;
		width: 50%;
		padding: 0.1rem;
		box-sizing: border-box;
		margin-left: 0.1rem;
		text-overflow: ellipsis;
		white-space: nowrap;
		
	}
	.hongbao_li>div:nth-child(2)>p:nth-child(1){
		margin: 0.1rem 0;
		
	}
	.hongbao_li>div:nth-child(2)>p{
		line-height: 0.2rem;
	}
	/*历史红包*/
	.history_hongbao{
		text-align: center;
		height: 0.4rem;
		line-height: 0.4rem;
	}
	.history_hongbao>a>p{
		color: rgb(160,160,160);
	}
	/*兑换红包和推荐*/
	.hongbao_bottom{
		width: 100%;
		background: white;
		height: 0.4rem;
	}
	.hongbao_bottom>a{
		height: 0.4rem;
		line-height: 0.4rem;
		display: inline-block;
		width: 49%;
		text-align: center;
		box-sizing: border-box;
	}
	.hongbao_bottom>a>span{
		color: black;
		font-size:0.15rem ;
	}
</style>


