<template>
	<div id="hongbaosm_wrap">
		<!--导航-->
		<div class="hongbaosm_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;">红包说明</p>
		</div>
		<!--内容-->
		<div class="hongbaosm_center">
			<h4>Q1: 怎么获得红包？</h4>
			<p>通过邀请好友获得</p>
			<p>在下单返红包的商家下单获得</p>
			<p>抢微信朋友分享的红包获得</p>
			<h4>Q2: 红包可以做什么？</h4>
			<p>可以抵扣在线支付时的实际支付金额。</p>
			<h4>Q3: 红包的门槛金额按照什么金额计算？</h4>
			<p>红包的的门槛金额按照（选购菜品折后价＋餐盒费）的金额作为计算门槛，即购物车显示的金额（7.3以上版本）。假设红包满30可用，只要选购的菜品现价（折后价）＋餐盒费超过30，就可以使用红包。</p>
		</div>
		
	</div>
</template>

<script>
	var HongbaoSm={
		name:"hongbaoSm",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		}
	}
	export default HongbaoSm
</script>

<style scoped>
	.hongbaosm_nav{
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		position: fixed;
		left: 0;top: 0;
	}
	.hongbaosm_nav>i{
		color: white;
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	.hongbaosm_center{
		margin-top: 0.5rem;
		text-align: left;
		padding: 0.1rem;
		box-sizing: border-box;
	}
	.hongbaosm_center>h4{
		font-size: 0.18rem;
		line-height: 0.5rem;
	}
	.hongbaosm_center>p{
		font-size: 0.15rem;
		line-height: 0.2rem;
	}
</style>


