// mutations:转变,变化,  类似于methods 存放事件 
// mutations可以处理异步操作,而methods一般来说处理同步操作
export default{
	// 触发此函数的方法
		// store.commit("方法名","传递的参数")  
			// this.$store.commit("reduce",el)
		// 这边接受时 mutation("此state","形参")
	mutation(state,el){
		this.state.count+=el
	},
	// 获取想要查看的某个食品的信息
	foodMsg(state,el){
		this.state.foodMsg=el
	},
	// 获取商铺id
	shopIds(state,el){
		this.state.shopId=el
	},
	// 所有店铺信息
	AllShop(state,el){
		this.state.AllShop=el
	},
	AllStore(state,el){
		this.state.AllStore=el
	},
	// 导购栏选择信息
	guideTitle(state,el){
		console.log(this.state.guideTitleMsg)
		this.state.guideTitleMsg=el
	},
	// 获取城市基本信息
	cityMsg(state,el){
		console.log(el)
		this.state.cityMsg=el
	},
	// 获取搜索方式
	orderBy(state,el){
		console.log(el)
		this.state.orderBy=el
	},
	// 添加搜索信息
	selectMsg(state,el){
		var a=[]
		var b = this.state.selectMsg.map(function(val,ind){
			a=val.id
			return a
		})
		var c = b.indexOf(el.id)
		if(c==-1){
			this.state.selectMsg.push(el)
		}else{
			this.state.selectMsg.splice(c,1)
		}
		console.log(state.selectMsg)
	},
	// 删除所有搜索信息
	delAllSelectMsg(state){
		this.state.selectMsg=[]
	},
	// 添加分类方式
	goodSelectMsg(state,el){
		console.log(el)
		this.state.goodSelectMsg=el
	},

	// 购买订单信息
	playBuy(state,el){
		this.state.playBuy=el
	},
	//所有订单的信息
	AllOrderMsg(state,el){
		this.state.AllOrderMsg=el
	},
	// 删除后的订单详情
	flowOrderMsg(state,el){
		this.state.flowOrderMsg=el
	},
	// 食物信息中的+号
	priceAdd(state,el){
		var priceOl ={
			name:el.name,
			prise:el.prise,
			msg:el.msg,
			lens:1,
			id:el.id
		}
		var a=[]
		var b = this.state.playBuy.map(function(val,ind){
			a=val.id
			return a
		})
		var c = b.indexOf(priceOl.id)
		if(c==-1){
			this.state.playBuy.push(priceOl)
		}else{
			this.state.playBuy[c].lens+=1
		}
		var e = 0;
		this.state.playBuy.forEach(function(val,ind){
			e +=(val.prise*val.lens)
		})
		this.state.allPrise = e
	},
	// 食物信息中的-号
	priceDel(state,el){
		var a=[]
		var b = this.state.playBuy.map(function(val,ind){
			a=val.id
			return a
		})
		var c = b.indexOf(el)
		if(c==-1){
			this.state.playBuy = this.state.playBuy
		}else{
			this.state.playBuy[c].lens -= 1
			if (!this.state.playBuy[c].lens) {
				this.state.playBuy.splice(c,1)
			}
		}
		var e = 0;
		this.state.playBuy.forEach(function(val,ind){
			e +=(val.prise*val.lens)
		})
		this.state.allPrise = e
	},
	// 清空操作
	delPriceAll(){
		this.state.playBuy=[]
		this.state.allPrise=0
	},
	// 购物车中的+号
	addfrult(state,el){
		this.state.playBuy[el].lens +=1
		var e = 0;
		this.state.playBuy.forEach(function(val,ind){
			e +=(val.prise*val.lens)
		})
		this.state.allPrise = e
		
	},
	// 购物车中的-号
	delfrult(state,el){
		if(this.state.playBuy[el].lens){
			this.state.playBuy[el].lens -= 1
		}else{
			this.state.playBuy.splice(el,1)
		}
		var e = 0;
		this.state.playBuy.forEach(function(val,ind){
			e +=(val.prise*val.lens)
		})
		this.state.allPrise = e
	},
	AllBuyMsg(state,el){
		this.state.AllBuyMsg = el
	},
	// 开始本地缓存  需要本地缓存名称 和 缓存内容
	localHist(state,el){
		var a = el.name
		var b = el.str
		if(a == "searchLocalHistory"){
			var cont = localStorage.getItem("searchLocalHistory")
			this.state.searchLocalHistory=JSON.parse(cont)
			var mmn=this.state.searchLocalHistory.indexOf(b)
			if (mmn==-1) {
				this.state.searchLocalHistory=this.state.searchLocalHistory
			}else{
				this.state.searchLocalHistory.splice(mmn,1)
			}
			this.state.searchLocalHistory.push(b)
			var c = this.state.searchLocalHistory
			localStorage.setItem("searchLocalHistory",JSON.stringify(c))
		} 
		if(a == "cityLocalHistory") {
			var cont = localStorage.getItem("cityLocalHistory")
			this.state.cityLocalHistory=JSON.parse(cont)
			var mmn=this.state.cityLocalHistory.indexOf(b)
			if (mmn==-1) {
				this.state.cityLocalHistory=this.state.cityLocalHistory
			}else{
				this.state.cityLocalHistory.splice(mmn,1)
			}
			this.state.cityLocalHistory.push(b)
			var c = this.state.cityLocalHistory
			localStorage.setItem("cityLocalHistory",JSON.stringify(c))
		};
		if(a == "personLocal") {
			this.state.personLocal=[]
			this.state.personLocal.push(b)
			var c = this.state.personLocal
			localStorage.setItem("personLocal",JSON.stringify(c))
		};
	},
	// 获取数据 需要本地缓存名称
	getLocal(state,el){
		if(el == "searchLocalHistory"){
			var c = localStorage.getItem("searchLocalHistory")
			if (c) {
				this.state.searchLocalHistory=JSON.parse(c)
			}else{
				this.state.searchLocalHistory=this.state.searchLocalHistory
				localStorage.setItem("searchLocalHistory",JSON.stringify(this.state.searchLocalHistory))
			}
		} 
		if(el == "cityLocalHistory") {
			var c = localStorage.getItem("cityLocalHistory")
			this.state.cityLocalHistory=JSON.parse(c)
		};
		if(el == "personLocal") {
			var c = localStorage.getItem("personLocal")
			this.state.personLocal=JSON.parse(c)
			console.log(c)
		};
	},
	// 删除某一条记录 缓存名称 和 当前数据
	removeHist(state,el){
		var a = el.name
		var b = el.val
		if(a == "searchLocalHistory"){
			var ind = this.state.searchLocalHistory.indexOf(b)
			this.state.searchLocalHistory.splice(ind,1)
			var c = this.state.searchLocalHistory
			localStorage.setItem("searchLocalHistory",JSON.stringify(c))
		} 
		if(a == "cityLocalHistory") {
			var ind = this.state.cityLocalHistory.indexOf(b)
			this.state.cityLocalHistory.splice(ind,1)
			var c = this.state.cityLocalHistory
			localStorage.setItem("cityLocalHistory",JSON.stringify(c))
		};
		if(a == "personLocal") {
			var ind = this.state.personLocal.indexOf(b)
			this.state.personLocal.splice(ind,1)
			var c = this.state.personLocal
			localStorage.setItem("personLocal",JSON.stringify(c))
		};
	},
	// 删除所有历史记录  缓存名称
	removeAllHist(state,el){
		if(el == "searchLocalHistory"){
			this.state.searchLocalHistory=[]
			var c = this.state.searchLocalHistory
			localStorage.setItem("searchLocalHistory",JSON.stringify(c))
		} 
		if(el == "cityLocalHistory") {
			this.state.cityLocalHistory=[]
			var c = this.state.cityLocalHistory
			localStorage.setItem("cityLocalHistory",JSON.stringify(c))
		};
		if(el == "personLocal") {
			this.state.personLocal=[{title:"登录 / 注册",id:'',avatar:""}]
			var c = this.state.personLocal
			localStorage.setItem("personLocal",JSON.stringify(c))
		};
	},
	// 编写地址信息,并临时存储
	addressText(state,el){
		this.state.addressText = el
	},
	// 存储从后台下载下来的地址
	localAddress(state,el){
		this.state.localAddress = el
	},
	onceAddress(state,el){
		this.state.onceAddress = el
	},
	localText(state,el){
		this.state.localText = el
	},
	// 食品的备注信息
	orderOtherMsg(state,el){
		this.state.orderOtherMsg = el
	},
	// 是否开发票
	orderGivePath(state,el){
		this.state.orderGivePath = el
	},




	//-------------------王明凯-----------------------
	//1.把城市地址传入主页标题
	cityTitleMsg(state,el){
		this.state.cityTitleMsg=el
	},
	//2.把用户名传入个人中心
	muiTitle(state,el){
		this.state.muiTitle = el
	},
	//优惠红包
	youhui(state,el){
		this.state.youhuiHongbao = el
	},
	
}


