<template>
	<div id="jifenwenti_wrap">
		<!--导航-->
		<div class="jifenwenti_wrap_nav">
			<router-link to="">
				<i class="el-icon-arrow-left" @click="sear()"></i>
			</router-link>
			<p>积分说明</p>
		</div>
		<!--问题内容-->
		<div class="jifenwenti_wrap_center">
			<Jifenneirong></Jifenneirong>
		</div>
	</div>
</template>

<script>
	import Jifenneirong from './jifenneirong'
	
	var Jifenwenti = {
		name:"Jifenwenti",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		},
		components:{
			Jifenneirong
		}
	}
	export default Jifenwenti
</script>

<style scoped>
	
	.jifenwenti_wrap_nav {
		width: 100%;
		line-height: 0.5rem;
		text-align: center;
		background: #3190E8;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
	}
	p,i{
		color: white;
	}
	i {
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
</style>


