<template>
	<div id="clubstate_wrap">
		<!--导航-->
		<div class="clubstate_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;line-height: 0.5rem;">我的优惠</p>
		</div>
		<!--内容-->
		<div class="clubstate_center">
			<div>
				<p>尊敬的用户，随着会员体系逐渐完善，自2016年10月10日起，饿了么会员权益将做如下优化：
              购卡后31天内，累积享有30单减免配送费服务（每日最多3单，每单最高减免4元）。
              注：已购买的会员服务不受影响，当前会员服务失效前无法购买新卡。</p>
			</div>

		<h3>Q1:特权介绍</h3>
		<p>身份标识：饿了么会员服务有效期内，享有专属皇冠标识。</p>
		<p>减免配送费： 饿了么会员卡自绑定账户之日起31天内，在「蜂鸟专送」标识商家下单，享有30次减免配送费特权，每日最多减免3单，每单最高可减4元。</p>
		
		<h3>Q2:资费介绍</h3>
		<p>饿了么会员卡：20元</p>
		
		<h3>Q3:使用说明</h3>
		<p>当用户满足以下任一条件，会员服务自动失效：</p>
		<ul style="margin-left: 0.3rem;">
			<li>自绑定之日起超过31天；</li>
			<li>data-v-67b03784</li>
		</ul>
		</div>
	</div>
</template>

<script>
	var Clubstate = {
		name:"clubstate",
		methods:{
			sear(){
				this.$router.go(-1)
			}
		}
	}
	export default Clubstate
</script>

<style scoped>
	.clubstate_nav{
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
	}
	.clubstate_nav>i{
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
		color: white;
	}
	.clubstate_center{
		text-align: left;
		padding: 0.1rem;
		box-sizing: border-box;
	}
	.clubstate_center>div{
		width: 60%;
		margin: 0.2rem auto;

	}
	h3{
		line-height: 0.4rem;
		font-weight: bolder;
	}
	p,li{
		line-height: 0.2rem;
		
	}
</style>


