<template>
	<div id="searchBody">
		<nav>
			<Search_top></Search_top>
		</nav>
		<div id="searchCent">
			<Search_text :cli="butns" :clo="clearBtns" :cd="goBBT"></Search_text>
		</div>
		<div id="thirdCent">
			<Search_history :cli="count" :clo="cont" :cd="goBack"></Search_history>
		</div>
		<footer>
			<Search_navbottom></Search_navbottom>
		</footer>
	</div>
</template>
<script>
import Vue from 'vue'  ;
import { Loading } from 'element-ui';
import Search_top from "@/components/search/searchTop"
import Search_text from "@/components/search/searchText"
import Search_history from "@/components/search/searchHistory"
import Search_navbottom from "@/components/home/navBottom"
	var search = {
		name:"search",
		data(){
			return {
				store:[],
				count:{
					cont:0,
					value:""
				},
				cont:0,
				goBBT:{
					cont:0,
					value:""
				},
				fun:0
				
			}
		},
		components:{
		    Search_top,
		    Search_text,
		    Search_history,
		    Search_navbottom,
		},
		// 请求演示
		created(){
			this.fun = Loading.service({ fullscreen: true });
			var _this = this
			var west =31.22967;
			var nest = 121.4762;
			this.StoreMsg(5,function(el){
				_this.store=el
			})
		},
		mounted(){
			this.fun.close();
		},
		methods:{
			butns(el){
				var a = this.count.cont+1
				var obj={
					cont:a,
					value:el
				}
				this.count=obj
			},
			clearBtns(){
				this.cont+=1
			},
			goBack(el){
				var a = this.goBBT.cont+1
				var obj={
					cont:a,
					value:el
				}
				this.goBBT=obj
			}
		}
	}
	export default search

</script>
<style scoped>
	#searchBody{
		height: 100%;
		background-color: #f5f5f5;
		width: 100%;
	}
	nav{
		position:fixed;
		top: 0;
		left: 0;
		z-index: 5;
		height: 0.5rem;
		background: #3190e8;
		width: 100%;
	}
	#searchCent{
		width:100%;
		height: 0.58rem;
		padding-top:0.5rem; 
		background-color: #fff;
	}
	#thirdCent{
		margin-top: 0.1rem;
		background-color: #f5f5f5;
		min-height: 5rem;
	}
	footer{
		position: fixed;
		height:0.5rem;
		bottom: 0;
		z-index: 6;
		left: 0;
		width: 100%;
		background: #fff;
		overflow: hidden;
	}

</style>


