<template>
	<div id="orderOtherMsg">
        <div id="orderOtherMsg_top">
        	<!--搜索-->
        	<a @click="goBack()">  
		  		<i class="el-icon-arrow-left"></i>
		  	</a>
		  	<div class="orderOtherMsg_to_nav">
			  	<router-link to="/city">
			  		<span>{{msg}}</span>
			  	</router-link>
		  	</div>
		</div>
		<div id="orderOtherMsg_quickTrue">
			<p>快速备注</p>
			<ul>
				<li v-for="(val,ind) in beizhu" @click="Imsure(ind)" :class="{lisnnop:val.cli}">{{val.name}}</li>
			</ul>
		</div>
		<div id="orderOtherMsg_otherMsg">
			<p>其他备注</p>
			<textarea id="textarea" cols="47" rows="6" placeholder="请输入备注内容(可不填)" v-model="others"></textarea>
		</div>
		<div id="orderOtherMsg_sure" @click="orderOtherMsg_sure">确定</div>
    </div>
</template>
<script>
	var orderOtherMsg = {
		name:"orderOtherMsg",
		data(){
			return {
				msg:"订单备注",
				beizhu:[
				{name:"不要辣",id:0,cli:false},
				{name:"少点辣",id:1,cli:false},
				{name:"多点辣",id:2,cli:false},
				{name:"不要辣",id:3,cli:false},
				{name:"不要香菜",id:4,cli:false},
				{name:"不要洋葱",id:5,cli:false},
				{name:"多点醋",id:6,cli:false},
				{name:"多点葱",id:7,cli:false},
				{name:"去冰",id:8,cli:false},
				{name:"少冰",id:9,cli:false},
				],
				others:""
			}
		},
		methods:{
			// 返回历史
			goBack(){
				this.$router.go(-1)
			},
			Imsure(ind){
				this.beizhu[ind].cli=!this.beizhu[ind].cli
			},
			orderOtherMsg_sure(){
				var pushed=[]
				this.beizhu.forEach(function(val){
					if(val.cli){
						pushed.push(val.name)
					}
				})
				var orderOtherMsg_All=[{
					quick:pushed,
					other:this.others
				}]
				this.$store.commit("orderOtherMsg",orderOtherMsg_All)
				this.$router.go(-1)
			}
		},
		created(){
			
		},
		watch:{
			
		}
	}
	export default orderOtherMsg
</script>
<style scoped>
	#orderOtherMsg{
		position: relative;
		top:0;
		left: 0;
		width: 100%;
		background:#f5f5f5;
		overflow: hidden;
	}
	/*标题信息*/
	#orderOtherMsg_top{
		line-height: 0.5rem;
		text-align: center;
		width: 100%;
		height: 0.5rem;
		font-size: 0.18rem;
		background-color: #3190e8;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 5;
	}
	.orderOtherMsg_to_nav{
		width: 1.5rem;
		margin: auto;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#orderOtherMsg_top a,i{
    	color: white;
	}
	#orderOtherMsg_top i{
		float: left;
		line-height: 0.5rem;;
		margin-left: 0.03rem;
	}
	
	#orderOtherMsg_quickTrue{
		margin-top: 0.6rem;
		background-color: #fff;
		overflow: hidden;
		width: 100%;
		text-align: left;
		padding-bottom: 0.3rem;
	}
	#orderOtherMsg_quickTrue p{
		height:0.45rem;
		line-height: 0.45rem;
		padding: 0 0.15rem;
	}
	#orderOtherMsg_quickTrue ul{
		display: flex;
		justify-content:flex-start;
		flex-wrap:wrap;
		text-align: center;
		padding: 0 0.14rem;
	}
	.lisnnop{
		background-color:#3190e8 !important;
		color:#fff !important;
	}
	#orderOtherMsg_quickTrue ul li{
		position: relative;
		height: 0.32rem;
		font-size: 0.14rem;
		line-height: 0.32rem;
		background: #fff;
		color:#333;
		border-radius: 0.1rem;
		border: 0.005rem solid #3190e8;
		width:1rem;
		margin-top: 0.05rem;
		margin-right: 0.1rem;
	}
	#orderOtherMsg_otherMsg{
		margin-top: 0.1rem;
		background-color: #fff;
		overflow: hidden;
		width: 100%;
		padding-bottom: 0.25rem;
	}
	#orderOtherMsg_otherMsg p{
		height:0.45rem;
		line-height: 0.45rem;
		padding: 0 0.15rem;
		text-align: left;
	}
	#orderOtherMsg_sure{
		width: 95%;
		height:0.45rem;
		background-color: #4cd964;
		line-height: 0.45rem;
		font-size: 0.16rem;
		margin: 0 auto;
		color:#fff;
	}
	#textarea{
		background-color: #f5f5f5;
		font-size: 0.15rem;
		width:85%;
		padding: 0.1rem;
		height:0.8rem;
		border: 0.005rem solid #eee;
		border-radius: 0.08rem;
	}
	#textarea:focus{
		outline:none;
	}
	
</style>


