<template>
	<div id="resetpass_wrap">
		<div id="resetpass_top">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p>重置密码</p>
		</div>
		<div id="resetpass_div">
			<input type="text" placeholder="账号" v-model="username"/>
			<input type="text" placeholder="旧密码" v-model="oldpassWord"/>
			<input type="text" placeholder="请输入新密码" v-model="newpassword"/>
			<input type="text" placeholder="请确认密码" v-model="confirmpassword"/>
			<input type="text" placeholder="验证码" v-model="captcha_code"/>
			<div class="login_pass_yanzheng">
				<div>
					<img :src="code&&code" alt="" class="yanzheng_img" />
				</div>
				<div >
					<label for="huanyizhang" style="font-size: 0.138rem;">看不清</label>
					<p style="font-size: 0.138rem; color: dodgerblue;" @click="kanbuqing()">换一张</p>
				</div>
			</div>
		</div>
		<el-row>
		 <el-button type="success" style="width: 90%;height: 0.5rem;margin:0.15rem 5% 0;" @click="changeUser()">确认修改</el-button>
		 </el-row>
	</div>
</template>

<script>
	var Resetpass = {
		name:"resetpass",
		data(){
			return{
				code:"",
				username:"",
				oldpassWord:"",
				newpassword:"",
				confirmpassword:"",
				captcha_code:""
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			},
			kanbuqing(){
				var _this = this
				var api_login = "https://elm.cangdu.org/v1/captchas";
				this.$http({
					method:'post',
					url:api_login,
					withCredentials: true,
				}).then(function(el){
					if(el.data.status == 200){}
          				_this.code = el.data.code
				})
			},
			changeUser(){
				var url = "https://elm.cangdu.org/v2/changepassword";
				var _this= this;
				this.axios({
					method:"post",
					url:url,
					withCredentials: true,
					data:{
						username:_this.username,
						oldpassWord:_this.oldpassWord,
						newpassword:_this.newpassword,
						confirmpassword:_this.confirmpassword,
						captcha_code:_this.captcha_code,
					}
				}).then(function(el){
					console.log(el)
					if (el.data.status) {
						alert(el.data.success)
						_this.$router.push({name:"mui"});
					}else{
						alert(el.data.message)
						_this.haveChange()
					}
				})
			},
			// 输入后,正确于错误,都刷新
			haveChange(){
				var _this=this;
				this.username="";
				this.oldpassWord="";
				this.newpassword="";
				this.confirmpassword="";
				this.captcha_code="";
				var url = "https://elm.cangdu.org/v1/captchas";
				this.$http({
					method:"post",
						url:url,
						withCredentials: true,
					}).then(function(val){
						console.log(val)
						if (val.status == 200) {
							_this.code=val.data.code
						}
				})	
			}
		},
		created(){
			var url = "https://elm.cangdu.org/v1/captchas"
			var _this=this
			this.$http({
					method:"post",
					url:url,
					withCredentials: true,
				}).then(function(val){
					console.log(val)
					if (val.status == 200) {
						_this.code=val.data.code
					}
			})
		}
	}
	export default Resetpass
</script>

<style scoped>
	#resetpass_wrap{
		width:100%;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgb(245, 245, 245);
		overflow: hidden;
	}
	input:focus{
		outline:none;
	}
	#resetpass_top {
		width: 100%;
		line-height: 0.5rem;
		background: #3190E8;
		width: 100%;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
	}
	#resetpass_div{
		margin-top: 0.15rem;
		position: relative;
	}
	#resetpass_div>input{
		width: 100%;
		height: 0.45rem;
		font-size: 0.15rem;
		padding-left: 0.15rem;
		border-bottom: 0.1px solid rgb(245, 245, 245);
	}
	p,i {
		color: white;
	}
	
	i {
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	/*验证码*/
	.login_pass_yanzheng{
		width: 0.5rem;
		position: absolute;
		right: 0.15rem;
		bottom:0.225rem;
		transform: translateY(50%)
	}
	/*验证图片*/
	.yanzheng_img{
		position: absolute;
		right: 0.6rem;
		bottom:0.15rem;
		transform: translateY(50%)
	}
</style>


