<template>
	<div id="clubbuy_wrap">
		<!--导航-->
		<div class="clubbuy_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p style="color: white;">在线支付</p>
		</div>
		<!--倒计时-->
		<div class="clubbuy_count">
			<p>支付剩余时间</p>
			<div>
				<span>00</span><span> : </span>
				<span>{{mine}}</span><span> : </span>
				<span>{{sec}}</span>
			</div>
		</div>
		<p class="clubbuy_wrap_p">选择支付方式</p>
		<!--支付方式-->
		<div class="clubbuy_pay">
			<div>
				<img src="@/../static/imgs/商品.png" alt="" />
				<span>支付宝</span>
				<i class="el-icon-success" @click="clubbuy_i_btnz()" id="clubbuy_pay_z"></i>
			</div>
			<div>
				<img src="@/../static/imgs/主页.png" alt="" />
				<span>微信</span>
				<i class="el-icon-success" @click="clubbuy_i_btnw()" id="clubbuy_pay_w"></i>
			</div>
		</div>
		<el-button type="success" class="clubbuy_wrap_btn" @click="clubbuy_wrap_ton()">确认支付</el-button>
		<!--弹框-->
		<div class="clubbuy_wrap_alert" v-show="clubbuy_wrap_none">
			<span>!</span>
			<p class="clubbuy_wrap_alert_p">{{msg}}</p>
			<el-button type="success" class="clubbuy_wrap_alert_btn" @click="clubbuy_alert_ton()">确认</el-button>
		</div>
	</div>
</template>
<script>
	var Clubbuy = {
		name: "clubbuy",
		data() {
			return {
				mine: "",
				sec: "",
				totaltime: 900,
				clubbuy_wrap_none:false,
				msg:"当前环境无法支付,请打开官方APP进行付款"
			}
		},
		methods: {
			sear() {
				this.$router.go(-1)
			},
			updata() {
				if(this.sec >= 10) {
					this.sec = this.sec
				} else {
					this.sec = '0' + this.sec
				}
				if(this.mine >= 10) {
					this.mine = this.mine
				} else {
					this.mine = '0' + this.mine
				}
				
			},
			clubbuy_i_btnz() {
				var z = document.getElementById("clubbuy_pay_z");
				var w = document.getElementById("clubbuy_pay_w")
				z.style.color = "palegreen";
				w.style.color = "darkgray"
			},
			clubbuy_i_btnw() {
				var z = document.getElementById("clubbuy_pay_z");
				var w = document.getElementById("clubbuy_pay_w")
				z.style.color = "darkgray";
				w.style.color = "palegreen"
			},
			clubbuy_wrap_ton() {
				this.clubbuy_wrap_none = true

			},
			clubbuy_alert_ton(){
				this.$router.push({name: "order"})
				
			}
			
		},
		created() {
			var _this = this
			_this.mine = Math.floor(_this.totaltime / 60 % 60)
			_this.sec = Math.floor(_this.totaltime % 60)
			this.updata()
			this.time = setInterval(function() {
				_this.totaltime -= 1;
				_this.mine = Math.floor(_this.totaltime / 60 % 60)
				_this.sec = Math.floor(_this.totaltime % 60)
			}, 1000)
			
		},
		watch: {
			totaltime(news) {
				this.updata()
				if(this.totaltime === 0){
					clearInterval(this.time)
					this.msg = "支付超时"
					this.clubbuy_wrap_none = true
				}
			}
		}
	}
	export default Clubbuy
</script>

<style scoped>
	#clubbuy_wrap {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgb(245, 245, 245);
	}
	
	.clubbuy_nav {
		width: 100%;
		line-height: 0.5rem;
		background: #3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center
	}
	
	.clubbuy_nav>i {
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
		color: white;
	}
	
	.clubbuy_wrap_p {
		padding: 0.1rem;
		text-align: left;
		box-sizing: border-box;
		height: 0.4rem;
		line-height: 0.2rem;
	}
	/*支付时间*/
	
	.clubbuy_count {
		background: white;
		height: 1.3rem;
	}
	
	.clubbuy_count>p {
		line-height: 0.7rem;
	}
	
	.clubbuy_count>div>span {
		font-size: 0.3rem;
		font-weight: bolder;
	}
	/*支付方式*/
	
	.clubbuy_pay {
		background: white;
	}
	
	.clubbuy_pay>div {
		height: 0.9rem;
		line-height: 0.7rem;
		text-align: left;
		padding: 0.1rem 0.2rem;
		box-sizing: border-box;
		font-size: 0.18rem;
		border-bottom: 1px solid rgb(220, 220, 220);
	}
	
	.clubbuy_pay>div>span:nth-child(2) {
		font-weight: bold;
	}
	
	.clubbuy_pay>div>i {
		float: right;
		line-height: 0.7rem;
		color: darkgray;
	}
	
	.clubbuy_pay>div:nth-child(1)>i {
		color: palegreen;
	}
	/*确认支付*/
	
	.clubbuy_wrap_btn {
		width: 90%;
		margin-top: 0.2rem;
		height: 0.5rem;
	}
	
	img {
		width: 0.4rem;
		margin-top: 0.15rem;
		vertical-align: top;
		margin-right: 0.1rem;
	}
	/*弹框*/

	.clubbuy_wrap_alert {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateY(-50%);
		transform: translateX(-50%);
		width: 80%;
		height: 20%;
		background-color: #fff;
		font-size: 0.18rem;
	}
	.clubbuy_wrap_alert>span{
		display: inline-block;
		text-align: center;
		width: 0.4rem;
		height: 0.4rem;
		padding: 0.2rem;
		margin-top:0.1rem ;
		border: 2px solid red;
		border-radius: 50%;
		font-size: 0.6rem;
		line-height: 0.4rem;
		color: red;
		font-weight: bolder;
	}
	.clubbuy_wrap_alert_p{
		font-size: 0.15rem;
		line-height: 0.4rem;
	}
	.clubbuy_wrap_alert_btn{
		width: 100%;
	}
</style>


