<template>
	<div id="duihuan_wrap">
		<!--导航-->
		<div id="duihuan_nav">
			<i class="el-icon-arrow-left" @click="sear()"></i>
			<p>兑换红包</p>
		</div>
		<!--内容-->
		<div class="duihuan_center">
			<input type="text" placeholder="请输入兑换码" />
			<input type="text" placeholder="验证码" />
			<div @click="kanbuqing()">
				<img :src="code&&code" alt="" class="yanzheng_img" />
				<label for="huanyizhang" style="font-size: 0.138rem;">看不清</label>
				<p style="font-size: 0.138rem; color: dodgerblue;" >换一张</p>
			</div>
			<el-button type="success" style="width: 100%;">兑换</el-button>
		</div>
	</div>
</template>

<script>
	var Duihuanhongbao = {
		name:"duihuanhongbao",
		data(){
			return {
				code:"",
			}
		},
		methods:{
			sear(){
				this.$router.go(-1)
			},
			kanbuqing(){
				var _this = this
				var api_login = "https://elm.cangdu.org/v1/captchas";
				this.$http({
					method:'post',
					url:api_login,
					withCredentials: true,
				}).then(function(el){
					if(el.data.status == 200){}
          				_this.code = el.data.code
				})
			},
		},
		created(){
			this.kanbuqing()
		},
	}
	export default Duihuanhongbao
</script>

<style scoped>
	p,i{
		color: white;
	}
	#duihuan_wrap{
		position: absolute;
		width: 100%;
		top: 0;bottom: 0;
		background: rgb(245,245,245);
		overflow: hidden;
		padding: 0.1rem;
		box-sizing: border-box;
		text-align: center;
	}
	#duihuan_nav{
		width: 100%;
		line-height: 0.5rem;
		background:#3190E8;
		height: 0.5rem;
		font-size: 0.15rem;
		text-align: center;
		position: fixed;
		left: 0;top: 0;
		
	}
	#duihuan_nav>i{
		float: left;
		line-height: 0.5rem;
		margin-left: 0.03rem;
	}
	.duihuan_center{
		margin-top: 0.5rem;
		text-align: left;
	}
	.duihuan_center>input{
		width: 95%;
		height: 0.6rem;
		margin:0.1rem 0 0.1rem 0.1rem;
		font-size: 0.15rem;
		padding-left: 0.1rem;
		box-sizing: border-box;
	}
	.duihuan_center>input:nth-child(2){
		width: 60%;
	}
	.duihuan_center>div{
		background: white;
		margin-top: 0.1rem;
		float: right;
		width: 35%;
		height: 0.6rem;
		text-align: right;
		line-height: 0.2rem;
		padding: 0.05rem;
		box-sizing: border-box;
		
	}
	/*验证码*/
	.yanzheng_img{
		float: left;
		width: 0.7rem;
		margin-top: 0.1rem;
	}
	
</style>


